import { useState } from "react";
import { useDispatch } from "react-redux";
import { userSlice } from "@/redux/User";
import { DelayActionIcon } from "@/assets/NodeIcon";
import type { NodeConfigProps } from "../types";

export default function DelayAction({
  data,
  onDataChange,
  onSave,
}: NodeConfigProps) {
  const dispatch = useDispatch();
  const [dropDown, setDropDown] = useState(false);

  function handleSave() {
    if (!data.delayAction.time) {
      dispatch(
        userSlice.actions.setToastPopUp({
          show: true,
          toastType: "error",
          message: "Please fill all the fields.",
        })
      );
      return;
    }
    onSave();
  }

  return (
    <div className="innerRightSideBar">
      <div className="title-header fx al-cnt jstfy-sp-btw">
        <div className="flex-cnt gap-10">
          <div
            className="node-icn flex-cnt"
            style={{ background: "#85A6000D", border: "1px solid #85A6001A" }}
          >
            <DelayActionIcon />
          </div>
          <div className="grid gap-6">
            <h1>
              <span>Edit</span>Delay Action
            </h1>
            <p>Delay the flow of sequence by a specific time.</p>
          </div>
        </div>
      </div>
      <div className="data-box-container">
        <span className="data-box-label">Edit Delay Time</span>
        <div className="data-box-wrap">
          <div className="row-container">
            <h2>Time</h2>
            <div
              className="node-dropdown h-mc"
              onClick={() => setDropDown(!dropDown)}
              onMouseLeave={() => setDropDown(false)}
            >
              <span className="delay-format cp">
                {data.delayAction.timeFormat}
              </span>
              {dropDown && (
                <div className="dropdown-items outer">
                  <ul className="grey-scrollbar">
                    {TIME_FORMAT.map((item, index) => (
                      <li
                        className="delay-list cp"
                        key={index}
                        onClick={() =>
                          onDataChange("delayAction.timeFormat", item)
                        }
                      >
                        {item}
                      </li>
                    ))}
                  </ul>
                </div>
              )}
            </div>
          </div>
          <input
            className="rightSidebar-tarea"
            type="number"
            value={data.delayAction.time}
            placeholder="Time"
            onChange={(event) =>
              onDataChange("delayAction.time", +event.target.value.slice(0, 9))
            }
          ></input>
        </div>
      </div>
      <div className="btm-sticky__btn flex-cnt">
        <button onClick={handleSave} className="saveDataBtn">
          Save
        </button>
      </div>
    </div>
  );
}

const TIME_FORMAT = ["days", "hours", "minutes", "seconds"];
