import { useQuery } from "@tanstack/react-query";
import { axiosGet } from "@/services/axiosService";

export default function useSmsConfig() {
  return useQuery({
    queryKey: ["configurations", "sms"],
    queryFn: async () => {
      const response = await axiosGet("/configurations/getAllSmsConfig");
      return response.data.data;
    },
    staleTime: Infinity,
  });
}
