import { useQuery } from "@tanstack/react-query";
import { axiosGet } from "@/services/axiosService";

export default function usePushConfig() {
  return useQuery({
    queryKey: ["configurations", "pushNotification"],
    queryFn: async () => {
      const response = await axiosGet("/configurations/getAllPushConfig");
      return response.data.data;
    },
    staleTime: Infinity,
  });
}
