import { useMutation } from "@tanstack/react-query";
import { AxiosError } from "axios";
import { axiosPost } from "@/services/axiosService";
import { useDispatch } from "react-redux";
import { userSlice } from "@/redux/User";
import useStore from "../store";
import { PREDEFINED_VARIABLES } from "../constant";

export default function useBotPublish() {
  const dispatch = useDispatch();

  return useMutation({
    mutationKey: ["bot", "publishBot"],
    mutationFn: async () => {
      const metaData = useStore.getState().metaData;

      if (metaData.startNodeId) {
        const nodes = useStore.getState().nodes;
        const isFirstNodeExist = nodes.find(
          (node) => node.id === metaData.startNodeId,
        );

        if (!isFirstNodeExist) throw new Error("Please Select Start Node");
      } else {
        throw new Error("Please Select Start Node");
      }

      const response = await axiosPost("/users/publishBot", {
        botId: metaData._id,
      });

      const publishData = response.data.data;
      useStore.getState().setMetaData({
        liveBotId: publishData.newBotId,
        lastPublishTime: publishData.lastPublishTime,
      });

      return response.data.data;
    },
    onSuccess: async (botData) => {
      dispatch(
        userSlice.actions.setToastPopUp({
          show: true,
          toastType: "success",
          message: botData.newPublished
            ? "Your bot is now live!"
            : "Changes published successfully",
        }),
      );

      await updateAudience(botData);
    },
    onError: (error) => {
      let errMessage = "";
      if (error instanceof AxiosError) {
        const errorData = error.response?.data.data?.errorData ?? {};
        useStore.getState().setNodeErrors(errorData);

        errMessage = error.response?.data.message;
      } else {
        errMessage = error.message;
      }

      dispatch(
        userSlice.actions.setToastPopUp({
          show: true,
          toastType: "error",
          message: errMessage || "Something went wrong",
        }),
      );
    },
  });
}

async function updateAudience(botData: Record<string, any>) {
  const { questions, botCustomId } = botData;
  const tagData: any[] = [];

  for (const question of questions) {
    if (question.variableName) {
      tagData.push({
        id: question.variableName,
        name: question.variableName,
        status: "active",
        dataType: "string",
      });
    }
  }

  for (const predefined of PREDEFINED_VARIABLES) {
    tagData.push({
      id: predefined,
      name: predefined,
      status: "active",
      dataType: "string",
    });
  }

  await axiosPost(`/audience/tags?list_id=${botCustomId}`, { tags: tagData });
}
