import { useState, useEffect, useRef } from "react";
import useStore from "../store";
import Picker from "@emoji-mart/react";
import emojiData from "@emoji-mart/data";
import VariableDropdown from "../components/VariableDropdown";
import { NotificationIcon } from "@/assets/NodeIcon";
import type { Variable, NodeConfigProps } from "../types";
import Froala from "../components/Froala";
import placeVariable from "../utils/placeVariable";
import AddIcon from "@/assets/AddIcon";
import usePushConfig from "../react-query/configs/usePushConfig";

export default function PushNotification({
  data,
  onDataChange,
  onSave,
}: NodeConfigProps) {
  const setOpenConfigModalId = useStore((state) => state.setOpenConfigModalId);
  const titleInputRef = useRef<React.ElementRef<"input">>(null);
  const [openDropdownId, setOpenDropdownId] = useState<DropDownId>("");
  const [showEmojiPicker, setShowEmojiPicker] = useState(false);
  const [configName, setConfigName] = useState("");

  const { data: configData, status: configStatus } = usePushConfig();

  useEffect(() => {
    if (configData) {
      const selectedConfig = configData.find(
        (config) => config._id === data.pushNotification.pushConfigId
      );

      if (selectedConfig) {
        setConfigName(selectedConfig.configName);
      } else if (configData[0]) {
        const config = configData[0];
        onDataChange("pushNotification.pushConfigId", config._id);
        setConfigName(config.configName);
      } else if (data.pushNotification.pushConfigId) {
        onDataChange("pushNotification.pushConfigId", "");
      }
    }
  }, [configData]);

  function handleVariableSelect(variable: Variable) {
    if (!titleInputRef.current) return;

    const result = placeVariable(titleInputRef.current, variable);
    onDataChange("data.title", result);
  }

  function handleDropdown(id: DropDownId) {
    setOpenDropdownId(openDropdownId ? "" : id);
  }

  function handleSave() {
    onSave();
  }

  return (
    <div className="innerRightSideBar">
      <div className="title-header fx al-cnt jstfy-sp-btw">
        <div className="flex-cnt gap-10">
          <div
            className="node-icn flex-cnt"
            style={{ background: "#DC3C2F0D", border: "1px solid #DC3C2F1A" }}
          >
            <NotificationIcon />
          </div>
          <div className="grid gap-6">
            <h1>
              <span>Edit</span>Push Notification
            </h1>
            <p>
              Send push notifications to the user using Firebase configuration.
            </p>
          </div>
        </div>
      </div>
      <div className="data-box-container">
        <span className="data-box-label">Integration Details</span>
        <div
          className={`data-box-wrap data-box-half-width ${
            !configData?.length ? "create-new-integration" : ""
          }`}
        >
          <div
            className="node-dropdown variableTypeDropDown rightSidebar-tarea no-m"
            onMouseLeave={() => handleDropdown("")}
          >
            {configStatus === "pending" ? (
              <div className="react-loading-skeleton"></div>
            ) : configData?.length ? (
              <div
                className="config-button"
                onClick={() => handleDropdown("config")}
              >
                <span>{configName || "Select Configuration"}</span>
                <svg
                  width="10"
                  height="7"
                  viewBox="0 0 10 7"
                  fill="none"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    d="M1 1.5L5 5.5L9 1.5"
                    stroke="#444444"
                    strokeWidth="2"
                    strokeLinecap="round"
                    strokeLinejoin="round"
                  />
                </svg>
                {openDropdownId === "config" && (
                  <div className="dropdown-items w-100 full-view-menu">
                    <ul className="grey-scrollbar config-list">
                      {configData.map((value, index) => (
                        <li
                          key={index}
                          className={
                            value.configName === configName ? "active-item" : ""
                          }
                          value={value.configName}
                          onClick={() => {
                            onDataChange(
                              "pushNotification.pushConfigId",
                              value._id
                            );
                            setConfigName(value.configName);
                          }}
                        >
                          {value.configName}
                        </li>
                      ))}
                    </ul>
                    <span
                      className="add-condition add_new_button cursor-pointer add-acc p-absolute bottom-0 left-0"
                      onClick={() => setOpenConfigModalId("firebase")}
                    >
                      <AddIcon />
                      Add Configuration
                    </span>
                  </div>
                )}
              </div>
            ) : (
              <span onClick={() => setOpenConfigModalId("firebase")}>
                Create new configuration
              </span>
            )}
          </div>
        </div>
        <span className="data-box-label mg-top">Message Details</span>
        <div className="data-box-wrap">
          <div className="row-container mt-3">
            <span className="input-label">Title</span>
            <div
              className="node-dropdown h-mc"
              onClick={() => handleDropdown("title")}
              onMouseLeave={() => handleDropdown("")}
            >
              <span className="color-red">Add Variable</span>
              {openDropdownId === "title" && (
                <VariableDropdown onSelect={handleVariableSelect} />
              )}
            </div>
          </div>
          <div className="rightSidebar-tarea fx  emoji_input ">
            <input
              className="bd-none no-bg"
              ref={titleInputRef}
              value={data.pushNotification.title}
              placeholder=""
              onChange={(event) =>
                onDataChange("pushNotification.title", event.target.value)
              }
            ></input>
            <button
              className="p-relative no-bg bd-none"
              onClick={() => setShowEmojiPicker(!showEmojiPicker)}
            >
              <svg
                xmlns="http://www.w3.org/2000/svg"
                width="16"
                height="16"
                viewBox="0 0 16 16"
                fill="none"
              >
                <path
                  d="M7.99563 1.88867C4.62229 1.88867 1.89062 4.62645 1.89062 7.99978C1.89062 11.3731 4.62229 14.1109 7.99563 14.1109C11.3751 14.1109 14.1128 11.3731 14.1128 7.99978C14.1128 4.62645 11.3751 1.88867 7.99563 1.88867ZM8.00174 12.8887C5.30063 12.8887 3.11285 10.7009 3.11285 7.99978C3.11285 5.29867 5.30063 3.11089 8.00174 3.11089C10.7028 3.11089 12.8906 5.29867 12.8906 7.99978C12.8906 10.7009 10.7028 12.8887 8.00174 12.8887ZM10.1406 7.38867C10.6478 7.38867 11.0573 6.97923 11.0573 6.47201C11.0573 5.96478 10.6478 5.55534 10.1406 5.55534C9.6334 5.55534 9.22396 5.96478 9.22396 6.47201C9.22396 6.97923 9.6334 7.38867 10.1406 7.38867ZM5.86285 7.38867C6.37007 7.38867 6.77951 6.97923 6.77951 6.47201C6.77951 5.96478 6.37007 5.55534 5.86285 5.55534C5.35563 5.55534 4.94618 5.96478 4.94618 6.47201C4.94618 6.97923 5.35563 7.38867 5.86285 7.38867ZM8.00174 11.3609C9.42563 11.3609 10.6356 10.4687 11.1245 9.22201H4.87896C5.36785 10.4687 6.57785 11.3609 8.00174 11.3609Z"
                  fill="black"
                  fillOpacity="0.54"
                />
              </svg>
            </button>
            <div className="p-absolute emoji-picker">
              {showEmojiPicker && (
                <Picker
                  data={emojiData}
                  previewPosition="none"
                  emojiSize="18"
                  maxFrequentRows="1"
                  onEmojiSelect={(event) =>
                    onDataChange(
                      "pushNotification.title",
                      data.pushNotification.title + event.native
                    )
                  }
                  onClickOutside={() => setShowEmojiPicker(false)}
                />
              )}
            </div>
          </div>
          <div className="row-container mg-top">
            <span className="input-label">Body</span>
          </div>
          <Froala
            type="notification"
            content={data.pushNotification.body}
            onContentChange={(content) =>
              onDataChange("pushNotification.body", content)
            }
          />
        </div>
      </div>
      <div className="btm-sticky__btn flex-cnt">
        <button onClick={handleSave} className="saveDataBtn">
          Save
        </button>
      </div>
    </div>
  );
}

type DropDownId = "title" | "config" | "";
