import { useEffect, useState } from "react";
import { useNavigate, useParams, useSearchParams } from "react-router-dom";
import { ReactFlowProvider } from "@xyflow/react";
import { useQueryClient } from "@tanstack/react-query";
import { useDispatch, useSelector } from "react-redux";
import { userSlice } from "@/redux/User";
import { State } from "@/redux/Types";
import useStore from "./store";
import { AutoSaveProvider } from "./AutoSaveContext";
import Builder from "./Builder";
import Analytics from "../analytics/Analytics";
import Config from "@/components/builder/config/Config";
import Loader from "@/components/loaders/Loader";
import BotName from "@/components/builder/BotName";
import TimeKitComponent from "@/components/TimeKitComponent";
import BooleanModal from "@/components/builder/BooleanModal";
import PublishModal from "@/components/builder/PublishModal";
import SaveStatus from "@/components/builder/SaveStatus";
import ScheduleCallButton from "@/components/builder/ScheduleCallButton";
import PreviewButton from "@/components/builder/PreviewButton";
import CopyButton from "@/components/builder/CopyButton";
import PublishButton from "@/components/builder/PublishButton";
import BuildIcon from "@/assets/builder/BuildIcon";
import ConfigureIcon from "@/assets/builder/ConfigureIcon";
import AnalyzeIcon from "@/assets/builder/AnalyzeIcon";
import BackIcon from "@/assets/builder/BackIcon";
import Smtp from "@/components/integration-modals/Smtp";
import Twilio from "@/components/integration-modals/Twilio";
import Firebase from "@/components/integration-modals/Firebase";
import OpenAi from "@/components/integration-modals/OpenAi";
import LinkedIn from "@/components/integration-modals/LinkedIn";
import Facebook from "@/components/integration-modals/Facebook";
import Whatsapp from "@/components/integration-modals/Whatsapp";
import Gmail from "@/components/integration-modals/Gmail";
import { TabTitle } from "@/utils/TitleFuntion";
import useDebounce from "@/hooks/useDebounce";
import useBot from "./react-query/useBot";
import useBotSave from "./react-query/useBotSave";
import useBotPreview from "./react-query/useBotPreview";
import useBotPublish from "./react-query/useBotPublish";
import introJs from "intro.js";
import { deleteCookies, getCookie } from "@/services/cookieService";
import { BuilderStepsCampaign, BuilderStepsWeb } from "@/constants/TourSteps";
import "@/styles/builder.css";
import confetti from "canvas-confetti";

import { axiosPost } from "@/services/axiosService";
import CampaignModal from "@/components/builder/CampaignModal";
import Outlook from "@/components/integration-modals/Outlook";
import Warmy from "@/components/integration-modals/Warmy";
import HelloBar from "@/components/layoutElements/helloBar";
interface Props {
  onClose: () => void;
}

export default function BuilderContainer() {
  const { botUrl } = useParams();
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const queryClient = useQueryClient();
  const openConfigModalId = useStore((state) => state.openConfigModalId);
  const setOpenConfigModalId = useStore((state) => state.setOpenConfigModalId);
  const metaData = useStore((state) => state.metaData);
  const setMetaData = useStore((state) => state.setMetaData);
  const setCampaignData = useStore((state) => state.setCampaignData);
  const userId = useSelector((state: State) => state.user.userId);
  const showBotAnalytics = useSelector(
    (state: State) => state.user.showBotAnalytics,
  );
  const [selectedTab, setSelectedTab] = useState<TabType>("build");
  const [showExitModal, setShowExitModal] = useState(false);
  const [showPublishModal, setShowPublishModal] = useState(false);
  const [showCampaignModal, setShowCampaignModal] = useState(false);
  const [showTimeKit, setShowTimeKit] = useState(false);
  const [helloBar, setHelloBar] = useState<boolean>(false);
  const nodes = useStore((state) => state.nodes);

  const { status: fetchStatus } = useBot(botUrl);
  const { mutateAsync: saveBot, status: saveStatus } = useBotSave();
  const { mutateAsync: previewBot, status: previewStatus } = useBotPreview();
  const { mutateAsync: publishBot, status: publishStatus } = useBotPublish();
  const [searchParams] = useSearchParams();
  const [componentsLoaded, setComponentsLoaded] = useState<boolean>(false);

  const debouncedSaveBot = useDebounce(saveBot, 1000);

  useEffect(() => {
    const initialTab = searchParams.get("tab");
    if (initialTab && TAB.some((t: any) => t.name === initialTab)) {
      setSelectedTab(initialTab as TabType);
    }

    return () => {
      queryClient.invalidateQueries({ queryKey: ["bots"] });
      queryClient.invalidateQueries({ queryKey: ["audience"] });
      queryClient.invalidateQueries({ queryKey: ["users", "getCompanyUsers"] });
    };
  }, []);

  useEffect(() => {
    if (metaData.projectName) TabTitle(` | ${metaData.projectName}`);
  }, [metaData.projectName]);

  const showConfetti = () => {
    const endTime = Date.now() + 2 * 1000;
    const interval = setInterval(() => {
      if (Date.now() > endTime) clearInterval(interval);
      confetti({
        startVelocity: 30,
        spread: 3600,
        ticks: 60,
        origin: {
          x: Math.random(),
          y: Math.random() - 0.2,
        },
      });
    }, 100);
  };

  useEffect(() => {
    if (
      fetchStatus != "pending" &&
      saveStatus != "pending" &&
      previewStatus != "pending" &&
      publishStatus != "pending"
    )
      setComponentsLoaded(true);
  }, [fetchStatus, saveStatus, previewStatus, publishStatus]);

  useEffect(() => {
    if (componentsLoaded && getCookie(`journey-builder:${userId}`)) {
      deleteCookies(`journey-builder:${userId}`, "/");
      const intro = introJs();
      intro.setOptions({
        steps:
          metaData.botType === "campaign"
            ? BuilderStepsCampaign
            : BuilderStepsWeb,
        showBullets: false,
        tooltipClass: "customTooltip",
        highlightClass: "customHighlight",
        buttonClass: "customButton",
      });

      intro.onbeforechange(function (this, targetElement) {
        if (
          this._currentStep === 1 ||
          (metaData.botType !== "campaign" && this._currentStep === 2)
        ) {
          console.log("here");
          $(".introjs-helperLayer").css("transform", "scale(.96)");
        }
        if (this._currentStep === 1) {
          $(".customTooltip").css({ width: "456px", "min-width": "416px" });
        } else {
          $(".customTooltip").css({ width: "347px", "min-width": "307px" });
        }
        return true;
      });
      intro.onafterchange(function (this, targetElement) {
        if (
          this._currentStep === 1 ||
          (metaData.botType !== "campaign" && this._currentStep === 2)
        ) {
          setTimeout(() => {
            $(".introjs-helperLayer").css(
              "top",
              targetElement.getBoundingClientRect().top - 5,
            );
            $(".introjs-tooltipReferenceLayer").css(
              "top",
              targetElement.getBoundingClientRect().top - 5,
            );
          }, 500);
        }
      });
      intro.start();
    }
  }, [componentsLoaded]);

  useEffect(() => {
    if (showBotAnalytics === true) {
      setSelectedTab("analyze");
      dispatch(userSlice.actions.setShowBotAnalytics(false));
    }
  }, []);

  function handlePublishBot(enableConfetti: boolean = false) {
    publishBot(undefined, {
      onSuccess: (botData) => {
        setShowPublishModal(true);
        if (enableConfetti) showConfetti();
      },
    });
  }

  function handleSendCampaign() {
    console.log("send campaign");
    publishBot(undefined, {
      onSuccess: (botData) => {
        setShowCampaignModal(true);
        // sendCampaign();
      },
    });
  }

  function handleMetaDataChange(data: Record<string, any>) {
    setMetaData(data);
    debouncedSaveBot();
  }

  function handleModalClose() {
    setOpenConfigModalId("");
  }

  if (fetchStatus === "pending") return <Loader />;
  if (fetchStatus === "error") {
    navigate("/dashboard");
    return null;
  }

  return (
    <div className="builder-container">
      <div className="builder-header main-menu main-menu-ai-builder pd-inline-30-20">
        <div className="logo-sec fx al-cnt pd-left-0 omni-botname">
          <div onClick={() => setShowExitModal(true)}>
            <BackIcon />
          </div>
          <BotName
            name={metaData.projectName || "Untitled"}
            onSave={(name) => handleMetaDataChange({ projectName: name })}
          />
        </div>
        <ul>
          {TAB.map((tab, index) => (
            <li
              key={index}
              className={`selected ${tab.name}-tab ${tab.name === selectedTab && "active"} `}
              onClick={() => setSelectedTab(tab.name)}
            >
              {tab.icon}
              <span className="capitalize">{tab.name}</span>
            </li>
          ))}
        </ul>
        <div className="top-btn-sec">
          <div className="top-btn-wrap">
            {selectedTab !== "analyze" && <SaveStatus status={saveStatus} />}
            <div id="preview_publish">
              {/* <ScheduleCallButton onClick={() => setShowTimeKit(true)} /> */}
              {metaData.botType !== "campaign" && (
                <>
                  <PreviewButton
                    loader={previewStatus === "pending"}
                    onClick={previewBot}
                  />
                  <CopyButton />
                </>
              )}
              <PublishButton
                metaData={metaData}
                loader={publishStatus === "pending"}
                onClick={
                  metaData.botType == "campaign"
                    ? handleSendCampaign
                    : handlePublishBot
                }
              />
            </div>
          </div>
        </div>
      </div>
      <HelloBar
        helloBar={helloBar}
        setHelloBar={setHelloBar}
        tab={selectedTab}
      />
      {selectedTab === "build" && (
        <AutoSaveProvider value={debouncedSaveBot}>
          <ReactFlowProvider>
            <Builder />
          </ReactFlowProvider>
        </AutoSaveProvider>
      )}
      {selectedTab === "configure" && (
        <Config
          metaData={metaData}
          onMetaDataChange={handleMetaDataChange}
          onConfigModalOpen={setOpenConfigModalId}
        />
      )}
      {selectedTab === "analyze" && (
        <Analytics
          calledFromBuilder={true}
          botCustomId={metaData.botCustomId}
        />
      )}
      {showExitModal && (
        <BooleanModal
          onConfirm={() => navigate("/dashboard")}
          onCancel={() => setShowExitModal(false)}
        />
      )}
      {showPublishModal && (
        <PublishModal
          onClose={() => setShowPublishModal(false)}
          metaData={metaData}
        />
      )}
      {showCampaignModal && (
        <CampaignModal
          campaignData={nodes[0]}
          setCampaignData={setCampaignData}
          onSuccess={() => setShowCampaignModal(false)}
          onClose={() => setShowCampaignModal(false)}
        />
      )}
      {showTimeKit && (
        <TimeKitComponent onClose={() => setShowTimeKit(false)} />
      )}
      <div>
        {openConfigModalId === "smtp" && <Smtp onClose={handleModalClose} />}
        {openConfigModalId === "twilio" && (
          <Twilio onClose={handleModalClose} />
        )}
        {openConfigModalId === "firebase" && (
          <Firebase onClose={handleModalClose} />
        )}
        {openConfigModalId === "openai" && (
          <OpenAi onClose={handleModalClose} />
        )}
        {openConfigModalId === "whatsapp" && (
          <Whatsapp onClose={handleModalClose} />
        )}
        {openConfigModalId === "facebook" && (
          <Facebook
            onClose={handleModalClose}
            botCustomId={metaData.botCustomId}
          />
        )}
        {openConfigModalId === "gmail" && <Gmail onClose={handleModalClose} />}
        {openConfigModalId === "outlook" && (
          <Outlook onClose={handleModalClose} />
        )}
        {openConfigModalId === "linkedin" && (
          <LinkedIn onClose={handleModalClose} />
        )}
        {openConfigModalId === "warmy" && <Warmy onClose={handleModalClose} />}
      </div>
    </div>
  );
}

type TabType = (typeof TAB)[number]["name"];
const TAB = [
  {
    name: "build",
    icon: <BuildIcon />,
  },
  {
    name: "configure",
    icon: <ConfigureIcon />,
  },
  {
    name: "analyze",
    icon: <AnalyzeIcon />,
  },
] as const;
