import FirstNode from "../components/FirstNode";
import { ABTestingIcon } from "@/assets/NodeIcon";
import type { NodeConfigProps } from "../types";

export default function AbTest({
  data,
  onDataChange,
  onSave,
}: NodeConfigProps) {
  const percentValue = data.abTestData.percent;

  function handleSave() {
    onSave();
  }

  return (
    <div className="innerRightSideBar">
      <div className="title-header fx al-cnt jstfy-sp-btw">
        <div className="flex-cnt gap-10">
          <div
            className="node-icn flex-cnt"
            style={{ background: "#00A1E20D", border: "1px solid #00A1E21A" }}
          >
            <ABTestingIcon />
          </div>
          <div className="grid gap-6">
            <h1>
              <span>Edit </span>A/B test
            </h1>
            <p>
              Perform A/B testing to divide the flow for various users based on
              percentages.
            </p>
          </div>
          <FirstNode />
        </div>
      </div>
      <div className="ab-test-variable data-box-container">
        <span className="data-box-label">Set AB split percentage</span>
        <div className="data-box-wrap">
          <div className="ab-test-input-wrap">
            <span className="slider-label">A</span>
            <div className="ab-test-slider-wrap">
              <span
                className="ab-test-slider-tooltip"
                style={{ marginLeft: `calc(${percentValue}% - 22px)` }}
              >
                {percentValue}%
              </span>
              <span
                className="ab-test-slider-bar"
                style={{ width: `${percentValue}%` }}
              ></span>
              <input
                className="ab-test-slider"
                type="range"
                min="0"
                max="100"
                step="1"
                value={percentValue}
                onChange={(event) =>
                  onDataChange("abTestData.percent", event.target.value)
                }
              />
            </div>
            <span className="slider-label">B</span>
          </div>
        </div>
        <div></div>
      </div>
      <div className="btm-sticky__btn flex-cnt">
        <button onClick={handleSave} className="saveDataBtn">
          Save
        </button>
      </div>
    </div>
  );
}
