import { useState, useRef } from "react";
import { useDispatch } from "react-redux";
import { userSlice } from "@/redux/User";
import FirstNode from "../components/FirstNode";
import VariableDropdown from "../components/VariableDropdown";
import { GetAddressIcon } from "@/assets/NodeIcon";
import placeVariable from "../utils/placeVariable";
import type { Variable, NodeConfigProps } from "../types";
import useAutoHeight from "../hooks/useAutoHeight";

export default function Address({
  data,
  onDataChange,
  onSave,
}: NodeConfigProps) {
  const dispatch = useDispatch();
  const titleInputRef = useRef<React.ElementRef<"textarea">>(null);
  const [showVarDropdown, setShowVarDropdown] = useState(false);
  useAutoHeight(titleInputRef, data.data.title);

  function handleVariableSelect(variable: Variable) {
    if (!titleInputRef.current) return;

    const result = placeVariable(titleInputRef.current, variable);
    onDataChange("data.title", result);
  }

  function handleSave() {
    if (!data.data.title || !data.variableName) {
      dispatch(
        userSlice.actions.setToastPopUp({
          show: true,
          toastType: "error",
          message: "Please fill all the fields.",
        }),
      );
      return;
    }
    onSave();
  }

  return (
    <div className="innerRightSideBar">
      <div className="title-header fx al-cnt jstfy-sp-btw">
        <div className="flex-cnt gap-10">
          <div
            className="node-icn flex-cnt"
            style={{ background: "#5640D60D", border: "1px solid #5640D61A" }}
          >
            <GetAddressIcon />
          </div>
          <div className="grid gap-6">
            <h1>
              <span>Edit</span> Get Address
            </h1>
            <p>Ask user their address using this trigger node.</p>
          </div>
        </div>
        <FirstNode />
      </div>
      <div className="data-box-container">
        <span className="data-box-label">Question</span>
        <div className="data-box-wrap">
          <div className="row-container">
            <span className="input-label">Question title</span>
            <div
              className="node-dropdown h-mc"
              onClick={() => setShowVarDropdown(!showVarDropdown)}
              onMouseLeave={() => setShowVarDropdown(false)}
            >
              <span className="color-red">Add Variable</span>
              {showVarDropdown && (
                <VariableDropdown onSelect={handleVariableSelect} />
              )}
            </div>
          </div>
          <div className="p-relative">
            <div className="rightSidebar-tarea-wrapper">
              <textarea
                ref={titleInputRef}
                className="rightSidebar-tarea grey-scrollbar"
                value={data.data.title}
                placeholder="What is your address?"
                onChange={(event) =>
                  onDataChange("data.title", event.target.value)
                }
              />
            </div>
          </div>
        </div>
        <span className="data-box-label mg-top">Edit variable name</span>
        <div className="data-box-wrap ">
          <span className="input-label">Variable name</span>
          <input
            className="rightSidebar-tarea"
            placeholder="Variable"
            onChange={(event) =>
              onDataChange("variableName", event.target.value)
            }
            value={data.variableName}
          />
        </div>
      </div>
      <div className="btm-sticky__btn flex-cnt">
        <button onClick={handleSave} className="saveDataBtn no-mt">
          Save
        </button>
      </div>
    </div>
  );
}
