import { useDraggable } from "@dnd-kit/core";
import TooltipIcon from "@/assets/TooltipIcon";
import { type NodeWithId } from "../selectionData";
import type { Tooltip } from "../types";

interface Props {
  node: NodeWithId;
  isSelPanelSCollapse: boolean;
  onTooltipOpen: (
    event: React.PointerEvent<HTMLDivElement | HTMLButtonElement>,
    type: Tooltip["type"],
    nodeId: Tooltip["nodeId"]
  ) => void;
  onTooltipClose: () => void;
}

export default function SelectionNode({
  node,
  isSelPanelSCollapse,
  onTooltipOpen,
  onTooltipClose,
}: Props) {
  const { attributes, listeners, setNodeRef } = useDraggable({
    id: `builder/${node.id}`,
    data: { nodeType: node.id },
  });

  return (
    <button
      ref={setNodeRef}
      className="drag-drawflow draw-flow"
      style={{
        background: node.background,
        borderColor: node.background,
      }}
      onPointerEnter={(event) =>
        isSelPanelSCollapse && onTooltipOpen(event, "name", node.id)
      }
      onPointerLeave={onTooltipClose}
      {...attributes}
      {...listeners}
    >
      {node.icon}
      {!isSelPanelSCollapse && <span>{node.name}</span>}
      {node.tooltip && (
        <div
          className="wbhk-tp"
          onPointerEnter={(event) => onTooltipOpen(event, "info", node.id)}
          onPointerLeave={onTooltipClose}
        >
          <TooltipIcon />
        </div>
      )}
    </button>
  );
}
