import { useState, useEffect, useRef } from "react";
import {
  ReactFlow,
  MiniMap,
  Background,
  BackgroundVariant,
  ConnectionLineType,
  useReactFlow,
  type Node,
  type Connection,
  type OnConnectStartParams,
} from "@xyflow/react";
import useStore from "./store";
import {
  nodeTypes,
  edgeTypes,
  connectionLineStyle,
  nodeColor,
} from "./react-flow/config";
import useAutoSave from "./AutoSaveContext";
import BuilderDragProvider from "./components/BuilderDragProvider";
import NodeSelectionPanel from "./react-flow/NodeSelectionPanel";
import NodeConfigPanel from "./react-flow/NodeConfigPanel";
import NavPanel from "./react-flow/NavPanel";
import AgentPanel from "./react-flow/AgentPanel";
import SmallScreenModal from "./components/SmallScreenModal";
import "@xyflow/react/dist/style.css";
import "./react-flow/react-flow.css";
import env from "@/constants/Environment";

const environmentData = env();

export default function Builder() {
  const autoSave = useAutoSave();
  const reactFlow = useReactFlow();
  const nodes = useStore((state) => state.nodes);
  const edges = useStore((state) => state.edges);
  const handleNodesChange = useStore((state) => state.handleNodesChange);
  const handleEdgesChange = useStore((state) => state.handleEdgesChange);
  const handleEdgesUpdate = useStore((state) => state.handleEdgesUpdate);
  const addEdge = useStore((state) => state.addEdge);
  const removeEdge = useStore((state) => state.removeEdge);
  const selectedNodeId = useStore((state) => state.selectedNodeId);
  const selectNode = useStore((state) => state.selectNode);
  const setVariables = useStore((state) => state.setVariables);
  const setEdgeHoverId = useStore((state) => state.setEdgeHoverId);
  const [isSmallScreen, setIsSmallScreen] = useState(false);
  const currentConnection = useRef<Pick<
    Connection,
    "source" | "sourceHandle"
  > | null>(null);

  useEffect(() => {
    const mediaQuery = window.matchMedia("(max-width: 1280px)");
    mediaQuery.addEventListener("change", handleSmallScreenWarning);

    return () => {
      mediaQuery.removeEventListener("change", handleSmallScreenWarning);
    };
  }, []);

  function handleSmallScreenWarning(event: MediaQueryListEvent) {
    const isDevelopment = environmentData.ENVIRONMENT === "development";
    setIsSmallScreen(event.matches && !isDevelopment);
  }

  function handleNodeConnect(connection: Connection) {
    if (connection.source === connection.target) return;
    addEdge(connection);
    autoSave();
    updateVariables();
  }

  function handleConnectionStart(_, params: OnConnectStartParams) {
    if (params.handleType && params.handleType === "source" && params.nodeId) {
      currentConnection.current = {
        source: params.nodeId,
        sourceHandle: params.handleId,
      };
    } else {
      currentConnection.current = null;
    }
  }

  function handleConnectionEnd(event: MouseEvent | TouchEvent) {
    if (!currentConnection.current) return;
    if (!(event.target instanceof HTMLElement)) return;

    const nodeElement = event.target.closest(".react-flow__node");
    if (
      nodeElement &&
      nodeElement instanceof HTMLElement &&
      nodeElement.dataset.id
    ) {
      const connection: Connection = {
        ...currentConnection.current,
        target: nodeElement.dataset.id,
        targetHandle: "input_1",
      };
      handleNodeConnect(connection);
      setEdgeHoverId(null);
    }
  }

  function handleNodeClick(_, node: Node) {
    if (_.detail > 1) {
      selectNode(node.id);
      setVariables(node);
    } else {
      if (selectedNodeId === node.id) {
        selectNode("");
      } else {
        selectNode(node.id);
        setVariables(node);
      }
    }
  }

  function handlePaneClick() {
    selectNode("");
  }

  function updateVariables() {
    if (!selectedNodeId) return;
    const node = reactFlow.getNode(selectedNodeId);
    if (!node) return;
    setVariables(node);
  }

  return (
    <BuilderDragProvider>
      <ReactFlow
        nodes={nodes}
        edges={edges}
        nodeTypes={nodeTypes}
        edgeTypes={edgeTypes}
        connectionLineType={ConnectionLineType.SmoothStep}
        connectionLineStyle={connectionLineStyle}
        onNodesChange={handleNodesChange}
        onEdgesChange={handleEdgesChange}
        onEdgeUpdate={handleEdgesUpdate}
        onEdgeUpdateStart={(_, edge) => removeEdge(edge.id)}
        onDelete={() => {
          autoSave();
          updateVariables();
        }}
        onConnect={handleNodeConnect}
        onConnectStart={handleConnectionStart}
        onConnectEnd={handleConnectionEnd}
        onNodeClick={handleNodeClick}
        onPaneClick={handlePaneClick}
        onEdgeMouseEnter={(_, edge) => setEdgeHoverId(edge.id)}
        onEdgeMouseLeave={() => setEdgeHoverId(null)}
        proOptions={{ hideAttribution: true }}
        fitView
        fitViewOptions={{ maxZoom: 1 }}
      >
        <NodeSelectionPanel />
        <NodeConfigPanel />
        <NavPanel />
        <AgentPanel />
        <MiniMap
          className="builder__minimap"
          style={{
            height: 200,
            width: 250,
          }}
          nodeColor={nodeColor}
          position="bottom-right"
          zoomable
          pannable
        />
        <Background
          variant={BackgroundVariant.Dots}
          gap={20}
          size={1}
          style={{
            backgroundColor: "#f8f8f8",
          }}
        />
      </ReactFlow>
      {isSmallScreen && <SmallScreenModal />}
    </BuilderDragProvider>
  );
}
