import { useQuery } from "@tanstack/react-query";
import { axiosGet } from "@/services/axiosService";

export default function useSegments() {
  return useQuery({
    queryKey: ["audience", "segment"],
    queryFn: async () => {
      const response = await axiosGet("/audience/segments");
      return response.data.data;
    },
    staleTime: Infinity,
    placeholderData: [],
  });
}
