import { useMutation } from "@tanstack/react-query";
import { axiosPost } from "@/services/axiosService";
import useStore from "../store";
import { reactFlowToDb } from "../utils/botTransformer";

export default function useBotSave() {
  return useMutation({
    mutationKey: ["bot", "saveBotInOne"],
    mutationFn: async () => {
      const { nodes, edges, metaData } = useStore.getState();
      const botData = reactFlowToDb(nodes, edges, metaData);

      const response = await axiosPost("/users/saveBot", botData);
      const updatedBotData = response.data.data;

      useStore.getState().setMetaData({ updatedAt: updatedBotData.updatedAt });
      useStore.getState().setNodeErrors({});

      return updatedBotData;
    },
  });
}
