import { useState, useRef, useEffect, useMemo } from "react";
import { useDispatch } from "react-redux";
import { userSlice } from "@/redux/User";
import useStore from "../store";
import VariableDropdown from "../components/VariableDropdown";
import { LinkedInIcon } from "@/assets/NodeIcon";
import placeVariable from "../utils/placeVariable";
import type { Variable, NodeConfigProps } from "../types";
import useLinkedinConfig from "../react-query/configs/useLinkedinConfig";

export default function LinkedIn({
  data,
  onDataChange,
  onSave,
}: NodeConfigProps) {
  const dispatch = useDispatch();
  const setOpenConfigModalId = useStore((state) => state.setOpenConfigModalId);
  const fieldInputRefs = useRef<FieldInputRefs>({});
  const [openDropdownId, setOpenDropdownId] = useState<DropDownId>("");
  const [configName, setConfigName] = useState("");
  const showLeadsSelector = useStore(
    (state) =>
      state.metaData.botType === "campaign" &&
      state.metaData.campaignType === "segment",
  );
  const leadsData = useStore((state) => state.leadsData);
  const updateLeadsData = useStore((state) => state.updateLeadsData);
  const [selectedLeadId, setSelectedLeadId] = useState<string | null>(
    leadsData?.[0]?._id ?? null,
  );
  const selectedLead = useMemo(
    () => leadsData.find((lead) => lead._id === selectedLeadId),
    [leadsData, selectedLeadId],
  );
  const messageKey = useMemo(
    () => data.linkedInData.message.match(/{(.*)}/)?.[1].trim(),
    [data.linkedInData.message],
  );

  const { data: configData, status: configStatus } = useLinkedinConfig();

  useEffect(() => {
    if (configData) {
      const selectedConfig = configData.find(
        (config) => config._id === data.linkedInData.configId,
      );

      if (selectedConfig) {
        setConfigName(selectedConfig.firstName + " " + selectedConfig.lastName);
      } else if (configData[0]) {
        const config = configData[0];
        onDataChange("linkedInData.configId", config._id);
        setConfigName(config.firstName + " " + config.lastName);
      } else if (data.linkedInData.configId) {
        onDataChange("linkedInData.configId", "");
      }
    }
  }, [configData]);

  function parseLeadVariables(value) {
    if (showLeadsSelector && selectedLead) {
      const key = value
        .trim()
        .match(/{(.*)}/)?.[1]
        .trim();
      return selectedLead[key] ?? value;
    }
    return value;
  }

  function handleVariableSelect(variable: Variable, field: Field) {
    const inputRef = fieldInputRefs.current[field];
    if (!inputRef) return;

    const result = placeVariable(inputRef, variable);
    onDataChange(`linkedInData.${field}`, result);
  }

  function handleDropdown(id: DropDownId) {
    setOpenDropdownId(openDropdownId ? "" : id);
  }

  function handleSave() {
    const isValidSearchData =
      data.linkedInData.name ||
      data.linkedInData.email ||
      data.linkedInData.profileId;

    const isValidMsgData =
      data.linkedInData.nodeType === "visit" || data.linkedInData.message;

    if (
      !data.linkedInData.nodeType ||
      !data.linkedInData.configId ||
      !isValidSearchData ||
      !isValidMsgData
    ) {
      dispatch(
        userSlice.actions.setToastPopUp({
          show: true,
          toastType: "error",
          message: "Please fill all the fields.",
        }),
      );
      return;
    }
    onSave();
  }

  return (
    <div className="innerRightSideBar">
      <div className="title-header fx al-cnt jstfy-sp-btw">
        <div className="flex-cnt gap-10">
          <div
            className="node-icn flex-cnt"
            style={{ background: "#0077B50D", border: "1px solid #0077B51A" }}
          >
            <LinkedInIcon />
          </div>
          <div className="grid gap-6">
            <h1>
              <span>Edit</span>LinkedIn
            </h1>
            <p>
              Send push notifications to the user using Firebase
              showInstruction.
            </p>
          </div>
        </div>
      </div>
      <div className="data-box-container fx fx-d-col gap-20">
        <div>
          <span className="data-box-label">LinkedIn Integrations Details</span>
          <div
            className={`data-box-wrap data-box-half-width ${
              !configData?.length ? "create-new-integration" : ""
            }`}
          >
            <div>
              <div className="sms-snd-onfiguation">
                <div
                  className="node-dropdown variableTypeDropDown rightSidebar-tarea"
                  onMouseLeave={() => handleDropdown("")}
                >
                  {configStatus === "pending" ? (
                    <div className="react-loading-skeleton" />
                  ) : configData?.length ? (
                    <div
                      className="config-button"
                      onClick={() => handleDropdown("config")}
                    >
                      <span>{configName || "Select account"}</span>
                      <svg
                        width="10"
                        height="7"
                        viewBox="0 0 10 7"
                        fill="none"
                        xmlns="http://www.w3.org/2000/svg"
                      >
                        <path
                          d="M1 1.5L5 5.5L9 1.5"
                          stroke="#444444"
                          strokeWidth="2"
                          strokeLinecap="round"
                          strokeLinejoin="round"
                        />
                      </svg>
                      {openDropdownId === "config" && (
                        <div className="dropdown-items w-100 full-view-menu">
                          <ul className="grey-scrollbar config-list">
                            {configData.map((config) => (
                              <li
                                key={config._id}
                                className={
                                  config.firstName + " " + config.lastName ===
                                  data.linkedInData.configName
                                    ? "active-config"
                                    : ""
                                }
                                onClick={() => {
                                  onDataChange(
                                    "linkedInData.configId",
                                    config._id,
                                  );
                                  setConfigName(
                                    config.firstName + " " + config.lastName,
                                  );
                                }}
                              >
                                {config.firstName + " " + config.lastName}
                              </li>
                            ))}
                          </ul>
                          <span
                            className="add-condition add_new_button add-acc p-absolute bottom-0 left-0 cursor-pointer"
                            onClick={() => setOpenConfigModalId("linkedin")}
                          >
                            <svg
                              width={16}
                              height={16}
                              viewBox="0 0 16 16"
                              fill="none"
                              xmlns="http://www.w3.org/2000/svg"
                            >
                              <path
                                d="M7.9987 14.6654C11.6654 14.6654 14.6654 11.6654 14.6654 7.9987C14.6654 4.33203 11.6654 1.33203 7.9987 1.33203C4.33203 1.33203 1.33203 4.33203 1.33203 7.9987C1.33203 11.6654 4.33203 14.6654 7.9987 14.6654Z"
                                stroke="#FB5F66"
                                strokeLinecap="round"
                                strokeLinejoin="round"
                              />
                              <g opacity={0.4}>
                                <path
                                  d="M5.33594 8H10.6693"
                                  stroke="#FB5F66"
                                  strokeLinecap="round"
                                  strokeLinejoin="round"
                                />
                                <path
                                  d="M8.00391 10.6654V5.33203"
                                  stroke="#FB5F66"
                                  strokeLinecap="round"
                                  strokeLinejoin="round"
                                />
                              </g>
                            </svg>
                            Add Configuration
                          </span>
                        </div>
                      )}
                    </div>
                  ) : (
                    <span onClick={() => setOpenConfigModalId("linkedin")}>
                      Create new integration
                    </span>
                  )}
                </div>
              </div>
            </div>
          </div>
        </div>
        <div>
          <span className="data-box-label">Select Node Type</span>
          <div className="data-box-wrap">
            <div
              className={`linkedIn-node-options fx p-relative gap-20 ${
                data.linkedInData.nodeType && "bottom_border"
              }`}
            >
              {NODE_TYPE.map((type, index) => (
                <div
                  key={index}
                  className={`linkedIn-node-option pd-10 ${
                    data.linkedInData.nodeType
                      ? data.linkedInData.nodeType === type.name
                        ? "node-active"
                        : "node-disable"
                      : ""
                  }`}
                  onClick={() =>
                    onDataChange("linkedInData.nodeType", type.name)
                  }
                >
                  <div className="option-header p-relative">
                    <span>{type.title}</span>
                    <div className="pseudo-radio"></div>
                  </div>
                  <img className="option-img" src={type.img} />
                </div>
              ))}
            </div>
            {data.linkedInData.nodeType && (
              <>
                <span className="node-type-heading">{`${
                  NODE_TYPE.find(
                    (type) => type.name === data.linkedInData.nodeType,
                  )?.name
                } details`}</span>
                <div className="node-type-fields grid gap-20">
                  <div className="fx fx-d-col">
                    <div className="fx al-fx-cnt">
                      <span className="input-label">Name</span>
                      <div
                        className="node-dropdown h-mc"
                        onClick={() => handleDropdown("name")}
                        onMouseLeave={() => handleDropdown("")}
                      >
                        <span className="color-red">Add Variable</span>
                        {openDropdownId === "name" && (
                          <VariableDropdown
                            onSelect={(variable) =>
                              handleVariableSelect(variable, "name")
                            }
                          />
                        )}
                      </div>
                    </div>
                    <input
                      className="rightSidebar-tarea"
                      ref={(ref) => ref && (fieldInputRefs.current.name = ref)}
                      placeholder="Enter your name"
                      value={parseLeadVariables(data.linkedInData.name)}
                      onChange={(event) => {
                        // if (showLeadsSelector && selectedLead) {
                        //   updateLeadsData(selectedLead._id, {
                        //     name: event.target.value,
                        //   });
                        //   return;
                        // }
                        onDataChange("linkedInData.name", event.target.value);
                      }}
                    />
                  </div>
                  <div className="fx fx-d-col">
                    <div className="fx al-fx-cnt">
                      <span className="input-label">Email</span>
                      <div
                        className="node-dropdown h-mc"
                        onClick={() => handleDropdown("email")}
                        onMouseLeave={() => handleDropdown("")}
                      >
                        <span className="color-red">Add Variable</span>
                        {openDropdownId === "email" && (
                          <VariableDropdown
                            onSelect={(variable) =>
                              handleVariableSelect(variable, "email")
                            }
                          />
                        )}
                      </div>
                    </div>
                    <input
                      className="rightSidebar-tarea"
                      ref={(ref) => ref && (fieldInputRefs.current.email = ref)}
                      placeholder="Enter your email"
                      value={parseLeadVariables(data.linkedInData.email)}
                      onChange={(event) =>
                        // showLeadsSelector && selectedLead
                        //   ? updateLeadsData(selectedLead._id, {
                        //       email: event.target.value,
                        //     })
                        //   :
                        onDataChange("linkedInData.email", event.target.value)
                      }
                    />
                  </div>
                  <div className="fx fx-d-col">
                    <div className="fx al-fx-cnt">
                      <span className="input-label">Profile ID</span>
                      <div
                        className="node-dropdown h-mc"
                        onClick={() => handleDropdown("profileId")}
                        onMouseLeave={() => handleDropdown("")}
                      >
                        <span className="color-red">Add Variable</span>
                        {openDropdownId === "profileId" && (
                          <VariableDropdown
                            onSelect={(variable) =>
                              handleVariableSelect(variable, "profileId")
                            }
                          />
                        )}
                      </div>
                    </div>
                    <input
                      className="rightSidebar-tarea"
                      ref={(ref) =>
                        ref && (fieldInputRefs.current.profileId = ref)
                      }
                      placeholder="Enter profile ID"
                      value={parseLeadVariables(data.linkedInData.profileId)}
                      onChange={(event) =>
                        // showLeadsSelector && selectedLead
                        //   ? updateLeadsData(selectedLead._id, {
                        //       profileId: event.target.value,
                        //     })
                        //   :
                        onDataChange(
                          "linkedInData.profileId",
                          event.target.value,
                        )
                      }
                    />
                  </div>
                  {showLeadsSelector && (
                    <div className="fx fx-d-col">
                      <div className="fx al-fx-cnt">
                        <span className="input-label">Choose Leads</span>
                      </div>
                      <div
                        className="node-dropdown variableTypeDropDown rightSidebar-tarea no-m h-mc w-100"
                        onClick={() => handleDropdown("leadsSelector")}
                        onMouseLeave={() => handleDropdown("")}
                      >
                        <div className="config-button">
                          {selectedLead?.name ?? "Choose leads"}
                          <svg
                            width="10"
                            height="6"
                            viewBox="0 0 10 6"
                            fill="none"
                            xmlns="http://www.w3.org/2000/svg"
                          >
                            <path
                              d="M1 1L5 5L9 1"
                              stroke="#444444"
                              strokeWidth="2"
                              strokeLinecap="round"
                              strokeLinejoin="round"
                            />
                          </svg>
                        </div>
                        {openDropdownId === "leadsSelector" && (
                          <div className="dropdown-items w-100 full-view-menu fx fx-d-col">
                            <div className="grey-scrollbar mb-0">
                              <ul className="grey-scrollbar">
                                {leadsData.map((lead) => (
                                  <li
                                    key={lead._id}
                                    value={lead._id}
                                    onClick={() => setSelectedLeadId(lead._id)}
                                    className="fx fx-d-col gap-5"
                                  >
                                    {lead.name}
                                  </li>
                                ))}
                              </ul>
                            </div>
                          </div>
                        )}
                      </div>
                    </div>
                  )}
                </div>
                {data.linkedInData.nodeType === "message" && (
                  <>
                    <div className="row-container mg-top">
                      <span className="input-label webhook_head">
                        {/* {data.linkedInData.nodeType !== "connect"
                          ? "Connection Message"
                          : "Message"} */}
                        Message
                      </span>
                      <div
                        className="node-dropdown h-mc"
                        onClick={() => handleDropdown("message")}
                        onMouseLeave={() => handleDropdown("")}
                      >
                        <span className="color-red">Add Variable</span>
                        {openDropdownId === "message" && (
                          <VariableDropdown
                            onSelect={(variable) =>
                              showLeadsSelector && selectedLead
                                ? null
                                : handleVariableSelect(variable, "message")
                            }
                          />
                        )}
                      </div>
                    </div>

                    <textarea
                      className="rightSidebar-tarea new-scrollbar textarea-hg"
                      ref={(ref) =>
                        ref && (fieldInputRefs.current.message = ref)
                      }
                      placeholder="Write Something Here"
                      value={
                        showLeadsSelector && selectedLead
                          ? selectedLead[messageKey]
                          : data.linkedInData.message
                      }
                      onChange={(event) => {
                        showLeadsSelector && selectedLead
                          ? updateLeadsData(selectedLead._id, {
                              [messageKey]: event.target.value,
                            })
                          : onDataChange(
                              "linkedInData.message",
                              event.target.value,
                            );
                      }}
                    />
                  </>
                )}
              </>
            )}
          </div>
        </div>
      </div>
      <div className="btm-sticky__btn flex-cnt">
        <button onClick={handleSave} className="saveDataBtn">
          Save
        </button>
      </div>
    </div>
  );
}

type Field = "name" | "email" | "profileId" | "message";
type DropDownId = Field | "config" | "leadsSelector" | "";
type FieldInputRefs = Partial<
  Record<Field, React.ElementRef<"textarea"> | React.ElementRef<"input">>
>;

const NODE_TYPE = [
  {
    name: "connect",
    title: "Connect People",
    img: "https://dlvkyia8i4zmz.cloudfront.net/v515JN1VQHyf6PwLhLSV_Frame_16006__1_.svg",
  },
  {
    name: "message",
    title: "Send Message",
    img: "https://dlvkyia8i4zmz.cloudfront.net/pOCBnobXT4iqXm9BT4CC_Frame_16006__2_.svg",
  },
  {
    name: "visit",
    title: "Visit Profile",
    img: "https://dlvkyia8i4zmz.cloudfront.net/pboe3DKNQmmx4ag2cT8w_Frame_16006__3_.svg",
  },
];
