import { useState, useEffect } from "react";
import { axiosPut } from "@/services/axiosService";
import { useDispatch } from "react-redux";
import useStore from "@/pages/omni-builder/store";
import { userSlice } from "@/redux/User";
import * as FacebookService from "@/services/facebook.service";
import BooleanModal from "../builder/BooleanModal";
import useWhatsAppConfig from "@/pages/omni-builder/react-query/configs/useWhatsAppConfig";
import { useQueryClient } from "@tanstack/react-query";
import { useSearchParams } from "react-router-dom";

export default function WhatsappNew({ onClose }) {
  const dispatch = useDispatch();
  const [state, setState] = useState(0);
  const [phoneNumbers, setPhoneNumbers] = useState<any[]>([]);
  const [whatsappDisconnecting, setWhatsappDisconnecting] =
    useState<boolean>(false);
  const setIsWhatsappLoading = useStore((state) => state.setIsWhatsappLoading);
  const queryClient = useQueryClient();
  const whatsappConfiguration = useWhatsAppConfig();
  const [fbReponse, setFbReponse] = useState<any>(null);
  const [searchParams] = useSearchParams();

  useEffect(() => {
    if (whatsappConfiguration.isFetching) {
      setFbReponse(null);
      return;
    }
    if (whatsappConfiguration.isLoading || fbReponse) return;
    if (whatsappConfiguration.isError) {
      dispatch(
        userSlice.actions.setToastPopUp({
          show: true,
          toastType: "error",
          // message: whatsappConfiguration.error.message,
          message: "Error while fetching whatsapp configuration",
        }),
      );
      onClose();
      return;
    }

    let config = whatsappConfiguration.data;
    if (!config) {
      const connectMeta = async () => {
        try {
          const forcedLogin = searchParams.get("forced");
          const forced = forcedLogin === "true" ? true : false;
          await FacebookService.whatsappLogin(forced);
        } catch (error: any) {
          if (error.cause === "suppress") return;
          const errMsg = typeof error === "string" ? error : error.message;

          dispatch(
            userSlice.actions.setToastPopUp({
              show: true,
              toastType: "error",
              message: errMsg || "Something went wrong",
            }),
          );
          onClose();
        }
      };

      connectMeta();
      return;
    }

    setPhoneNumbers(config?.phoneNumbers ?? []);
    setState(1);
  }, [whatsappConfiguration, fbReponse]);

  const disconnectMeta = async () => {
    try {
      setWhatsappDisconnecting(false);
      let res = await axiosPut("/users/deleteAppConfig?siteName=whatsapp");
      res = await res.data;
      dispatch(
        userSlice.actions.setToastPopUp({
          show: true,
          toastType: "success",
          message: "Your Meta WhatsApp account disconnected successfully",
        }),
      );

      await FacebookService.logout();
      setIsWhatsappLoading(false);
      queryClient.invalidateQueries({
        queryKey: ["configurations", "whatsapp"],
      });
      onClose();
    } catch (err) {
      console.log("error while disconnecting Account");
    }
  };

  if (!state) return null;

  return (
    <>
      <div className="general-container">
        <div
          className="overflow"
          onClick={() => {
            onClose();
          }}
        >
          <div
            className="no-data-add-button integration-content"
            onClick={(e) => {
              e.stopPropagation();
            }}
          >
            {/* MODAL HEADER */}
            <div className="modal-head">
              <h2>Whatsapp Business Account connection with Omniengage</h2>
              {
                <svg
                  width="40"
                  height="40"
                  viewBox="0 0 40 40"
                  fill="none"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <g clipPath="url(#clip0_3471_6984)">
                    <path
                      d="M0.071106 39.7254L2.84692 29.5906L2.85243 29.5704L2.84201 29.5524C1.1174 26.5624 0.21152 23.1705 0.215686 19.7188L0.215686 19.7188C0.220232 8.87333 9.04622 0.0500134 19.8919 0.05L0.071106 39.7254ZM0.071106 39.7254L10.4443 37.0052L10.4635 37.0002L10.4809 37.0097C13.3548 38.5767 16.5904 39.4028 19.8831 39.4042H19.8917C30.7358 39.4042 39.5632 30.5792 39.568 19.7346C39.5698 14.4786 37.5257 9.53681 33.8104 5.8188C30.0951 2.1008 25.1552 0.0526792 19.892 0.05L0.071106 39.7254Z"
                      fill="white"
                      stroke="#E7E5E5"
                      strokeWidth="0.1"
                    />
                    <path
                      d="M19.894 3.33208C10.8502 3.33208 3.49542 10.6842 3.4922 19.721C3.48775 22.8069 4.35673 25.831 5.99867 28.4437L6.38876 29.0638L4.73217 35.1116L10.9376 33.4845L11.5368 33.8395C14.0535 35.3331 16.939 36.1229 19.8817 36.1245H19.8879C28.9247 36.1245 36.2795 28.7716 36.283 19.7342C36.2898 17.5802 35.8693 15.4462 35.0458 13.4558C34.2223 11.4654 33.0121 9.65809 31.4854 8.13858C29.9671 6.61075 28.1608 5.39925 26.1712 4.57421C24.1815 3.74917 22.048 3.32698 19.894 3.33208Z"
                      fill="url(#paint0_linear_3471_6984)"
                    />
                    <path
                      fillRule="evenodd"
                      clipRule="evenodd"
                      d="M14.9606 11.4753C14.5911 10.6547 14.2024 10.638 13.8514 10.6238L12.9066 10.6123C12.5779 10.6123 12.0439 10.7356 11.5924 11.2289C11.1409 11.7222 9.86719 12.9145 9.86719 15.3394C9.86719 17.7644 11.6334 20.1076 11.8795 20.4368C12.1257 20.7661 15.2892 25.901 20.2992 27.8766C24.4626 29.5185 25.3098 29.1919 26.2138 29.1099C27.1178 29.0278 29.1299 27.9176 29.5404 26.7667C29.9508 25.6157 29.9511 24.6297 29.8281 24.4235C29.705 24.2173 29.3763 24.0948 28.883 23.8481C28.3897 23.6015 25.9669 22.4093 25.5151 22.2446C25.0634 22.08 24.735 21.9983 24.406 22.4916C24.077 22.9849 23.1336 24.0945 22.8459 24.4235C22.5582 24.7524 22.2711 24.7937 21.7778 24.5474C21.2845 24.301 19.6974 23.7803 17.8142 22.1012C16.349 20.7948 15.36 19.1813 15.0721 18.6883C14.7841 18.1953 15.0415 17.9282 15.2887 17.6826C15.5099 17.4617 15.7815 17.107 16.0284 16.8194C16.2753 16.5317 16.3566 16.3261 16.5206 15.9976C16.6847 15.6692 16.6029 15.3807 16.4796 15.1344C16.3563 14.888 15.3986 12.4504 14.9606 11.4756"
                      fill="white"
                    />
                  </g>
                  <defs>
                    <linearGradient
                      id="paint0_linear_3471_6984"
                      x1="19.5538"
                      y1="5.29993"
                      x2="19.72"
                      y2="33.3231"
                      gradientUnits="userSpaceOnUse"
                    >
                      <stop stopColor="#57D163" />
                      <stop offset="1" stopColor="#23B33A" />
                    </linearGradient>
                    <clipPath id="clip0_3471_6984">
                      <rect width="39.8171" height="40" fill="white" />
                    </clipPath>
                  </defs>
                </svg>
              }

              <div className="closing-modal" onClick={onClose}>
                <button>
                  <svg
                    width="10"
                    height="10"
                    viewBox="0 0 10 10"
                    fill="none"
                    xmlns="http://www.w3.org/2000/svg"
                  >
                    <path
                      d="M10 1.00714L8.99286 0L5 3.99286L1.00714 0L0 1.00714L3.99286 5L0 8.99286L1.00714 10L5 6.00714L8.99286 10L10 8.99286L6.00714 5L10 1.00714Z"
                      fill="#6D6D6D"
                    />
                  </svg>
                </button>
              </div>
            </div>
            <div className="fb-wrap pd-t-30">
              {/* New Connections */}
              {
                <div className="account-fb">
                  <h3>Account</h3>
                  <div className="user-fb user-wp">
                    <div className="user-details-fb">
                      <img src={whatsappConfiguration?.data?.picture} alt="" />
                      <p>{whatsappConfiguration?.data?.name}</p>
                    </div>
                    <div
                      className="fx al-fx-cnt logout-fb logout-wp gap-10"
                      onClick={() => setWhatsappDisconnecting(true)}
                    >
                      <svg
                        xmlns="http://www.w3.org/2000/svg"
                        width="14"
                        height="15"
                        viewBox="0 0 14 15"
                        fill="none"
                      >
                        <g clipPath="url(#clip0_1591_315003)">
                          <path
                            d="M8.41339 9.52068C8.23934 9.52068 8.07242 9.58982 7.94935 9.7129C7.82628 9.83597 7.75714 10.0029 7.75714 10.1769V11.7757C7.75692 12.0215 7.65923 12.2571 7.4855 12.4308C7.31177 12.6046 7.07619 12.7024 6.83046 12.7027H2.24382C1.99809 12.7024 1.76251 12.6046 1.58878 12.4308C1.41505 12.2571 1.31736 12.0215 1.31714 11.7757V2.26338C1.31736 2.01765 1.41505 1.78204 1.58878 1.60826C1.76251 1.43448 1.99809 1.33672 2.24382 1.33643H6.83046C7.07619 1.33672 7.31177 1.43448 7.4855 1.60826C7.65923 1.78204 7.75692 2.01765 7.75714 2.26338V3.64533C7.75714 3.81938 7.82628 3.9863 7.94935 4.10937C8.07242 4.23244 8.23934 4.30158 8.41339 4.30158C8.58744 4.30158 8.75436 4.23244 8.87743 4.10937C9.0005 3.9863 9.06964 3.81938 9.06964 3.64533V2.26338C9.06892 1.66971 8.83278 1.10056 8.41302 0.680742C7.99325 0.260928 7.42413 0.0247217 6.83046 0.0239258H2.24382C1.65015 0.0247217 1.08102 0.260928 0.661262 0.680742C0.241499 1.10056 0.00536213 1.66971 0.00463867 2.26338V11.7757C0.00536213 12.3694 0.241499 12.9385 0.661262 13.3584C1.08102 13.7782 1.65015 14.0144 2.24382 14.0152H6.83046C7.42413 14.0144 7.99325 13.7782 8.41302 13.3584C8.83278 12.9385 9.06892 12.3694 9.06964 11.7757V10.1769C9.06964 10.0029 9.0005 9.83597 8.87743 9.7129C8.75436 9.58982 8.58744 9.52068 8.41339 9.52068Z"
                            fill="#FB5F66"
                          />
                          <path
                            d="M13.8022 6.54181L11.4668 4.20802C11.3453 4.08656 11.181 4.01759 11.0093 4.0159C10.8375 4.01421 10.6719 4.07995 10.548 4.199C10.2828 4.45411 10.2891 4.88614 10.5494 5.14646L11.7654 6.36325H6.07788C5.90383 6.36325 5.73691 6.43239 5.61384 6.55546C5.49077 6.67854 5.42163 6.84546 5.42163 7.0195C5.42163 7.19355 5.49077 7.36047 5.61384 7.48354C5.73691 7.60661 5.90383 7.67575 6.07788 7.67575H11.7654L10.5376 8.90349C10.4145 9.02656 10.3454 9.19348 10.3453 9.36755C10.3453 9.54162 10.4144 9.70857 10.5375 9.83167C10.6606 9.95478 10.8275 10.0239 11.0016 10.024C11.1756 10.024 11.3426 9.95488 11.4657 9.83181L13.8022 7.49638C13.865 7.43375 13.9148 7.35935 13.9487 7.27745C13.9827 7.19555 14.0002 7.10776 14.0002 7.01909C14.0002 6.93043 13.9827 6.84263 13.9487 6.76073C13.9148 6.67884 13.865 6.60444 13.8022 6.54181Z"
                            fill="#FB5F66"
                          />
                        </g>
                        <defs>
                          <clipPath id="clip0_1591_315003">
                            <rect
                              width="14"
                              height="14"
                              fill="white"
                              transform="translate(0 0.0195312)"
                            />
                          </clipPath>
                        </defs>
                      </svg>
                      Logout
                    </div>
                  </div>
                </div>
              }

              {/* shows all phone numbers connected to the account */}
              <div className="fb-input fb-pages skeletonPages">
                <h3>
                  {phoneNumbers?.length >= 1
                    ? "Your Whatsapp Business Numbers"
                    : phoneNumbers?.length
                      ? "No Numbers Found"
                      : "No Numbers Available"}
                </h3>
                {phoneNumbers?.length > 0 && (
                  <div className="fb-list">
                    <ul className="ul-fb-list">
                      {phoneNumbers?.map((data, index) => (
                        <li
                          key={index}
                          style={{
                            display: data?.showPrompt ? "block" : "flex",
                          }}
                        >
                          <div className="pgname fx al-cnt">
                            {data?.display_phone_number}
                          </div>
                        </li>
                      ))}
                    </ul>
                  </div>
                )}
              </div>
            </div>
          </div>
        </div>
      </div>

      {whatsappDisconnecting ? (
        <BooleanModal
          onConfirm={() => disconnectMeta()}
          onCancel={() => setWhatsappDisconnecting(false)}
          content={{
            title: "Are you sure you want to logout?",
            message:
              "Disconnecting will stop all the WorkFlows running on whatsapp!",
          }}
        />
      ) : null}
    </>
  );
}
