import { useState, useMemo } from "react";
import { Panel } from "@xyflow/react";
import useStore from "../store";
import SelectionNode from "../components/SelectionNode";
import SearchIcon from "@/assets/SearchIcon";
import {
  TRIGGERS,
  ACTIONS,
  APPS,
  EXTRAS,
  NODE_DATA,
  SECTIONS,
  type NodeWithId,
  type SectionsType,
} from "../selectionData";
import { handleSearch } from "@/utils/handleSearch";
import type { Tooltip } from "../types";

export default function NodeSelectionPanel() {
  const isSelPanelSCollapse = useStore((state) => state.isSelPanelSCollapse);
  const metaData = useStore((state) => state.metaData);
  const setIsSelPanelSCollapse = useStore(
    (state) => state.setIsSelPanelSCollapse,
  );
  const [selectedSection, setSelectedSection] = useState<SectionsType>(
    metaData.botType === "campaign" ? "actions" : "triggers",
  );
  const [searchText, setSearchText] = useState("");
  const [tooltipData, setTooltipData] = useState<Tooltip | null>(null);

  const sectionData = useMemo(
    () => generateSectionData(searchText, metaData.botType),
    [searchText],
  );

  function handleTooltipOpen(
    event: React.PointerEvent<HTMLDivElement | HTMLButtonElement>,
    type: Tooltip["type"],
    nodeId: Tooltip["nodeId"],
  ) {
    const targetRect = event.currentTarget.getBoundingClientRect();

    const offsetX = type === "name" ? 35 : 20;
    const offsetY = type === "name" ? -15 : -25;

    const x = targetRect.x + targetRect.width / 2 + offsetX;
    const y = targetRect.y + targetRect.height / 2 + offsetY;

    setTooltipData({ type, nodeId, position: { x, y } });
  }

  return (
    <Panel position="top-left">
      <div className="sidebar-wrapper">
        <div
          className={`col rightSidebar-revs ${
            isSelPanelSCollapse ? "node-collapse" : null
          } `}
          id="reverse-bar"
          onScroll={() => setTooltipData(null)}
        >
          <div className="rereverse-btn"></div>
          <div className="menuItem thin-bar" data-scroll={0} data-touchmove={0}>
            <div
              className="menuItem-container"
              onMouseLeave={() =>
                setTimeout(() => setIsSelPanelSCollapse(true), 500)
              }
              onMouseOver={() => setIsSelPanelSCollapse(false)}
            >
              <div className="nodes-menu fx al-cnt jstfy-sp-btw">
                <h3>Triggers/Actions/Apps</h3>
                <span
                  className="cp grid"
                  style={{
                    transform: isSelPanelSCollapse
                      ? "rotate(180deg)"
                      : "rotate(0deg)",
                  }}
                >
                  <ReversibleArrowIcon />
                </span>
              </div>
              <div
                className="node-search"
                onClick={() => setIsSelPanelSCollapse(false)}
              >
                <SearchIcon />
                <input
                  type="search"
                  placeholder="Search..."
                  value={searchText}
                  onChange={(event) => setSearchText(event.target.value)}
                />
              </div>
              {sectionData.showResult ? (
                SECTIONS.map((section) => (
                  <div key={section} className="list-content">
                    {sectionData[section].length ? (
                      <div className={`list-wrap ${section}-node`}>
                        <div
                          className="list-titles"
                          onClick={() => setSelectedSection(section)}
                        >
                          <span>{section}</span>
                        </div>
                        {(selectedSection === section || searchText) && (
                          <div className="node-items nodeList-acc">
                            <div className="draggable-nodes">
                              {sectionData[section].map((node) => (
                                <SelectionNode
                                  key={node.id}
                                  node={node}
                                  isSelPanelSCollapse={isSelPanelSCollapse}
                                  onTooltipOpen={handleTooltipOpen}
                                  onTooltipClose={() => setTooltipData(null)}
                                />
                              ))}
                            </div>
                          </div>
                        )}
                      </div>
                    ) : null}
                  </div>
                ))
              ) : (
                <NoResultFound />
              )}
            </div>
          </div>
        </div>
      </div>
      {tooltipData?.type === "name" && (
        <div
          className="node-name-tooltip"
          style={{
            top: tooltipData.position.y,
            left: tooltipData.position.x,
          }}
        >
          {NODE_DATA[tooltipData.nodeId].name}
        </div>
      )}
      {tooltipData?.type === "info" && (
        <div
          className="node-info-tooltip"
          style={{
            top: tooltipData.position.y,
            left: tooltipData.position.x,
          }}
        >
          <div className="inf-hd">
            {NODE_DATA[tooltipData.nodeId].icon}
            <h2>{NODE_DATA[tooltipData.nodeId].name}</h2>
          </div>
          <div className="info-details-tp">
            {NODE_DATA[tooltipData.nodeId].tooltip}
          </div>
        </div>
      )}
    </Panel>
  );
}

function generateSectionData(searchText: string, botType: string) {
  const triggers: Array<NodeWithId> = [];
  const actions: Array<NodeWithId> = [];
  const apps: Array<NodeWithId> = [];
  const extras: Array<NodeWithId> = [];

  if (botType !== "campaign") {
    for (const nodeType of TRIGGERS) {
      const match = handleSearch(searchText, nodeType);
      if (match) triggers.push({ id: nodeType, ...NODE_DATA[nodeType] });
    }
  }

  for (const nodeType of ACTIONS) {
    const match = handleSearch(searchText, nodeType);
    if (match) actions.push({ id: nodeType, ...NODE_DATA[nodeType] });
  }

  for (const nodeType of APPS) {
    const match = handleSearch(searchText, nodeType);
    if (match) apps.push({ id: nodeType, ...NODE_DATA[nodeType] });
  }

  for (const nodeType of EXTRAS) {
    const match = handleSearch(searchText, nodeType);
    if (match) extras.push({ id: nodeType, ...NODE_DATA[nodeType] });
  }

  return {
    triggers,
    actions,
    apps,
    extras,
    showResult:
      triggers.length || actions.length || apps.length || extras.length,
  };
}

export function ReversibleArrowIcon() {
  return (
    <svg
    width={24}
    height={24}
    viewBox="0 0 24 24"
    fill="none"
    xmlns="http://www.w3.org/2000/svg"
  >
    <path
      d="M11.6797 14.62L14.2397 12.06L11.6797 9.5"
      stroke="white"
      strokeWidth={1.5}
      strokeMiterlimit={10}
      strokeLinecap="round"
      strokeLinejoin="round"
    />
    <path
      d="M4 12.0596H14.17"
      stroke="white"
      strokeWidth={1.5}
      strokeMiterlimit={10}
      strokeLinecap="round"
      strokeLinejoin="round"
    />
    <path d="M17 9V15" stroke="white" strokeWidth={1.5} strokeLinecap="round" />
  </svg>
  );
}

function NoResultFound() {
  return (
    <div className="no-result-found">
      <object
        type="image/svg+xml"
        data="https://dlvkyia8i4zmz.cloudfront.net/i5lqUWynQc2gAe0r4Rqu_No_bot_found.svg"
      />
      <div className="suggestions">
        <h3>No search result</h3>
      </div>
    </div>
  );
}
