import { useState, useEffect, Fragment } from "react";
import useStore from "../store";
import VariableDropdown from "../components/VariableDropdown";
import { WhatsappIcon } from "@/assets/NodeIcon";
import type { Variable, NodeConfigProps } from "../types";
import AddIcon from "@/assets/AddIcon";
import NewTemplate, {
  NoImgIcon,
  TemplateArrow,
} from "../components/NewTemplate";
import { useDispatch } from "react-redux";
import { userSlice } from "@/redux/User";
import axios from "axios";
import AllTemplates from "../components/AllTemplates";
import { DeleteImageIcon, formatBytes } from "./Button";
import ImageSearchPanel from "../components/ImageSearchPanel";
import uploadImage from "@/services/fileStack.service";
import useWhatsAppConfig from "../react-query/configs/useWhatsAppConfig";
import environment from "@/constants/Environment";
import PdfToImageConverter from "../components/PdfToImage";
import { axiosPost } from "@/services/axiosService";

export default function WhatsApp({
  data,
  onDataChange,
  onSave,
}: NodeConfigProps) {
  const environmentData = environment();
  const setOpenConfigModalId = useStore((state) => state.setOpenConfigModalId);
  const [openDropdownId, setOpenDropdownId] = useState<DropDownId>("");
  const [selectedNumber, setSelectedNumber] = useState("");
  const [whatsappTemplates, setWhatsappTemplates] = useState<Array<any>>([]);
  const [approvedTemplates, setApprovedTemplates] = useState<Array<any>>([]);
  const [showModal, setShowModal] = useState<Modal>("");
  const [selectedTempName, setSelectedTempName] = useState<string>("");
  const [selectedTemplate, setSelectedTemplate] = useState<any>();
  const [phoneNumbers, setPhoneNumbers] = useState<any>();
  const [whatsappBusinessIds, setWhatsAppBusinessIds] = useState<Array<any>>(
    [],
  );
  const [loader, setLoader] = useState(true);
  const whatsappDetail = useWhatsAppConfig();
  // const [index, setIndex] = useState(0);
  const [selectedWabaId, setSelectedWabaId] = useState<any>(
    data?.whatsappDetails?.wabaId,
  );
  const [varValues, setVarValues] = useState<any>({});
  const dispatch = useDispatch();
  let varCount = 1;
  const [searchQuery, setSearchQuery] = useState("");
  const [media, setMedia] = useState<any>({});
  const [editTemplate, setEditTemplate] = useState<any>();

  const [companyName, setCompanyName] = useState(""); // State to store company name

  // Function to handle generating the document
  const generateDocument = async () => {
    try {
      const response = await axiosPost("/agent/generate", {
        companyName: "Apple",
      });

      console.log("Document generated:", response.data);
    } catch (error) {
      console.error("Error generating document:", error);
    }
  };

  const handleUploadFile = (type: string) => {
    uploadImage({
      accept: [
        type === "video"
          ? "video/*"
          : type === "document"
            ? "application/pdf"
            : "image/*",
      ],
      fromSources: ["local_file_system", "url"],
      transformations: {
        crop: false,
        circle: true,
        rotate: true,
      },
      maxFiles: 1,
      minFiles: 1,
      maxSize: 10 * 1024 * 1024,
      onFileUploadFinished: (file) => {
        saveMedia({
          [type]: file.url,
          [type + "Size"]: file.size ?? 0,
          [type + "Name"]: file.filename ?? "",
        });
      },
    });
  };

  const templateData = {
    messaging_product: "whatsapp",
    to: data?.whatsappDetails?.from,
    type: "template",
    template: {
      name: selectedTemplate?.name,
      language: {
        code: selectedTemplate?.language,
      },
      components: [
        varValues["HEADER"] && {
          type: "HEADER",
          parameters: varValues["HEADER"],
        },
        varValues["BODY"] && {
          type: "BODY",
          parameters: varValues["BODY"],
        },
        ...(selectedTemplate?.components || []).map((item) => {
          if (
            (item.format === "IMAGE" ||
              item.format === "VIDEO" ||
              item.format === "DOCUMENT") &&
            media[item.format.toLowerCase()]
          ) {
            return {
              type: "HEADER",
              parameters: [
                {
                  type: item.format.toLowerCase(),
                  [item.format.toLowerCase()]: {
                    link: media[item.format.toLowerCase()],
                    ...(item.format.toLowerCase() === "document" && {
                      filename:
                        media[item.format.toLowerCase() + "Name"] || null,
                    }),
                  },
                },
              ],
            };
          }
          return null;
        }),
      ].filter(Boolean),
    },
  };

  const findVariables = (text: string) => {
    const matches = text.match(/\{\{\d+\}\}/g);
    return matches;
  };

  const handlePhoneNumberClick = async (index: number) => {
    const config = whatsappDetail.data;
    setSelectedWabaId(whatsappBusinessIds[index]);
    fetchTemplates(config, index);
  };

  useEffect(() => {
    setMedia(
      data.whatsappDetails?.media?.length ? data.whatsappDetails?.media[0] : {},
    );
  }, []);

  const saveMedia = (mediaData: any) => {
    setMedia((prev: any) => {
      const temp = {
        ...prev,
        ...mediaData,
      };
      onDataChange("whatsappDetails", {
        ...data.whatsappDetails,
        media: [temp],
      });
      return temp;
    });
  };

  useEffect(() => {
    if (!whatsappDetail.isLoading) {
      setLoader(false);
    }

    if (whatsappDetail.isError) {
      dispatch(
        userSlice.actions.setToastPopUp({
          show: true,
          toastType: "error",
          message: "Error while fetching whatsapp configuration",
        }),
      );
    }

    fetchConfig();
  }, [whatsappDetail.data, whatsappDetail.isError]);

  useEffect(() => {
    if (!whatsappDetail.data) return;

    phoneNumbers?.map((phoneNumber, index) => handlePhoneNumberClick(index));
  }, []);

  useEffect(() => {
    const template = whatsappTemplates.find(
      (item) => item.name === data?.whatsappDetails.templateName,
    );
    let varObj: any = {};
    template?.components.map((template) => {
      if (template.format === "TEXT" || template.type === "BODY") {
        const matches = findVariables(template.text);
        const objectsArray: Record<string, any>[] = matches
          ? matches.map((item: string) => ({ type: "text", text: "" }))
          : [];
        if (objectsArray.length) {
          if (template.format === "TEXT") varObj["HEADER"] = objectsArray;
          else varObj["BODY"] = objectsArray;
        }
      }
    });
    setVarValues(varObj);
    setSelectedTemplate(template);
  }, [selectedTempName, approvedTemplates]);

  async function phoneNumberExists(
    config: any,
    phoneNumbers: any[],
    wabaIds: any[],
  ) {
    if (!phoneNumbers?.length && !data.whatsappDetails?.from) return;
    phoneNumbers.forEach(async (phone, index) => {
      if (phone.displayPhoneNumber === data.whatsappDetails?.from) {
        const response = await axios.get(
          `https://graph.facebook.com/${environmentData.META_API_VERSION}/${wabaIds[index]}/message_templates?status`,
          {
            headers: {
              Authorization: `Bearer ${config.accessToken}`,
            },
          },
        );
        setWhatsappTemplates(response.data.data);
        setApprovedTemplates(
          response.data.data.filter((item) => item.status === "APPROVED"),
        );
        return;
      }
    });
  }

  async function fetchConfig() {
    try {
      if (!whatsappDetail.data) return;
      const config = whatsappDetail.data;
      const wabaIds = whatsappDetail.data.wabaIds;
      setWhatsAppBusinessIds(wabaIds);

      let phoneNumbers = whatsappDetail.data.phoneNumbers.map(
        (phone, index) => ({
          phoneId: phone.id,
          displayPhoneNumber: phone.display_phone_number,
          index: index,
        }),
      );

      setPhoneNumbers(phoneNumbers);

      if (phoneNumbers.length > 0) {
        phoneNumberExists(config, phoneNumbers, wabaIds);
      }
    } catch (error: any) {
      if (error.cause === "suppress") return;
      const errMsg = typeof error === "string" ? error : error.message;

      dispatch(
        userSlice.actions.setToastPopUp({
          show: true,
          toastType: "error",
          message: errMsg || "Something went wrong",
        }),
      );
    }
  }

  async function fetchTemplates(config: any, index: number) {
    const headers = {
      Authorization: `Bearer ${config.accessToken}`,
    };

    const wabaId = whatsappBusinessIds[index];
    onDataChange("whatsappDetails", {
      ...data.whatsappDetails,
      wabaId: wabaId,
      from: phoneNumbers[index].displayPhoneNumber,
      phoneNumberId: phoneNumbers[index].phoneId,
    });

    const response = await axios.get(
      `https://graph.facebook.com/${environmentData.META_API_VERSION}/${wabaId}/message_templates`,
      {
        headers: headers,
      },
    );

    setWhatsappTemplates(response.data.data);

    setApprovedTemplates(
      response.data.data.filter((item) => item.status === "APPROVED"),
    );

    return response.data.data;
  }

  const handleDropdown = (id: DropDownId) => {
    setOpenDropdownId((prevId) => (id === prevId ? "" : id));
  };

  function handleSave() {
    onDataChange("whatsappDetails", {
      ...data.whatsappDetails,
      components: templateData?.template?.components,
    });
    onSave();
  }

  const formatText = (text: string): string => {
    const matches = findVariables(text);
    let idx = 0;
    const formattedText = text.split(" ");
    formattedText.forEach((item, index) => {
      if (matches && idx > matches.length) return formattedText.join("");
      if (matches && item == matches[idx]) {
        formattedText[index] = `{{${varCount++}}}`;
        idx++;
      }
    });
    return formattedText.join(" ");
  };

  return (
    <>
      <div className="innerRightSideBar">
        <div className="title-header fx al-cnt jstfy-sp-btw">
          <div className="flex-cnt gap-10">
            <div
              className="node-icn flex-cnt"
              style={{ background: "#26B53C0D", border: "1px solid #26B53C1A" }}
            >
              <WhatsappIcon />
            </div>
            <div className="grid gap-6">
              <h1>
                <span>Edit</span>Whatsapp Template
              </h1>
              <p>Send campaigns to multiple WhatsApp users</p>
            </div>
          </div>
        </div>

        <div className="data-box-container">
          <span className="data-box-label">Account Details</span>
          <div
            className={`data-box-wrap data-box-half-width ${
              !phoneNumbers?.length ? "create-new-integration" : ""
            }`}
          >
            <div
              className="node-dropdown variableTypeDropDown rightSidebar-tarea no-mt"
              onMouseLeave={() => handleDropdown("")}
              onClick={() => phoneNumbers?.length && handleDropdown("config")}
            >
              {loader ? (
                <div className="react-loading-skeleton"></div>
              ) : phoneNumbers?.length ? (
                <div className="config-button">
                  <span>
                    {selectedNumber
                      ? selectedNumber
                      : data.whatsappDetails?.from || "Select Number"}
                  </span>
                  <svg
                    width="10"
                    height="6"
                    viewBox="0 0 10 6"
                    fill="none"
                    xmlns="http://www.w3.org/2000/svg"
                  >
                    <path
                      d="M1 1L5 5L9 1"
                      stroke="#444444"
                      strokeWidth="2"
                      strokeLinecap="round"
                      strokeLinejoin="round"
                    />
                  </svg>
                  {openDropdownId === "config" && (
                    <div className="dropdown-items w-100 full-view-menu new-scrollbar">
                      <ul className="grey-scrollbar config-list fx fx-d-col gap-5">
                        {phoneNumbers?.map((phoneNumber, index) => (
                          <li
                            onClick={() => {
                              handlePhoneNumberClick(index);
                              setSelectedNumber(phoneNumber.displayPhoneNumber);
                            }}
                            key={index}
                            className={
                              data?.whatsappDetails?.from ===
                              phoneNumber.displayPhoneNumber
                                ? "active-item"
                                : ""
                            }
                          >
                            {phoneNumber.displayPhoneNumber}
                          </li>
                        ))}
                      </ul>
                      <span
                        className="add-condition add_new_button add-acc p-absolute bottom-0 left-0 cursor-pointer"
                        onClick={() => setOpenConfigModalId("whatsapp")}
                      >
                        <AddIcon />
                        Add new account
                      </span>
                    </div>
                  )}
                </div>
              ) : (
                <span
                  onClick={() => setOpenConfigModalId("whatsapp")}
                  className="fx al-cnt jstfy-cnt gap-10"
                >
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    width="16"
                    height="17"
                    viewBox="0 0 16 17"
                    fill="none"
                  >
                    <circle cx="8" cy="8.5" r="7.5" stroke="#FB5F66" />
                    <path
                      d="M7.8718 12.1352C7.78647 12.1352 7.71394 12.1054 7.6542 12.0456C7.59447 11.9859 7.5646 11.9134 7.5646 11.828V9.33205H5.107C5.02167 9.33205 4.94914 9.30218 4.8894 9.24245C4.82967 9.18272 4.7998 9.11018 4.7998 9.02485V8.34645C4.7998 8.25258 4.82967 8.18005 4.8894 8.12885C4.94914 8.06912 5.02167 8.03925 5.107 8.03925H7.5646V5.60725C7.5646 5.52192 7.59447 5.44938 7.6542 5.38965C7.71394 5.32992 7.78647 5.30005 7.8718 5.30005H8.61421C8.70807 5.30005 8.7806 5.32992 8.8318 5.38965C8.89154 5.44938 8.92141 5.52192 8.92141 5.60725V8.03925H11.379C11.4643 8.03925 11.5369 8.06912 11.5966 8.12885C11.6563 8.18005 11.6862 8.25258 11.6862 8.34645V9.02485C11.6862 9.11018 11.6563 9.18272 11.5966 9.24245C11.5369 9.30218 11.4643 9.33205 11.379 9.33205H8.92141V11.828C8.92141 11.9134 8.89154 11.9859 8.8318 12.0456C8.7806 12.1054 8.70807 12.1352 8.61421 12.1352H7.8718Z"
                      fill="#FB5F66"
                    />
                  </svg>
                  Add new account
                </span>
              )}
            </div>
          </div>

          <span className="data-box-label mg-top">To</span>
          <div className="data-box-wrap data-box-half-width">
            <div>
              <div className="fx al-fx-cnt">
                <span className="input-label">Phone Number</span>
                <div
                  className="node-dropdown h-mc"
                  onClick={() => handleDropdown("phone")}
                  onMouseLeave={() => handleDropdown("")}
                >
                  <span className="color-red">Add Variable</span>
                  {openDropdownId === "phone" && (
                    <VariableDropdown
                      onSelect={(variable) =>
                        onDataChange(
                          "whatsappDetails.to",
                          `{ ${variable.name} }`,
                        )
                      }
                    />
                  )}
                </div>
              </div>
              <div className="node-dropdown d-block mt-10">
                <input
                  className="rightSidebar-tarea condition no-m"
                  value={data.whatsappDetails?.to}
                  placeholder="Type or select the variable"
                  onChange={(e) =>
                    onDataChange("data.whatsappDetails.to", e.target.value)
                  }
                />
              </div>
            </div>
          </div>

          <span className="fx jstfy-sp-btw mg-top al-fx-end mb-10 ">
            <span className="data-box-label mb-0">Message template</span>
            <span
              className={`fx al-cnt new-template cp gap-8 ${!phoneNumbers?.length ? "hover-none opacity-50" : ""}`}
              onClick={() => (setShowModal("new"), setEditTemplate(null))}
            >
              <svg
                    xmlns="http://www.w3.org/2000/svg"
                    width="16"
                    height="17"
                    viewBox="0 0 16 17"
                    fill="none"
                  >
                    <circle cx="8" cy="8.5" r="7.5" stroke="#FB5F66" />
                    <path
                      d="M7.8718 12.1352C7.78647 12.1352 7.71394 12.1054 7.6542 12.0456C7.59447 11.9859 7.5646 11.9134 7.5646 11.828V9.33205H5.107C5.02167 9.33205 4.94914 9.30218 4.8894 9.24245C4.82967 9.18272 4.7998 9.11018 4.7998 9.02485V8.34645C4.7998 8.25258 4.82967 8.18005 4.8894 8.12885C4.94914 8.06912 5.02167 8.03925 5.107 8.03925H7.5646V5.60725C7.5646 5.52192 7.59447 5.44938 7.6542 5.38965C7.71394 5.32992 7.78647 5.30005 7.8718 5.30005H8.61421C8.70807 5.30005 8.7806 5.32992 8.8318 5.38965C8.89154 5.44938 8.92141 5.52192 8.92141 5.60725V8.03925H11.379C11.4643 8.03925 11.5369 8.06912 11.5966 8.12885C11.6563 8.18005 11.6862 8.25258 11.6862 8.34645V9.02485C11.6862 9.11018 11.6563 9.18272 11.5966 9.24245C11.5369 9.30218 11.4643 9.33205 11.379 9.33205H8.92141V11.828C8.92141 11.9134 8.89154 11.9859 8.8318 12.0456C8.7806 12.1054 8.70807 12.1352 8.61421 12.1352H7.8718Z"
                      fill="#FB5F66"
                    />
                  </svg>
              New template
            </span>
          </span>

          <div className="data-box-wrap fx template-wrapper gap-20">
            <div className="wd-50 fx fx-d-col fx-strt">
              <span className="input-label">Select templates</span>
              <div
                className="node-dropdown variableTypeDropDown rightSidebar-tarea w-100 whstp-nodedrp mb-20"
                onClick={() => {
                  handleDropdown("template");
                  setSearchQuery("");
                }}
                onMouseLeave={() => handleDropdown("")}
              >
                <span>
                  {data?.whatsappDetails?.templateName
                    ? data?.whatsappDetails?.templateName
                    : selectedTempName || "No template selected"}
                </span>
                <svg
                  width="10"
                  height="6"
                  viewBox="0 0 10 6"
                  fill="none"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    d="M1 1L5 5L9 1"
                    stroke="#444444"
                    strokeWidth="2"
                    strokeLinecap="round"
                    strokeLinejoin="round"
                  />
                </svg>
                {openDropdownId === "template" && (
                  <div className="dropdown-items w-100 full-view-menu">
                    {approvedTemplates.length > 0 && phoneNumbers && (
                      <div
                        className="whstp-search-input fx al-cnt p-relative"
                        onClick={(e) => {
                          e.stopPropagation();
                        }}
                      >
                        <SearchIconSvg />
                        <input
                          type="text"
                          placeholder="Search Templates"
                          onChange={(e) => setSearchQuery(e.target.value)}
                          value={searchQuery}
                        />
                      </div>
                    )}
                    <ul className="grey-scrollbar">
                      {!approvedTemplates.length ? (
                        !phoneNumbers ? (
                          <span>Select Number First</span>
                        ) : (
                          <span>No templates Found</span>
                        )
                      ) : (
                        ""
                      )}
                      {approvedTemplates.map((value, index) => {
                        return (
                          value.name
                            .toLowerCase()
                            .includes(searchQuery.toLowerCase()) && (
                            <li
                              onClick={() => {
                                onDataChange("whatsappDetails", {
                                  ...data.whatsappDetails,
                                  templateName: value.name,
                                  lang: value.language,
                                });
                                setSelectedTempName(value.name);
                              }}
                              key={index}
                              className={
                                value?.name === selectedTempName
                                  ? selectedTempName
                                  : value.name ===
                                      data?.whatsappDetails?.templateName
                                    ? "active-item"
                                    : ""
                              }
                            >
                              {value.name}
                            </li>
                          )
                        );
                      })}
                    </ul>
                  </div>
                )}
              </div>
              {selectedTemplate ? (
                <>
                  {Object.values(varValues).length > 0 && (
                    <span className="input-label mb-10">
                      Add sample parameter values
                    </span>
                  )}
                  <div
                    className={`data-box-container h-100 pd-15 fx fx-d-col gap-15 fx-strt ${
                      Object.values(varValues).length ||
                      selectedTemplate?.components[0]?.example?.header_handle
                        ? ""
                        : "no-var-found"
                    }`}
                  >
                    {Object.values(varValues).length ||
                    selectedTemplate?.components[0]?.example?.header_handle ? (
                      <>
                        {Object.values(varValues).map((item: any, index) => (
                          <Fragment key={index}>
                            {item.map((variable, varIndex) => (
                              <div key={varIndex}>
                                <div className="row-container">
                                  <span className="input-label">
                                    Enter{" "}
                                    {`{{${
                                      (index === 1
                                        ? varValues[Object.keys(varValues)[0]]
                                            .length + varIndex
                                        : varIndex) + 1
                                    }}}`}{" "}
                                    value
                                  </span>
                                  <div
                                    className="node-dropdown h-mc"
                                    onClick={() =>
                                      handleDropdown(
                                        `templateVar-${index}-${varIndex}` as DropDownId, // Unique identifier for each dropdown
                                      )
                                    }
                                    onMouseLeave={() => handleDropdown("")}
                                  >
                                    <span className="color-red">
                                      Add Variable
                                    </span>
                                    {openDropdownId.includes(
                                      `templateVar-${index}-${varIndex}`, // Check if current dropdown is open
                                    ) && (
                                      <VariableDropdown
                                        onSelect={(variable) =>
                                          setVarValues((prevState) => {
                                            const temp = [...item];
                                            temp[varIndex].text =
                                              `{ ${variable.name} }`;
                                            const tempObj = { ...prevState };
                                            tempObj[
                                              Object.keys(varValues)[index]
                                            ] = temp;
                                            return tempObj;
                                          })
                                        }
                                      />
                                    )}
                                  </div>
                                </div>
                                <input
                                  className="rightSidebar-tarea condition"
                                  placeholder="Enter header text"
                                  value={item[varIndex].text}
                                  onChange={(e) => {
                                    setVarValues((prevState) => {
                                      const tempObj = { ...prevState };
                                      tempObj[Object.keys(varValues)[index]][
                                        varIndex
                                      ].text = e.target.value;
                                      return tempObj;
                                    });
                                  }}
                                />
                              </div>
                            ))}
                          </Fragment>
                        ))}
                        {selectedTemplate?.components[0]?.example
                          ?.header_handle && (
                          <div className="header-img">
                            <div className="upload-img w-100 pd-20">
                              {selectedTemplate?.components[0]?.format &&
                              media[
                                selectedTemplate?.components[0]?.format.toLowerCase()
                              ] ? (
                                <div className="upload-img-container uploaded-img-container cursor-pointer">
                                  <div className="img-wrap br-10 ovr-flw-hd fs-0">
                                    {selectedTemplate?.components.some(
                                      (item) => item.format === "VIDEO",
                                    ) && <video src={media?.video} />}
                                    {selectedTemplate?.components.some(
                                      (item) => item.format === "DOCUMENT",
                                    ) && (
                                      // <iframe
                                      //   title="PDF Document"
                                      //   src={media?.document}
                                      //   width="100%"
                                      //   height="100%"
                                      //   scrolling="no" // here
                                      // />

                                      // <button onClick={generateDocument}>
                                      //   Generate Document
                                      // </button>
                                      <PdfToImageConverter
                                        pdfUrl={media?.document}
                                      />
                                    )}
                                    {selectedTemplate?.components.some(
                                      (item) => item.format === "IMAGE",
                                    ) && <img src={media?.image} />}
                                  </div>
                                  <div className="img-content">
                                    <div className="flex-cnt gap-8">
                                      <h4>
                                        {selectedTemplate?.components[0].format
                                          ? media[
                                              selectedTemplate?.components[0].format.toLowerCase() +
                                                "Name"
                                            ]
                                          : ""}
                                      </h4>
                                      <span
                                        className="close-icn"
                                        onClick={() => {
                                          const format =
                                            selectedTemplate?.components[0]
                                              .format;

                                          saveMedia({
                                            [format.toLowerCase()]: "",
                                            [format.toLowerCase() + "Name"]: "",
                                            [format.toLowerCase() + "Size"]: 0,
                                          });
                                        }}
                                      >
                                        <DeleteImageIcon />
                                      </span>
                                    </div>
                                    <p>
                                      {selectedTemplate?.components[0].format
                                        ? formatBytes(
                                            media[
                                              selectedTemplate?.components[0].format.toLowerCase() +
                                                "Size"
                                            ],
                                          )
                                        : 0}
                                    </p>
                                  </div>
                                </div>
                              ) : (
                                <div
                                  className="upload-img-container container cursor-pointer"
                                  onClick={() =>
                                    selectedTemplate?.components.some(
                                      (item) => item.format === "IMAGE",
                                    )
                                      ? setShowModal(true)
                                      : selectedTemplate?.components.some(
                                            (item) => item.format === "VIDEO",
                                          )
                                        ? handleUploadFile("video")
                                        : handleUploadFile("document")
                                  }
                                >
                                  <div className="img-wrap grid-cnt br-10 ovr-flw-hd fs-0">
                                    <img src="https://dlvkyia8i4zmz.cloudfront.net/LCPtGMwTQaaHG5Bf2RSD_Upload_icon.svg" />
                                  </div>
                                  <div className="img-content">
                                    {selectedTemplate?.components.some(
                                      (item) => item.format === "VIDEO",
                                    ) ? (
                                      <>
                                        <h4>Upload Video</h4>
                                        <p>
                                          Supported files: mp4 |{" "}
                                          <span> (Max 10 MB)</span>
                                        </p>
                                      </>
                                    ) : selectedTemplate?.components.some(
                                        (item) => item.format === "DOCUMENT",
                                      ) ? (
                                      <>
                                        <h4>Upload Document</h4>
                                        <p>
                                          Supported files: pdf |{" "}
                                          <span> (Max 10 MB)</span>
                                        </p>
                                      </>
                                    ) : (
                                      <>
                                        <h4>Upload image</h4>
                                        <p>
                                          Supported files: JPG, PNG, JPEG |{" "}
                                          <span> (Max 5 MB)</span>
                                        </p>
                                      </>
                                    )}
                                  </div>
                                </div>
                              )}
                            </div>
                          </div>
                        )}
                      </>
                    ) : (
                      <div className="no-template-selected h-100 flex-cnt fx-d-col">
                        <img
                          src="https://dlvkyia8i4zmz.cloudfront.net/oFnzNGTS6etZPwRyf73g_Group_26815.svg"
                          alt="no-template"
                        />
                        <h4 className="mt-20">No Variables Found!</h4>
                      </div>
                    )}
                  </div>
                </>
              ) : (
                <div className="no-template-selected h-100 flex-cnt fx-d-col">
                  <img
                    src="https://dlvkyia8i4zmz.cloudfront.net/oFnzNGTS6etZPwRyf73g_Group_26815.svg"
                    alt="no-template"
                  />
                  <h4 className="mt-20">No Template selected yet !</h4>
                  <p>Please select WhatsApp template.</p>
                </div>
              )}
            </div>

            <div className="wd-50">
              <span className="input-label">Message preview</span>
              <div
                className={`template-preview-container pd-20 mt-10 ${
                  !selectedTemplate ? "flex-cnt" : ""
                }`}
              >
                {!selectedTemplate ? (
                  <div className="no-template-preview flex-cnt template-preview-wrapper br-10 pd-10 text-center">
                    No preview available
                  </div>
                ) : (
                  <div className="template-preview-wrapper pd-10 br-10 fx fx-d-col gap-20">
                    {selectedTemplate?.components.map((template, index) => (
                      <Fragment key={index}>
                        {template.format === "TEXT" && (
                          <h3 className="fw-500 no-m">
                            {formatText(template.text)}
                          </h3>
                        )}
                        {template?.format &&
                          template?.format !== "TEXT" &&
                          template?.type === "HEADER" &&
                          media[template.format.toLowerCase()] && (
                            <div
                              className={`flex-cnt data-box-wrap br-5 ovr-flw-hd template-preview-img no-p ${
                                template.format === "DOCUMENT" ? "fx-strt" : ""
                              }`}
                            >
                              {template.format === "IMAGE" && (
                                <img
                                  src={media?.image}
                                  alt={media?.imageName}
                                />
                              )}
                              {template.format === "VIDEO" && (
                                <video src={media?.video} />
                              )}
                              {/* {template.format === "DOCUMENT" && (
                                <iframe
                                  title="PDF Document"
                                  src={media?.document}
                                  scrolling="no"
                                />
                              )} */}
                              {template.format === "DOCUMENT" && (
                                <PdfToImageConverter pdfUrl={media?.document} />
                              )}
                            </div>
                          )}
                        {template?.format &&
                          template?.format != "TEXT" &&
                          !media[template.format.toLowerCase()] && (
                            <div className="flex-cnt data-box-wrap br-5 ovr-flw-hd pd-25">
                              <NoImgIcon />
                            </div>
                          )}
                        {template.type === "BODY" && (
                          <p>{formatText(template.text)}</p>
                        )}
                        {template.type === "FOOTER" && (
                          <p className="opacity-50">{template.text}</p>
                        )}
                        {template.type === "BUTTONS" && (
                          <div className="fx fx-d-col gap-10">
                            {template.buttons.map((item, index) => (
                              <span
                                className="flex-cnt template-cta gap-10"
                                key={index}
                              >
                                <TemplateArrow />
                                {item.text}
                              </span>
                            ))}
                          </div>
                        )}
                      </Fragment>
                    ))}
                  </div>
                )}
              </div>
            </div>
          </div>
        </div>
        <div className="btm-sticky__btn flex-cnt">
          <button onClick={handleSave} className="saveDataBtn">
            Save
          </button>
        </div>
      </div>

      {showModal === "new" && (
        <NewTemplate
          openDropdownId={openDropdownId}
          handleDropdown={handleDropdown}
          setShowModal={setShowModal}
          wabaid={data?.whatsappDetails?.wabaId || ""}
          data={data}
          editTemplate={editTemplate}
          saveMedia={saveMedia}
          media={media}
          setWhatsappTemplates={setWhatsappTemplates}
        />
      )}
      {showModal === "all" && (
        <AllTemplates
          whatsappTemplates={whatsappTemplates}
          setShowModal={setShowModal}
          selectedWabaId={selectedWabaId}
          setEditTemplate={setEditTemplate}
        />
      )}
      {showModal === true && (
        <ImageSearchPanel
          onSelect={(data) =>
            saveMedia({
              image: data.url ?? "",
              imageSize: data.size ?? 0,
              imageName: data.name ?? "",
            })
          }
          onClose={() => setShowModal(false)}
          type="modal"
        />
      )}
    </>
  );
}

type Field = "to" | "body";
type DropDownId =
  | Field
  | "config"
  | "template"
  | "category"
  | "language"
  | "phone"
  | "templateVar"
  | "";
type FieldInputRefs = Partial<
  Record<Field, React.ElementRef<"textarea"> | React.ElementRef<"input">>
>;
export type Modal = "new" | "all" | boolean | "";

const SearchIconSvg = () => (
  <svg
    width={16}
    height={17}
    viewBox="0 0 16 17"
    fill="none"
    xmlns="http://www.w3.org/2000/svg"
  >
    <path
      d="M7.33398 13.8333C10.6477 13.8333 13.334 11.147 13.334 7.83325C13.334 4.51954 10.6477 1.83325 7.33398 1.83325C4.02028 1.83325 1.33398 4.51954 1.33398 7.83325C1.33398 11.147 4.02028 13.8333 7.33398 13.8333Z"
      stroke="#444444"
      strokeWidth={1.5}
      strokeLinecap="round"
      strokeLinejoin="round"
    />
    <path
      opacity={0.4}
      d="M15 15.4999L13.334 13.8333"
      stroke="#444444"
      strokeWidth={1.5}
      strokeLinecap="round"
      strokeLinejoin="round"
    />
  </svg>
);
