import ModalWrapper from "./ModalWrapper";
import { useNavigate } from "react-router-dom";

export default function SmallScreenModal() {
  const navigate = useNavigate();

  return (
    <ModalWrapper>
      <div className="createbot-modal small-modal sm-modal">
        <div className="craetebot-container">
          <div className="success-cont">
            <div className="err-header">
              <ComputerIcon />
            </div>
            <div className="succ-footer">
              <h1>Screen Too Small</h1>
              <div className="err-ft-p">
                <p>
                  Please access it through a device with minimum screen
                  resolution of 1280 x 960 or higher
                </p>
              </div>
              <button onClick={() => navigate("/dashboard")}>
                Go to Dashboard
              </button>
            </div>
          </div>
        </div>
      </div>
    </ModalWrapper>
  );
}

