import { useState, useEffect, useRef } from "react";
import { userSlice } from "@/redux/User";
import { useDispatch } from "react-redux";
import useStore from "../store";
import { axiosGet, axiosPatch, axiosPost } from "@/services/axiosService";
import { variableParser } from "@/services/parserService";
import VariableDropdown from "../components/VariableDropdown";
import placeVariable from "../utils/placeVariable";
import type { Variable, NodeConfigProps } from "../types";
import SmallLoader from "@/components/loaders/SmallLoader";
import { OpenAIIcon } from "@/assets/NodeIcon";
import InfoIcon from "@/assets/InfoIcon";
import AddIcon from "@/assets/AddIcon";
import useOpenaiConfig from "../react-query/configs/useOpenaiConfig";

export default function OpenAi({
  data,
  onDataChange,
  onSave,
}: NodeConfigProps) {
  const dispatch = useDispatch();
  const promptInputRef = useRef<React.ElementRef<"textarea">>(null);
  const setOpenConfigModalId = useStore((state) => state.setOpenConfigModalId);
  const [tab, setTab] = useState<"default" | "test">("default");
  const [openDropdownId, setOpenDropdownId] = useState<DropDownId>("");
  const [configName, setConfigName] = useState("");
  const [optionalParams, setOptionalParams] =
    useState<OpenAiParam[]>(DEFAULT_PARAMS);
  const [generateLoader, setGenerateLoader] = useState(false);
  const [response, setResponse] = useState<string>("");
  const [testVariableData, setTestVariableData] = useState<TestVariable[]>([]);

  const { data: configData, status: configStatus } = useOpenaiConfig();

  useEffect(() => {
    if (configData) {
      const selectedConfig = configData.find(
        (config) => config._id === data.openAiData.configId,
      );

      if (selectedConfig) {
        setConfigName(selectedConfig.name);
        checkIfModelIsUnder16K(selectedConfig.model);
      } else if (configData[0]) {
        const config = configData[0];
        onDataChange("openAiData", {
          ...data.openAiData,
          configId: config._id,
          model: config.model,
        });
        setConfigName(config.name);
        checkIfModelIsUnder16K(config.model);
      } else if (data.openAiData.configId) {
        onDataChange("openAiData", {
          ...data.openAiData,
          configId: "",
          model: "",
        });
      }
    }
  }, [configData]);

  async function handleGenerateResponse() {
    try {
      if (generateLoader) return;
      setResponse("");
      setGenerateLoader(true);

      const variableData = Object.assign(
        {},
        ...testVariableData.map((item) => ({ [item.key]: item.value })),
      );

      const checkedItems = {};
      if (data.openAiData.showParams) {
        optionalParams?.forEach((item) => {
          if (item.isCheck) {
            checkedItems[item.name] = item.value;
          } else {
            checkedItems[item.name] = item.defaultValue;
          }
        });
      }

      const dataToSend = {
        configId: data.openAiData.configId,
        prompt: variableParser(data.openAiData.prompt, variableData),
        showParams: data.openAiData.showParams,
        ...checkedItems,
      };

      const response = await axiosPost("/liveBot/openAiResponse", {
        ...dataToSend,
      });
      setResponse(response.data?.data);
    } catch (err: any) {
      dispatch(
        userSlice.actions.setToastPopUp({
          show: true,
          toastType: "error",
          message: `${
            err.response?.data.message ? err.response.data.message : err.message
          }`,
        }),
      );
    } finally {
      setGenerateLoader(false);
    }
  }

  const handleDataChange = (index: number, value: string) => {
    const tempArr: TestVariable[] = testVariableData.map(
      (item: TestVariable, i: number) => {
        if (index == i) return { key: item.key, value: value };
        else return item;
      },
    );
    setTestVariableData(tempArr);
  };

  const checkIfModelIsUnder16K = async (
    model: string,
    firstTime: boolean = true,
  ) => {
    const currentParams = optionalParams;
    const newParams = currentParams.map((param, i) => {
      if (param.name == "max_tokens") {
        return {
          ...param,
          ["maxValue"]: model?.includes("-16k") ? 16384 : 4095,
          ["value"]: firstTime ? param.value : 2500,
        };
      } else return param;
    });
    setOptionalParams(newParams);
  };

  useEffect(() => {
    if (tab === "test") {
      const tempTestVariableData: TestVariable[] = [];
      const variables = data.openAiData.prompt.matchAll(/{([^{}]*)}/g) ?? [];
      for (const variable of variables) {
        tempTestVariableData.push({ key: variable[1], value: "" });
      }
      setTestVariableData(tempTestVariableData);
    } else {
      setResponse("");
    }
  }, [tab]);

  const handlePrompt = () => {
    if (tab === "test") setTab("default");
    else {
      if (!data.openAiData.configId || !data.openAiData.prompt)
        return dispatch(
          userSlice.actions.setToastPopUp({
            show: true,
            toastType: "error",
            message: `Please fill all the required data`,
          }),
        );
      else setTab("test");
    }
  };

  function handleVariableSelect(variable: Variable) {
    if (!promptInputRef.current) return;

    const result = placeVariable(promptInputRef.current, variable);
    onDataChange("openAiData.prompt", result);
  }

  function handleParamSelect(name: string) {
    const params = { ...data.openAiData.params };

    if (name in params) {
      delete params[name];
    } else {
      const param = DEFAULT_PARAMS.find((param) => param.name === name);
      if (!param) return;
      params[name] = param.value;
    }

    onDataChange("openAiData.params", params);
  }

  function handleParamValueChange(name: string, value: number) {
    const params = { ...data.openAiData.params };
    params[name] = value;

    onDataChange("openAiData.params", params);
  }

  function handleDropdown(id: DropDownId) {
    setOpenDropdownId(openDropdownId ? "" : id);
  }

  function handleSave() {
    onSave();
  }

  return (
    <div className="innerRightSideBar">
      <div className="title-header fx al-cnt jstfy-sp-btw">
        <div className="flex-cnt gap-10">
          <div
            className="node-icn flex-cnt"
            style={{ background: "#0000000D", border: "1px solid #0000001A" }}
          >
            <OpenAIIcon />
          </div>
          <div className="grid gap-6">
            <h1>
              <span>Edit</span>Open AI
            </h1>
            <p>Use AI prompts to generate content</p>
          </div>
        </div>
      </div>
      <div className="data-box-container">
        {tab == "default" && (
          <>
            <span className="data-box-label">Connect Account</span>
            <div className="data-box-wrap ">
              <div className="fx gap-20">
                <div className="w-100">
                  <div
                    className={`${
                      !configData?.length ? "create-new-integration" : ""
                    }`}
                  >
                    <span
                      className={`input-label asterisk ${
                        !configData?.length ? "mb-10" : ""
                      }`}
                    >
                      Select / Add account
                    </span>
                    <div className="sms-snd-onfiguation mt-10">
                      <div
                        className="node-dropdown variableTypeDropDown rightSidebar-tarea w-100"
                        onMouseLeave={() => handleDropdown("")}
                        onClick={() =>
                          configData?.length
                            ? handleDropdown("config")
                            : setOpenConfigModalId("openai")
                        }
                      >
                        {configStatus === "pending" ? (
                          <div className="react-loading-skeleton"></div>
                        ) : configData?.length ? (
                          <div className="config-button">
                            <span>{configName || "Select Configuration"}</span>
                            <svg
                              width="10"
                              height="7"
                              viewBox="0 0 10 7"
                              fill="none"
                              xmlns="http://www.w3.org/2000/svg"
                            >
                              <path
                                d="M1 1.5L5 5.5L9 1.5"
                                stroke="#444444"
                                strokeWidth="2"
                                strokeLinecap="round"
                                strokeLinejoin="round"
                              />
                            </svg>
                          </div>
                        ) : (
                          <span>Create new configuration</span>
                        )}
                        {openDropdownId === "config" && (
                          <div className="dropdown-items w-100 full-view-menu openai_drp">
                            <ul className="grey-scrollbar config-list">
                              {configData.map((config, index) => (
                                <li
                                  key={index}
                                  className={
                                    config.name === configName
                                      ? "active-item"
                                      : ""
                                  }
                                  value={config._id}
                                  onClick={() => {
                                    onDataChange(
                                      "openAiData.configId",
                                      config._id,
                                    );
                                    setConfigName(config.name);
                                    handleDropdown("");
                                  }}
                                >
                                  {config.name}
                                </li>
                              ))}
                            </ul>
                            <span
                              className="add-condition add_new_button add-acc p-absolute bottom-0 left-0 cursor-pointer"
                              onClick={() => setOpenConfigModalId("openai")}
                            >
                              <AddIcon />
                              Add account
                            </span>
                          </div>
                        )}
                      </div>
                    </div>
                  </div>
                </div>
                <div className="w-100">
                  <span className="input-label">Select AI model</span>
                  <div
                    className="node-dropdown variableTypeDropDown rightSidebar-tarea w-100"
                    onClick={() => handleDropdown("modal")}
                    onMouseLeave={() => handleDropdown("")}
                  >
                    <span>{data.openAiData.model || "Select Model"}</span>
                    <svg
                      width="10"
                      height="7"
                      viewBox="0 0 10 7"
                      fill="none"
                      xmlns="http://www.w3.org/2000/svg"
                    >
                      <path
                        d="M1 1.5L5 5.5L9 1.5"
                        stroke="#444444"
                        strokeWidth="2"
                        strokeLinecap="round"
                        strokeLinejoin="round"
                      />
                    </svg>
                    {openDropdownId === "modal" && (
                      <div className="dropdown-items w-100 full-view-menu">
                        <ul className="grey-scrollbar">
                          {!configData?.length && (
                            <span>Select Configuration First</span>
                          )}
                          {configData?.length > 0 &&
                            MODELS.map((model, index) => (
                              <li
                                key={index}
                                onClick={() => {
                                  onDataChange("openAiData.model", model);
                                  checkIfModelIsUnder16K(model);
                                }}
                              >
                                {model}
                              </li>
                            ))}
                        </ul>
                      </div>
                    )}
                  </div>
                </div>
              </div>
            </div>
            <span className="data-box-label mg-top">Prompt Details</span>
            <div className="data-box-wrap">
              <div className="row-container align-unset">
                <h2 className="asterisk">Enter prompt description</h2>
                <div
                  className="node-dropdown h-mc"
                  onClick={() => handleDropdown("prompt")}
                  onMouseLeave={() => handleDropdown("")}
                >
                  <span className="color-red">Add Variable</span>
                  {openDropdownId === "prompt" && (
                    <VariableDropdown
                      onSelect={(variable) => handleVariableSelect(variable)}
                    />
                  )}
                </div>
              </div>
              <textarea
                className="rightSidebar-tarea sendmsg-tarea prompt_text mt-10"
                ref={promptInputRef}
                placeholder="Write prompt here"
                value={data.openAiData.prompt}
                onChange={(event) =>
                  onDataChange("openAiData.prompt", event.target.value)
                }
              />
              <h2 className="mg-top">Store AI response as</h2>
              <input
                className="rightSidebar-tarea"
                type="text"
                placeholder="Write here"
                value={data.variableName}
                onChange={(event) =>
                  onDataChange("variableName", event.target.value)
                }
              />
            </div>
            <span className="data-box-label mg-top">Prompt customization</span>
            <div className="data-box-wrap">
              <div className="fx al-cnt jstfy-sp-btw">
                <h3 className="no-m">Optional customization parameters</h3>
                <div className="toggle-btn">
                  <label className="switch">
                    <input
                      type="checkbox"
                      checked={data.openAiData.showParams}
                      onChange={(event) =>
                        onDataChange(
                          "openAiData.showParams",
                          event.target.checked,
                        )
                      }
                    />
                    <span className="toggle round"></span>
                  </label>
                </div>
              </div>
              {data.openAiData.showParams && (
                <>
                  <div className="fx gap-15 mg-top fx-wrap fx-strt">
                    {DEFAULT_PARAMS.map((item, index) => (
                      <div className="custom_prompts fx alcnt gap-10">
                        <label className="checkbox-container">
                          <input
                            type="checkbox"
                            checked={item.name in data.openAiData.params}
                            onChange={() => handleParamSelect(item.name)}
                          />
                          <span className="mark">
                            <svg
                              width="14"
                              height="12"
                              viewBox="0 0 14 12"
                              fill="none"
                              xmlns="http://www.w3.org/2000/svg"
                            >
                              <path
                                d="M14.0002 1.85403L4.80016 11.25L0.583496 6.94351L1.6645 5.83949L4.80016 9.03412L12.9192 0.75L14.0002 1.85403Z"
                                fill="white"
                              />
                            </svg>
                          </span>
                        </label>
                        <span className="check_names" key={index}>
                          {item.label}
                        </span>
                      </div>
                    ))}
                  </div>
                  {DEFAULT_PARAMS.map(
                    (item, index) =>
                      item.name in data.openAiData.params && (
                        <div className="progress_checks mt-15 fx" key={index}>
                          <div className="custom_params grid gap-20">
                            <div className="params_title grid gap-10">
                              <div className="fx jstfy-unset gap-10">
                                <span>{item.label}</span>
                                <div className="param_details cp">
                                  <InfoIcon />
                                  <div className="modeltooltip">
                                    {item.tooltipContent}
                                  </div>
                                </div>
                              </div>
                              <div className="param_value flex-cnt">
                                {data.openAiData.params[item.name]}
                              </div>
                            </div>
                            <hr />
                            <div className="progress-slider grid gap-5">
                              <div className="row-container">
                                <span>{item.minValue}</span>
                                <span>{item.maxValue}</span>
                              </div>
                              <div className="ab-test-slider-wrap prompt_progress">
                                <span
                                  className="ab-test-slider-btn "
                                  style={{
                                    marginLeft: `calc(${
                                      item.name === "max_tokens"
                                        ? item.maxValue > 4096
                                          ? data.openAiData.params[item.name] /
                                            163.84 //for 16384
                                          : data.openAiData.params[item.name] /
                                            40
                                        : item.name === "top_p"
                                          ? data.openAiData.params[item.name] *
                                            100
                                          : data.openAiData.params[item.name] *
                                            50
                                    }% - ${
                                      item.name === "max_tokens"
                                        ? "24px"
                                        : "20px"
                                    })`,
                                  }}
                                ></span>
                                {item.name === "max_tokens" ? (
                                  <span
                                    className="ab-test-slider-bar"
                                    style={{
                                      width: `calc(${
                                        item.maxValue > 4096
                                          ? data.openAiData.params[item.name] /
                                            163.84 //for 16384
                                          : data.openAiData.params[item.name] /
                                            40
                                      }%`,
                                    }}
                                  ></span>
                                ) : item.name === "top_p" ? (
                                  <span
                                    className="ab-test-slider-bar"
                                    style={{
                                      width: `calc(${
                                        data.openAiData.params[item.name] * 100
                                      }%`,
                                    }}
                                  ></span>
                                ) : (
                                  <span
                                    className="ab-test-slider-bar"
                                    style={{
                                      width: `calc(${
                                        data.openAiData.params[item.name] * 50
                                      }%`,
                                    }}
                                  ></span>
                                )}
                                <input
                                  className="ab-test-slider"
                                  type="range"
                                  min={item.minValue}
                                  max={item.maxValue}
                                  step={item.steps}
                                  value={data.openAiData.params[item.name]}
                                  onChange={(event) =>
                                    handleParamValueChange(
                                      item.name,
                                      +event.target.value,
                                    )
                                  }
                                />
                              </div>
                              <div className="row-container">
                                <span>{item.leftEnd}</span>
                                <span>{item.rightEnd}</span>
                              </div>
                            </div>
                          </div>
                        </div>
                      ),
                  )}
                </>
              )}
            </div>
          </>
        )}
        {tab == "test" && (
          <>
            <span className="data-box-label">Test Prompt</span>
            <div className="data-box-wrap">
              <h2>Enter test values for sample output</h2>
              <div className="header-table-container ovr-flw-x-au mb-20">
                <table className="header-table test-prompt-table">
                  <tr>
                    <th className="header-key">Variable</th>
                    <th className="header-value">Test Value</th>
                  </tr>
                  <tbody className="grey-scrollbar">
                    {testVariableData?.length !== 0 ? (
                      testVariableData.map((variableData, index) => (
                        <tr key={index}>
                          <td>{variableData.key}</td>
                          <td>
                            <input
                              type="text"
                              placeholder="Enter test value"
                              value={variableData.value}
                              onChange={(e) =>
                                handleDataChange(index, e.target.value)
                              }
                            />
                          </td>
                        </tr>
                      ))
                    ) : (
                      <tr className="c-not-allw">
                        <td>No variable selected</td>
                        <td
                          style={{ background: "#F9F9F9" }}
                          className="c-not-allw"
                        >
                          <input
                            type="text"
                            placeholder="Enter test value"
                            disabled
                            readOnly
                            className="c-not-allw"
                          />
                        </td>
                      </tr>
                    )}
                  </tbody>
                </table>
              </div>
              <div className="row-container">
                <h2>Sample output</h2>
                <button
                  className="test-prompt-gen generate-btn fx al-cnt p-0"
                  onClick={handleGenerateResponse}
                >
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    width="14"
                    height="14"
                    viewBox="0 0 14 14"
                    fill="none"
                  >
                    <path
                      d="M12.25 3.50001C12.25 2.53348 13.0335 1.75 14 1.75C13.0335 1.75 12.25 0.966502 12.25 0C12.25 0.966473 11.4665 1.75 10.5 1.75C11.4665 1.75 12.25 2.53345 12.25 3.50001ZM12.6875 5.24998C12.6875 5.97484 12.0999 6.56248 11.375 6.56248C12.0999 6.56248 12.6875 7.15011 12.6875 7.87497C12.6875 7.15008 13.2751 6.56248 14 6.56248C13.2751 6.56251 12.6875 5.97487 12.6875 5.24998ZM6.125 5.24998C6.125 3.80023 7.30024 2.62499 8.74999 2.62499C7.30024 2.62499 6.125 1.44975 6.125 0C6.125 1.44975 4.94976 2.62499 3.50001 2.62499C4.94976 2.62499 6.125 3.80026 6.125 5.24998ZM9.62501 2.62499L0 12.25L1.75 14L11.375 4.37499L9.62501 2.62499ZM8.13134 5.43115L9.36863 4.19386L9.80614 4.63137L8.56885 5.86866L8.13134 5.43115Z"
                      fill="#FB5F66"
                    />
                  </svg>
                  {response ? "Regenerate " : "Generate"}
                </button>
              </div>
              <div className="p-relative flex-cnt">
                {!generateLoader && (
                  <textarea
                    name="body"
                    className="rightSidebar-tarea new-scrollbar textarea-hg p-relative"
                    value={response}
                    readOnly
                  />
                )}
                {!response && (
                  <button
                    className={`generate-overlay flex-cnt ${
                      generateLoader
                        ? "loader-overlay w-100 mt-10 textarea-hg"
                        : "p-absolute"
                    }`}
                  >
                    {generateLoader ? (
                      <SmallLoader />
                    ) : (
                      "Click on Generate to see output"
                    )}
                  </button>
                )}
              </div>
            </div>
          </>
        )}
      </div>
      <div className="btm-sticky__btn flex-cnt">
        <button
          className="closeSideBar CnclBtn test_prompt_btn"
          onClick={handlePrompt}
        >
          {tab == "test" ? "Back" : "Test Prompt"}
        </button>
        <button className="saveDataBtn" onClick={handleSave}>
          Save
        </button>
      </div>
    </div>
  );
}

type DropDownId = "config" | "modal" | "prompt" | "";

interface OpenAiParam {
  label: string;
  steps: number;
  name: string;
  rightEnd: string;
  leftEnd: string;
  isCheck: boolean;
  minValue: number;
  maxValue: number;
  value: number;
  defaultValue: number;
  tooltipContent: string;
}
interface TestVariable {
  key: string;
  value: string;
}

const DEFAULT_PARAMS: Array<OpenAiParam> = [
  {
    label: "Temperature",
    name: "temperature",
    isCheck: false,
    leftEnd: "Low creativity",
    rightEnd: "High creativity",
    minValue: 0,
    maxValue: 2,
    value: 1,
    steps: 0.01,
    defaultValue: 1,
    tooltipContent:
      "Controls randomness: Lowering results in less random completions. As the temperature approaches zero, the model will become deterministic and repetitive.",
  },
  {
    label: "Max Tokens",
    name: "max_tokens",
    isCheck: false,
    leftEnd: "Min.",
    rightEnd: "Max.",
    minValue: 0,
    maxValue: 4095,
    steps: 1,
    value: 2500,
    defaultValue: 2500,
    tooltipContent:
      "The maximum number of tokens to generate shared between the prompt and completions. The exact limit varies by model. (One token is roughly 4 characters for standard English text)",
  },
  {
    label: "Presence Penality",
    name: "presence_penalty",
    leftEnd: "Low",
    rightEnd: "High",
    isCheck: false,
    minValue: 0,
    maxValue: 2,
    steps: 0.01,
    value: 1,
    defaultValue: 0,
    tooltipContent:
      "How much to penalize new tokens based on whether they appear in the text so far. Increase the model's likelihood to talk about new topics.",
  },
  {
    label: "Frequency Penality",
    name: "frequency_penalty",
    isCheck: false,
    leftEnd: "Low",
    rightEnd: "High",
    minValue: 0,
    maxValue: 2,
    steps: 0.01,
    value: 1,
    defaultValue: 0,
    tooltipContent:
      "How much to penalize new token based on their existing frequency in the etxt so far. Decrease the model's likelihood to repeat the same line verbatim",
  },
  {
    label: "Top P",
    name: "top_p",
    isCheck: false,
    leftEnd: "Min.",
    rightEnd: "Max.",
    minValue: 0.01,
    maxValue: 1.0,
    steps: 0.01,
    value: 0.5,
    defaultValue: 1,
    tooltipContent:
      "Controls diversity via nucleas sampling: 0.5 means half of all likelihood-weighted options are considered.",
  },
];

const MODELS = [
  "gpt-3.5-turbo",
  "gpt-3.5-turbo-0301",
  "gpt-3.5-turbo-0613",
  "gpt-3.5-turbo-16k",
  "gpt-3.5-turbo-16k-0613",
  "gpt-4",
  "gpt-4-0613",
  "gpt-4-32k",
  "gpt-4-32k-0613",
];
