import { Panel, useReactFlow } from "@xyflow/react";
import useAutoLayout from "../hooks/useAutoLayout";
import useStore from "../store";

export default function NavPanel() {
  const isSelPanelSCollapse = useStore((state) => state.isSelPanelSCollapse);
  const reactFlow = useReactFlow();
  const autoLayout = useAutoLayout();

  return (
    <Panel
      className="builder__controls"
      position="bottom-left"
      style={{
        marginLeft: isSelPanelSCollapse ? 120 : 340,
        marginBottom: 30
      }}
    >
      <div className="controls_zoom">
        <button onClick={() => reactFlow.zoomIn()}>+</button>
        <button onClick={() => reactFlow.zoomOut()}>-</button>
      </div>
      <div className="controls_layout">
        <button className="flex-cnt" onClick={autoLayout}>
          <LayoutIcon />
        </button>
      </div>
    </Panel>
  );
}

