import { useState, useEffect } from "react";
import { useParams } from "react-router-dom";
import { io } from "socket.io-client";
import useStore from "../store";
import { useDispatch, useSelector } from "react-redux";
import { userSlice } from "@/redux/User";
import { State } from "@/redux/Types";
import { axiosGet, axiosPost } from "@/services/axiosService";
import environment from "@/constants/Environment";
import FirstNode from "../components/FirstNode";
import { WebHookIcon } from "@/assets/NodeIcon";
import type { NodeConfigProps } from "../types";
import flatObject from "@/utils/flatObject";
import { WEBHOOK_SUPPORT_DOC } from "@/constants/URLS";

const { SERVER_API_URL } = environment();

export default function SocialData({
  data,
  onDataChange,
  onSave,
}: NodeConfigProps) {
  const dispatch = useDispatch();
  const { botUrl } = useParams();
  const userId = useSelector((state: State) => state.user.userId);
  const botCustomId = useStore((state) => state.metaData.botCustomId);
  const [apiKey, setApiKey] = useState();
  const [webhookData, setWebhookData] = useState<Array<WebhookData>>([]);

  const webhookUrl = `${SERVER_API_URL}/users/webhookFlow?botUrl=${botUrl}&api_key=${apiKey}`;
  const { isConfigured } = data.webhookData ?? {};

  useEffect(() => {
    fetchApiKey();

    const socket = io(SERVER_API_URL, {
      transports: ["websocket"],
    });

    socket.emit("join", { userId: userId });
    socket.on("outgrowData", (response) => {
      const result = flatObject(response.data);
      const webhookData: Array<WebhookData> = [];

      for (const key in result) {
        webhookData.push({
          id: key,
          name: key,
          value: result[key],
          isSelected: false,
        });
      }
      setWebhookData(webhookData);
    });

    return () => {
      socket.disconnect();
    };
  }, []);

  async function fetchApiKey() {
    try {
      const response = await axiosGet("/api/getApiKey");
      setApiKey(response.data.api_key);
    } catch (error: any) {
      console.log(error);
      dispatch(
        userSlice.actions.setToastPopUp({
          show: true,
          toastType: "error",
          message: error.response.data.message || "Something went wrong",
        }),
      );
    }
  }

  async function generateApiKey() {
    try {
      const response = await axiosGet("/api/genApiKey");
      setApiKey(response.data.data.apiKey);
    } catch (error: any) {
      console.log(error);
      dispatch(
        userSlice.actions.setToastPopUp({
          show: true,
          toastType: "error",
          message: error.response.data.message || "Something went wrong",
        }),
      );
    }
  }

  function handleDataChange(
    type: "name" | "select",
    variableIndex: number,
    value?: string,
  ) {
    const data = webhookData.map((variable, index) => {
      if (index === variableIndex) {
        switch (type) {
          case "name":
            variable.name = value ?? "";
            break;
          case "select":
            variable.isSelected = !variable.isSelected;
            break;
          default:
            break;
        }
      }

      return variable;
    });

    setWebhookData(data);
  }

  async function handleCopy() {
    await navigator.clipboard.writeText(webhookUrl);
    dispatch(
      userSlice.actions.setToastPopUp({
        show: true,
        toastType: "success",
        message: "Copied to Clipboard!",
      }),
    );
  }

  async function handleSave() {
    try {
      const fields = {};
      const variables: Array<any> = [];

      for (const data of webhookData) {
        if (!data.isSelected) continue;

        fields[data.id] = {
          customName: data.name,
          value: data.value,
        };
        variables.push({
          name: data.id,
          customName: data.name,
        });
      }

      await axiosPost("/socialWebhook/saveData", {
        fields,
        botCustomId,
      });

      onSave({
        ...data,
        webhookData: {
          isConfigured: true,
          variables,
        },
      });
    } catch (error: any) {
      console.log(error);
      dispatch(
        userSlice.actions.setToastPopUp({
          show: true,
          toastType: "error",
          message: error.response.data.message || "Something went wrong",
        }),
      );
    }
  }

  async function handleReset() {
    try {
      await axiosGet(`/socialWebhook/resetData?botCustomId=${botCustomId}`);
      onDataChange("webhookData", {
        isConfigured: false,
        variables: [],
      });
    } catch (error: any) {
      console.log(error);
      dispatch(
        userSlice.actions.setToastPopUp({
          show: true,
          toastType: "error",
          message: error.response.data.message || "Something went wrong",
        }),
      );
    }
  }

  return (
    <div className=" innerRightSideBar apisec-head">
      <div className="title-header fx al-cnt jstfy-sp-btw">
        <div className="flex-cnt gap-10">
          <div
            className="node-icn flex-cnt"
            style={{
              background: "rgba(199, 58, 99, 0.05)",
              border: "1px solid rgba(199, 58, 99, 0.10)",
            }}
          >
            <WebHookIcon />
          </div>
          <div className="grid gap-6">
            <h1>
              <span>Edit</span>Webhook
            </h1>
            <p>
              Trigger a flow based on events and data received in the webhook.
            </p>
          </div>
        </div>
        {/* <FirstNode /> */}
      </div>
      <div className="data-box-container">
        <div className="webhook-data-box-container">
          <div className="data-box-label">
            <span className="webhook_head">support doc</span>
          </div>
          <div className="data-box-wrap mb-20">
            <div className="view-support-doc">
              <p>
                You can understand how to use this node by opening the support
                document.
              </p>
              <a
                href={WEBHOOK_SUPPORT_DOC}
                target="_blank"
                className="cursor-pointer"
              >
                <svg
                  width="12"
                  height="12"
                  viewBox="0 0 12 12"
                  fill="none"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    opacity="0.4"
                    d="M9.33333 11.9993H2C1.46957 11.9993 0.960859 11.7886 0.585787 11.4136C0.210714 11.0385 0 10.5298 0 9.99935V2.66602C0 2.13558 0.210714 1.62687 0.585787 1.2518C0.960859 0.876729 1.46957 0.666016 2 0.666016H4.66667C4.84348 0.666016 5.01305 0.736254 5.13807 0.861278C5.2631 0.986302 5.33333 1.15587 5.33333 1.33268C5.33333 1.50949 5.2631 1.67906 5.13807 1.80409C5.01305 1.92911 4.84348 1.99935 4.66667 1.99935H2C1.82319 1.99935 1.65362 2.06959 1.5286 2.19461C1.40357 2.31964 1.33333 2.4892 1.33333 2.66602V9.99935C1.33333 10.1762 1.40357 10.3457 1.5286 10.4708C1.65362 10.5958 1.82319 10.666 2 10.666H9.33333C9.51014 10.666 9.67971 10.5958 9.80474 10.4708C9.92976 10.3457 10 10.1762 10 9.99935V7.33268C10 7.15587 10.0702 6.9863 10.1953 6.86128C10.3203 6.73625 10.4899 6.66602 10.6667 6.66602C10.8435 6.66602 11.013 6.73625 11.1381 6.86128C11.2631 6.9863 11.3333 7.15587 11.3333 7.33268V9.99935C11.3333 10.5298 11.1226 11.0385 10.7475 11.4136C10.3725 11.7886 9.86377 11.9993 9.33333 11.9993Z"
                    fill="#FB5F66"
                  />
                  <path
                    d="M11.332 0H7.33199C7.15518 0 6.98561 0.0702379 6.86059 0.195262C6.73556 0.320286 6.66532 0.489856 6.66532 0.666667C6.66532 0.843478 6.73556 1.01305 6.86059 1.13807C6.98561 1.2631 7.15518 1.33333 7.33199 1.33333H9.72532L3.52532 7.52667C3.46284 7.58864 3.41324 7.66238 3.3794 7.74362C3.34555 7.82486 3.32812 7.91199 3.32812 8C3.32812 8.08801 3.34555 8.17515 3.3794 8.25638C3.41324 8.33762 3.46284 8.41136 3.52532 8.47333C3.5873 8.53582 3.66103 8.58541 3.74227 8.61926C3.82351 8.65311 3.91065 8.67053 3.99866 8.67053C4.08667 8.67053 4.1738 8.65311 4.25504 8.61926C4.33628 8.58541 4.41002 8.53582 4.47199 8.47333L10.6653 2.27333V4.66667C10.6653 4.84348 10.7356 5.01305 10.8606 5.13807C10.9856 5.2631 11.1552 5.33333 11.332 5.33333C11.5088 5.33333 11.6784 5.2631 11.8034 5.13807C11.9284 5.01305 11.9987 4.84348 11.9987 4.66667V0.666667C11.9987 0.489856 11.9284 0.320286 11.8034 0.195262C11.6784 0.0702379 11.5088 0 11.332 0Z"
                    fill="#FB5F66"
                  />
                </svg>
                View support doc
              </a>
            </div>
          </div>
        </div>
        <div className="webhook-data-box-container">
          <div className="data-box-label">
            <span className="webhook_head">
              {isConfigured ? "webhook flow" : "webhook endpoint"}
            </span>
          </div>
          <div className="data-box-wrap mb-20">
            <div className="row-container">
              <span className="input-label wh-bx-title">
                {isConfigured
                  ? "Trigger Flow End Point"
                  : "Send Data to Omniengage"}
                <div onClick={handleCopy}>
                  <span className="color-red cursor-pointer">Copy</span>
                </div>
              </span>
            </div>
            <div className="copy-url-btns">
              <input
                className="rightSidebar-tarea wh-input"
                type="text"
                value={apiKey ? webhookUrl : "Generate Api Key"}
                readOnly
              />
            </div>
          </div>
        </div>
        {!!webhookData.length && (
          <div className="webhook-data-box-container">
            <div className="data-box-label">
              <span className="webhook_head">VARIABLE DATA</span>
            </div>
            <div className="data-box-wrap">
              <div className="row-container">
                <span className="input-label wh-bx-title">Variables</span>
              </div>
              <div className="wh-table-outer thin-bar">
                <table
                  className={`wh-table ${
                    isConfigured ? "wh-table-background" : ""
                  }`}
                >
                  <thead>
                    <tr className="wh-table-background">
                      <th>Select</th>
                      <th>Name</th>
                      <th>Value</th>
                    </tr>
                  </thead>
                  <tbody>
                    {webhookData.map((data, index) => (
                      <tr key={index}>
                        <td>
                          <input
                            type="checkbox"
                            id={`wb-check${index}`}
                            checked={data.isSelected}
                            onChange={() => handleDataChange("select", index)}
                          />
                          <label
                            className={`${isConfigured ? "opacity-50" : ""}`}
                            htmlFor={`wb-check${index}`}
                          ></label>
                        </td>
                        <td>
                          <input
                            type="text"
                            value={data.name}
                            onChange={(event) =>
                              handleDataChange(
                                "name",
                                index,
                                event.target.value,
                              )
                            }
                          />
                        </td>
                        <td className="wh-table-background">
                          <input
                            type="text"
                            value={data.value || "null"}
                            readOnly
                          />
                        </td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        )}
      </div>
      <div className="btm-sticky__btn flex-cnt">
        <button
          className="saveDataBtn no-mt"
          onClick={() => {
            apiKey
              ? isConfigured
                ? handleReset()
                : handleSave()
              : generateApiKey();
          }}
        >
          {apiKey ? (isConfigured ? "Reset" : "Save") : "Generate API Key"}
        </button>
      </div>
    </div>
  );
}

interface WebhookData {
  id: string;
  name: string;
  value: string;
  isSelected: boolean;
}
