import { Panel } from "@xyflow/react";
import { useNavigate } from "react-router-dom";
import useStore from "../store";

export default function AgentPanel() {
  const navigate = useNavigate();
  const { createdBy, agentData } = useStore((state) => state.metaData);

  if (createdBy !== "agent") return null;

  return (
    <Panel position="top-center" className="agent-bar">
      <button
        className="agent-back-btn flex-cnt gap-5"
        onClick={() =>
          navigate(`/agent/${agentData.agentCode}/${agentData.agentChatId}`)
        }
      >
        <BackIcon /> Back to {agentData?.agentCode}
      </button>
    </Panel>
  );
}

const BackIcon = () => (
  <svg
  width={20}
  height={20}
  viewBox="0 0 20 20"
  fill="none"
  xmlns="http://www.w3.org/2000/svg"
>
  <path
    d="M7.975 4.94165L2.91666 9.99998L7.975 15.0583"
    stroke="#444444"
    strokeWidth={1.5}
    strokeMiterlimit={10}
    strokeLinecap="round"
    strokeLinejoin="round"
  />
  <path
    d="M17.0833 10H3.05833"
    stroke="#444444"
    strokeWidth={1.5}
    strokeMiterlimit={10}
    strokeLinecap="round"
    strokeLinejoin="round"
  />
</svg>
);
