import { AllTemplateSearchIcon } from "@/assets/AiBotAssets";
import { useEffect, useState } from "react";
import useWhatsAppConfig from "@/pages/omni-builder/react-query/configs/useWhatsAppConfig";
import axios from "axios";
import { useDispatch } from "react-redux";
import { userSlice } from "@/redux/User";
import { countryLanguages } from "@/constants/CountryLanguages";
import environment from "@/constants/Environment";
import WhatsApp from "../node-configs/WhatsApp";

const AllTemplates = ({
  whatsappTemplates,
  setShowModal,
  selectedWabaId,
  setEditTemplate,
}) => {
  const environmentData = environment();
  const [filterStatus, setFilterStatus] = useState<StatusType>("all");
  const [filteredData, setFilteredData] = useState<any>();
  const [searchVal, setSearchVal] = useState<string>("");
  const [checkedSet, setCheckedSet] = useState<Set<number>>(new Set());
  const [allChecked, setAllChecked] = useState<boolean>(false);
  const [openDropdownId, setOpenDropdownId] = useState<DropDownId>("");
  const [templateData, setTemplateData] = useState(whatsappTemplates);
  const whatsappConfiguration = useWhatsAppConfig();
  const dispatch = useDispatch();
  const accessToken = whatsappConfiguration?.data?.accessToken;
  const [deleteTemp, setDelete] = useState(false);

  const deleteTemplate = async (indexToDelete) => {
    try {
      const wabaId = selectedWabaId;
      const templateName = templateData[indexToDelete].name;
      const url = `https://graph.facebook.com/${environmentData.META_API_VERSION}/${wabaId}/message_templates?name=${templateName}`;

      const response = await axios.delete(url, {
        headers: {
          Authorization: `Bearer ${accessToken}`,
        },
      });

      if (response.status === 200) {
        // Remove deleted index from checkedSet
        const updatedCheckedSet = new Set(checkedSet);
        updatedCheckedSet.delete(indexToDelete);

        // Filter out the deleted template from templateData
        const updatedTemplateData = templateData.filter(
          (_, i) => i !== indexToDelete
        );

        // Filter out the deleted template from filteredData
        const updatedFilteredData = updatedTemplateData.filter((item) => {
          const statusMatches =
            filterStatus === "all" ||
            item.status.toLowerCase() === filterStatus;
          const nameMatches = item.name
            .toLowerCase()
            .includes(searchVal.toLowerCase());
          return statusMatches && nameMatches;
        });

        dispatch(
          userSlice.actions.setToastPopUp({
            show: true,
            toastType: "success",
            message: "Your Template Deleted Successfully",
          })
        );

        // Update state with a delay
        setTimeout(() => {
          setTemplateData(updatedTemplateData);
          setFilteredData(updatedFilteredData);
          setCheckedSet(updatedCheckedSet); // Update checkedSet
          setAllChecked(false);
        }, 1000); // Delay in milliseconds (adjust as needed)
      }
    } catch (error) {
      dispatch(
        userSlice.actions.setToastPopUp({
          show: true,
          toastType: "error",
          message: "Error deleting template",
        })
      );
    }
  };

  useEffect(() => {
    fetchTemplates();
  }, []);

  async function fetchTemplates() {
    try {
      const response = await axios.get(
        `https://graph.facebook.com/v19.0/${selectedWabaId}/message_templates`,
        {
          headers: {
            Authorization: `Bearer ${accessToken}`,
          },
        }
      );
      setTemplateData(response.data.data);
      setFilteredData(response.data.data);
    } catch (error) {
      dispatch(
        userSlice.actions.setToastPopUp({
          show: true,
          toastType: "error",
          message: "Error fetching templates",
        })
      );
    }
  }

  useEffect(() => {
    const element = document.querySelector(".builder") as HTMLDivElement;
    if (element) element.style.zIndex = "100";
    return () => {
      if (element) element.style.zIndex = "98";
    };
  }, []);

  useEffect(() => {
    const updatedData = templateData.filter((item) => {
      const statusMatches =
        filterStatus === "all" || item.status.toLowerCase() === filterStatus;
      const nameMatches = item.name
        .toLowerCase()
        .includes(searchVal.toLowerCase());
      return statusMatches && nameMatches;
    });
    setFilteredData(updatedData);
    setCheckedSet(new Set());
    setAllChecked(false);
  }, [filterStatus, searchVal]);

  function handleDropdown(id: DropDownId) {
    setOpenDropdownId(openDropdownId ? "" : id);
  }

  useEffect(() => {
    for (const index of checkedSet) {
      deleteTemplate(index);
    }
  }, [deleteTemp]);

  const toggleChecks = (index) => {
    const updatedSet = new Set(checkedSet);
    if (updatedSet.has(index)) {
      updatedSet.delete(index);
    } else {
      updatedSet.add(index);
    }
    setCheckedSet(updatedSet);
  };

  return (
    <div
      className="createbot-modal createbot-new-background modal-scroll whatsapp-template-modal all-template-modal"
      onClick={() => setShowModal("")}
    >
      <div className="call-api-container" onClick={(e) => e.stopPropagation()}>
        <div className="call-api-close close" onClick={() => setShowModal("")}>
          <svg
            width="10"
            height="10"
            viewBox="0 0 10 10"
            fill="#6D6D6D"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path
              d="M10 1.00714L8.99286 0L5 3.99286L1.00714 0L0 1.00714L3.99286 5L0 8.99286L1.00714 10L5 6.00714L8.99286 10L10 8.99286L6.00714 5L10 1.00714Z"
              fill="#6D6D6D"
            />
          </svg>
        </div>
        <div className="call-api-header">
          <h2>All template status</h2>
        </div>

        <div className="all-template-body fx fx-d-col">
          <div className="fx jstfy-sp-btw al-fx-cnt">
            <h3>WhatsApp templates</h3>
            <div className="header-right fx gap-20 al-fx-cnt">
              {(allChecked || checkedSet.size > 0) && (
                <div
                  className="cp del-template br-10 pd-10 fx"
                  onClick={() => setDelete(true)}
                >
                  <DeleteAllIcon />
                </div>
              )}
              <div className="searchbar fx al-fx-cnt">
                <AllTemplateSearchIcon />
                <input
                  type="text"
                  placeholder="Search templates"
                  value={searchVal}
                  onChange={(e) => setSearchVal(e.target.value)}
                  className="p-0"
                />
              </div>
              <div className="fx gap-10">
                <span className="ws-nowrap fx al-cnt">Sort by</span>
                <div
                  className="node-dropdown rightSidebar-tarea no-m br-10"
                  onClick={() => handleDropdown("filter")}
                  onMouseLeave={() => handleDropdown("")}
                >
                  <span>{filterStatus}</span>
                  <svg
                    width="10"
                    height="6"
                    viewBox="0 0 10 6"
                    fill="none"
                    xmlns="http://www.w3.org/2000/svg"
                  >
                    <path
                      d="M1 1L5 5L9 1"
                      stroke="#444444"
                      strokeWidth="2"
                      strokeLinecap="round"
                      strokeLinejoin="round"
                    />
                  </svg>
                  {openDropdownId === "filter" && (
                    <div className="dropdown-items w-100 full-view-menu template-filter">
                      <ul className="grey-scrollbar">
                        {Status.map((item) => (
                          <li
                            className={
                              item === filterStatus ? "active-item" : ""
                            }
                            onClick={() => setFilterStatus(item)}
                          >
                            {item}
                          </li>
                        ))}
                      </ul>
                    </div>
                  )}
                </div>
              </div>
            </div>
          </div>
          <table className={`ovr-flw-au br-10 fx fx-d-col ${!filteredData?.length ? "h-100" : "" }`}>
            <tr className="fs-0">
              {Rows.map((item) => (
                <th className="gap-10 capitalize text-clr fw-500">
                  {item === "name" && (
                    <label className="checkbox-container link-mark no-m">
                      <input
                        className="bot-check"
                        type="checkbox"
                        checked={allChecked}
                        onChange={(e) => setAllChecked(!allChecked)}
                      />
                      <span className="mark">
                        <svg
                          xmlns="http://www.w3.org/2000/svg"
                          width="16"
                          height="16"
                          viewBox="0 0 16 16"
                          fill="none"
                        >
                          <rect width="16" height="16" rx="5" fill="#FB5F66" />
                          <path
                            d="M11.6923 4.11088C11.3794 3.90411 10.9616 3.99489 10.7589 4.31353L6.9773 10.2532L5.13304 8.51563C4.85926 8.25774 4.43251 8.2747 4.17942 8.55368C3.92634 8.83243 3.94298 9.26775 4.21677 9.52541L6.63828 11.8068C6.63828 11.8068 6.70779 11.8677 6.73996 11.8891C6.85334 11.9642 6.98067 12 7.10665 12C7.32757 12 7.54421 11.8895 7.67356 11.6864L11.8912 5.06198C12.0941 4.74334 12.005 4.31742 11.6923 4.11088Z"
                            fill="white"
                          />
                        </svg>
                      </span>
                    </label>
                  )}
                  {item}
                </th>
              ))}
            </tr>
            {filteredData?.map((item, index) => (
              <tr className="fs-0">
                <td className="gap-10 text-clr capitalize">
                  <label className="checkbox-container link-mark">
                    <input
                      className="bot-check"
                      type="checkbox"
                      checked={allChecked || checkedSet.has(index)}
                      onChange={() => toggleChecks(index)}
                      onClick={
                        allChecked
                          ? () => setCheckedSet(new Set([index]))
                          : undefined
                      }
                    />
                    <span className="mark">
                      <svg
                        xmlns="http://www.w3.org/2000/svg"
                        width="16"
                        height="16"
                        viewBox="0 0 16 16"
                        fill="none"
                      >
                        <rect width="16" height="16" rx="5" fill="#FB5F66" />
                        <path
                          d="M11.6923 4.11088C11.3794 3.90411 10.9616 3.99489 10.7589 4.31353L6.9773 10.2532L5.13304 8.51563C4.85926 8.25774 4.43251 8.2747 4.17942 8.55368C3.92634 8.83243 3.94298 9.26775 4.21677 9.52541L6.63828 11.8068C6.63828 11.8068 6.70779 11.8677 6.73996 11.8891C6.85334 11.9642 6.98067 12 7.10665 12C7.32757 12 7.54421 11.8895 7.67356 11.6864L11.8912 5.06198C12.0941 4.74334 12.005 4.31742 11.6923 4.11088Z"
                          fill="white"
                        />
                      </svg>
                    </span>
                  </label>
                  <span className="ellipsis">{item.name}</span>
                </td>
                <td className="ellipsis">
                  Hi customer, we have some amazing offers for you Hi customer,
                  we have some amazing offers for you Hi customer, we have some
                  amazing offers for you
                </td>
                <td className="grid-cnt approval-status">
                  <span
                    className={`br-10 capitalize ${item.status.toLowerCase()}`}
                  >
                    {item.status.toLowerCase()}
                  </span>
                </td>
                <td className="capitalize">{item.category.toLowerCase()}</td>
                <td className="capitalize">
                  {
                    countryLanguages.find(
                      (value: any) => value.code === item.language
                    ).name
                  }
                </td>
                <td className="grid-cnt">
                  <div
                    className={`node-dropdown rightSidebar-tarea w-100 no-bg p-0 no-m ${
                      index === filteredData.length - 1 ||
                      index === filteredData.length - 2
                        ? "dropdown-upside"
                        : ""
                    }`}
                    onClick={() => handleDropdown(index)}
                    onMouseLeave={() => handleDropdown("")}
                  >
                    <ThreeDot />
                    {openDropdownId === index && (
                      <div className="dropdown-items">
                        <ul className="grey-scrollbar">
                          {Operations?.map((operationItem, operationIndex) => (
                            <li
                              key={operationIndex}
                              onClick={() => {
                                if (operationItem.name === "delete") {
                                  deleteTemplate(index);
                                }
                                if (operationItem.name === "edit") {
                                  setEditTemplate(item);
                                  setShowModal("new");
                                }
                              }}
                              className={`fx gap-10`}
                            >
                              {operationItem.icon}
                              {operationItem.name}
                            </li>
                          ))}
                        </ul>
                      </div>
                    )}
                  </div>
                </td>
              </tr>
            ))}
            {!filteredData?.length && (
              <div className="no-template-selected h-100 flex-cnt fx-d-col">
                <img
                  src="https://dlvkyia8i4zmz.cloudfront.net/oFnzNGTS6etZPwRyf73g_Group_26815.svg"
                  alt="no-template"
                />
                <h4 className="mt-20">No Data Found</h4>
              </div>
            )}
          </table>
        </div>

        <div className="call-api-footer">
          <button className="save-btn px-15 w-mc" onClick={() => setShowModal("new")}>
            Back to request template
          </button>
        </div>
      </div>
    </div>
  );
};

export default AllTemplates;

