import { useQuery } from "@tanstack/react-query";
import { axiosGet } from "@/services/axiosService";
import useStore, { useResetStore } from "../store";
import { dbToReactFlow } from "../utils/botTransformer";

export default function useBot(botUrl: string | undefined) {
  return useQuery({
    queryKey: ["bot", "completeBot", { botUrl }],
    queryFn: async () => {
      if (!botUrl) throw new Error("Bot url missing");
      const response = await axiosGet(`/users/completeBot?url=${botUrl}`);
      const data = dbToReactFlow(response.data.data);

      useResetStore();
      useStore.setState(data);

      if (
        data.metaData.botType === "campaign" &&
        data.metaData.campaignType === "segment"
      ) {
        const listId = data.nodes[0].data?.audienceData?.listId;
        if (listId) useStore.getState().fetchSegmentData(listId);
      }

      return response.data.data;
    },
    gcTime: 0,
  });
}
