import { useState, useEffect } from "react";
import useStore from "../store";
import VariableDropdown from "../components/VariableDropdown";
import { SendSMSIcon } from "@/assets/NodeIcon";
import type { NodeConfigProps } from "../types";
import Froala from "../components/Froala";
import GenWithAiModal from "@/components/GenWithAiModal";
import ModalWrapper from "../components/ModalWrapper";
import AddIcon from "@/assets/AddIcon";
import useSmsConfig from "../react-query/configs/useSmsConfig";
import { axiosPost } from "@/services/axiosService";
import { useDispatch, useSelector } from "react-redux";
import { userSlice } from "@/redux/User";

export default function SendSms({
  data,
  onDataChange,
  onSave,
}: NodeConfigProps) {
  const setOpenConfigModalId = useStore((state) => state.setOpenConfigModalId);
  const [openDropdownId, setOpenDropdownId] = useState<DropDownId>("");
  const [configName, setConfigName] = useState("");
  const [showGenWithAiModal, setShowGenWithAiModal] = useState(false);
  const [loader, setLoader] = useState(false);
  const { data: configData, status: configStatus } = useSmsConfig();
  const dispatch = useDispatch();
  const metaData = useStore((state) => state.metaData);

  function generateContent(body: string) {
    setLoader(true);
    const content = `I would like to update the  content based on the following information:
    Body: ${body}
    Please enhance the professionalism and engagement of the body. The return body should be in JSON format.
    
    give response as 

    {
      body: body
    }
    `;

    const smsBody = {
      configId: "6629fd49faa87bdbb7f9ded5",
      content: content,
    };

    axiosPost(`/configurations/completechat?responses=${1}`, smsBody)
      .then((response) => {
        if (response.status === 200) {
          setLoader(false);
          let tempArr: any = [];
          response.data.choices.forEach((data, index) => {
            tempArr.push(JSON.parse(data?.message.content));
          });
          tempArr && onDataChange("sendSMSDetails.smsBody", tempArr[0].body);
        }
      })
      .catch((err) => {
        setLoader(false);
        dispatch(
          userSlice.actions.setToastPopUp({
            show: true,
            toastType: "error",
            message:
              err.response?.data?.message ??
              err.message ??
              "Something went wrong, please try again.",
          }),
        );
      });
  }

  // useEffect(() => {
  //   if (showGenWithAiModal) {
  //     generateContent(data.sendSMSDetails.smsBody);
  //   }
  // }, [showGenWithAiModal]);

  useEffect(() => {
    if (configData) {
      const selectedConfig = configData.find(
        (config) => config._id === data.sendSMSDetails.smsConfig,
      );

      if (selectedConfig) {
        setConfigName(selectedConfig.configName);
      } else if (configData[0]) {
        const config = configData[0];
        onDataChange("sendSMSDetails.smsConfig", config._id);
        setConfigName(config.configName);
      } else if (data.sendSMSDetails.smsConfig) {
        onDataChange("sendSMSDetails.smsConfig", "");
      }
    }
  }, [configData]);

  function handleDropdown(id: DropDownId) {
    setOpenDropdownId(openDropdownId ? "" : id);
  }

  function handleSave() {
    onSave();
  }

  return (
    <div className="innerRightSideBar">
      <div className="title-header fx al-cnt jstfy-sp-btw">
        <div className="flex-cnt gap-10">
          <div
            className="node-icn flex-cnt"
            style={{ background: "#19B21F0D", border: "1px solid #19B21F1A" }}
          >
            <SendSMSIcon />
          </div>
          <div className="grid gap-6">
            <h1>
              <span>Edit</span> SMS Configuration
            </h1>
            <p>
              Send mobile SMS to user at that instance using Twilio
              configuration.
            </p>
          </div>
        </div>
      </div>
      <div className="data-box-container fx fx-d-col gap-20">
        <div>
          <span className="data-box-label">Integration Details</span>
          <div
            className={`data-box-wrap data-box-half-width ${
              !configData?.length ? "create-new-integration" : ""
            }`}
          >
            <div>
              <div className="sms-snd-onfiguation">
                <div
                  className="node-dropdown variableTypeDropDown rightSidebar-tarea"
                  onMouseLeave={() => handleDropdown("")}
                >
                  {configStatus === "pending" ? (
                    <div className="react-loading-skeleton"></div>
                  ) : configData?.length ? (
                    <div
                      className="config-button"
                      onClick={() => handleDropdown("config")}
                    >
                      <span>{configName || "Select Configuration"}</span>
                      <svg
                        width="10"
                        height="7"
                        viewBox="0 0 10 7"
                        fill="none"
                        xmlns="http://www.w3.org/2000/svg"
                      >
                        <path
                          d="M1 1.5L5 5.5L9 1.5"
                          stroke="#444444"
                          strokeWidth="2"
                          strokeLinecap="round"
                          strokeLinejoin="round"
                        />
                      </svg>
                      {openDropdownId === "config" && (
                        <div className="dropdown-items w-100 full-view-menu">
                          <ul className="grey-scrollbar config-list">
                            {configData.map((config, index) => (
                              <li
                                key={index}
                                className={
                                  config?.configName === configName
                                    ? "active-item"
                                    : ""
                                }
                                onClick={() => {
                                  onDataChange(
                                    "sendSMSDetails.smsConfig",
                                    config._id,
                                  );
                                  setConfigName(data.configName);
                                }}
                                value={config._id}
                              >
                                {config.configName}
                              </li>
                            ))}
                          </ul>
                          <span
                            className="add-condition add_new_button add-acc p-absolute bottom-0 left-0 cursor-pointer"
                            onClick={() => setOpenConfigModalId("twilio")}
                          >
                            <AddIcon />
                            Add Configuration
                          </span>
                        </div>
                      )}
                    </div>
                  ) : (
                    <span onClick={() => setOpenConfigModalId("twilio")}>
                      Create new configuration
                    </span>
                  )}
                </div>
              </div>
            </div>
          </div>
        </div>
        <div>
          <span className="data-box-label fx jstfy-sp-btw">
            Message Details
          </span>
          <div className="data-box-wrap data-box-half-width">
            <div>
              <div className="fx al-fx-cnt">
                <span className="input-label">Phone Number</span>
                <div
                  className="node-dropdown h-mc"
                  onClick={() => handleDropdown("phone")}
                  onMouseLeave={() => handleDropdown("")}
                >
                  <span className="color-red">Add Variable</span>
                  {openDropdownId === "phone" && (
                    <VariableDropdown
                      onSelect={(variable) =>
                        onDataChange(
                          "sendSMSDetails.to",
                          `{ ${variable.name} }`,
                        )
                      }
                    />
                  )}
                </div>
              </div>
              <div className="node-dropdown d-block mt-10">
                <input
                  className="rightSidebar-tarea condition no-m"
                  value={data.sendSMSDetails.to}
                  placeholder="Type or select the variable"
                  onChange={(event) =>
                    onDataChange("sendSMSDetails.to", event.target.value)
                  }
                />
              </div>
            </div>
          </div>
        </div>
        <div>
          <span className="data-box-label fx jstfy-sp-btw">
            Message Content
            {metaData?.botType != "campaign" && (<div
              className="generate-btn fx al-cnt"
              onClick={() => setShowGenWithAiModal(true)}
            >
              <svg
                width="14"
                height="14"
                viewBox="0 0 14 14"
                fill="none"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  d="M12.25 3.50001C12.25 2.53348 13.0335 1.75 14 1.75C13.0335 1.75 12.25 0.966502 12.25 0C12.25 0.966473 11.4665 1.75 10.5 1.75C11.4665 1.75 12.25 2.53345 12.25 3.50001ZM12.6875 5.24998C12.6875 5.97484 12.0999 6.56248 11.375 6.56248C12.0999 6.56248 12.6875 7.15011 12.6875 7.87497C12.6875 7.15008 13.2751 6.56248 14 6.56248C13.2751 6.56251 12.6875 5.97487 12.6875 5.24998ZM6.125 5.24998C6.125 3.80023 7.30024 2.62499 8.74999 2.62499C7.30024 2.62499 6.125 1.44975 6.125 0C6.125 1.44975 4.94976 2.62499 3.50001 2.62499C4.94976 2.62499 6.125 3.80026 6.125 5.24998ZM9.62501 2.62499L0 12.25L1.75 14L11.375 4.37499L9.62501 2.62499ZM8.13134 5.43115L9.36863 4.19386L9.80614 4.63137L8.56885 5.86866L8.13134 5.43115Z"
                  fill="#FB5F66"
                />
              </svg>
              {loader ? "Generating..." : "Generate with AI "}
            </div>)}
          </span>
          <div className="data-box-wrap">
            <div>
              <div className="row-container">
                <span className="input-label">Body</span>
              </div>
              <Froala
                type="sms"
                content={data.sendSMSDetails.smsBody}
                onContentChange={(content) =>
                  onDataChange("sendSMSDetails.smsBody", content)
                }
              />
            </div>
          </div>
        </div>
      </div>
      <div className="btm-sticky__btn flex-cnt">
        <button onClick={handleSave} className="saveDataBtn">
          Save
        </button>
      </div>
      {showGenWithAiModal && (
        <ModalWrapper>
          <GenWithAiModal
            nodeName="sendSms"
            onSave={(subject, body) =>
              onDataChange("sendSMSDetails.smsBody", subject + "\n\n" + body)
            }
            onClose={() => setShowGenWithAiModal(false)}
            setEditEmailTable={() => {}}
            setShowGenWithAiModal={() => {}}
          />
        </ModalWrapper>
      )}
    </div>
  );
}

type DropDownId = "config" | "phone" | "";
