import { useState, useEffect, useRef } from "react";
import { Panel, useReactFlow, useNodesData, type Node } from "@xyflow/react";
import { motion } from "framer-motion";
import useStore from "../store";
import useAutoSave from "../AutoSaveContext";
import type { NodeConfigProps } from "../types";
import { useDispatch } from "react-redux";
import { userSlice } from "@/redux/User";
import setValueByPath from "@/utils/setValueByPath";
import AskQuestion from "../node-configs/AskQuestion";
import Button from "../node-configs/Button";
import Email from "../node-configs/Email";
import Phone from "../node-configs/Phone";
import Username from "../node-configs/Username";
import Address from "../node-configs/Address";
import Result from "../node-configs/Result";
import SendMessage from "../node-configs/SendMessage";
import SocialData from "../node-configs/SocialData";
import Segment from "../node-configs/Segment";
import LiveChat from "../node-configs/LiveChat";
import SendEmail from "../node-configs/SendEmail";
import SendSms from "../node-configs/SendSms";
import HitApi from "../node-configs/HitApi";
import DelayAction from "../node-configs/DelayAction";
import PushNotification from "../node-configs/PushNotification";
import AbTest from "../node-configs/AbTest";
import Condition from "../node-configs/Condition";
import Assignees from "../node-configs/Assignees";
import WhatsApp from "../node-configs/WhatsApp";
import Envoie from "../node-configs/Envoie";
import OpenAi from "../node-configs/OpenAi";
import LinkedIn from "../node-configs/LinkedIn";
import DynamicApps from "../node-configs/DynamicApps";
import Sticky from "../node-configs/Sticky";

export default function NodeConfigPanel() {
  const { updateNodeData } = useReactFlow();
  const autoSave = useAutoSave();
  const dispatch = useDispatch();
  const selectNode = useStore((state) => state.selectNode);
  const selectedNodeId = useStore((state) => state.selectedNodeId);
  const campaignType = useStore((state) => state.metaData.campaignType);
  const renderFromPanel = useRef(false); // use to skip render while nodeData updated inside Config Panel
  const _nodeData = useNodesData(selectedNodeId);

  // XYFlow update nodeData asynchronously(in batches), so we can't directly use useNodesData, a local nodeData state is required to preserve
  // caret position. Ref: https://github.com/xyflow/xyflow/blob/main/examples/react/src/examples/UseNodesData/TextNode.tsx
  const [nodesData, setNodesData] = useState<Pick<
    Node,
    "id" | "type" | "data"
  > | null>(null);

  useEffect(() => {
    if (renderFromPanel.current) {
      renderFromPanel.current = false;
    } else {
      setNodesData(_nodeData);
    }
  }, [_nodeData, campaignType]);

  function handleDataChange(path: string, value: any, replace?: boolean) {
    if (!nodesData) return;
    const data = replace ? value : setValueByPath(nodesData.data, path, value);

    updateNodeData(selectedNodeId, data);
    setNodesData({ ...nodesData, data });
    renderFromPanel.current = true;
    autoSave();
  }

  function handleSave(data?: Record<string, any>) {
    if (!nodesData) return;
    if (data) {
      updateNodeData(selectedNodeId, data);
      setNodesData({ ...nodesData, data });
      renderFromPanel.current = true;
      autoSave();
    }
    selectNode("");
    dispatch(
      userSlice.actions.setToastPopUp({
        show: true,
        toastType: "success",
        message: "Data Saved",
      }),
    );
  }

  function handlePanelClose() {
    selectNode("");
  }

  if (!nodesData || !nodesData.type) return null;

  const PanelComponent =
    (nodesData.type === "campaign"
      ? panelComponentData[campaignType]
      : panelComponentData[nodesData.type]) ?? panelComponentData["unknown"];

  return (
    <Panel position="top-right" style={{ zIndex: 6 }}>
      <motion.div
        className="rightSidebar new-scrollbar"
        initial={{ right: "-100%" }}
        animate={{ right: "0px" }}
      >
        <PanelComponent
          data={nodesData.data}
          onDataChange={handleDataChange}
          onSave={handleSave}
          onClose={handlePanelClose}
        />
      </motion.div>
    </Panel>
  );
}

const panelComponentData: Record<string, React.FC<NodeConfigProps>> = {
  ask_question: AskQuestion,
  button: Button,
  email: Email,
  phone: Phone,
  username: Username,
  address: Address,
  result: Result,
  sendMessage: SendMessage,
  socialData: SocialData,
  segment: Segment,
  liveChat: LiveChat,
  sendEmail: SendEmail,
  sendSMS: SendSms,
  hitApi: HitApi,
  delayAction: DelayAction,
  pushNotification: PushNotification,
  abTest: AbTest,
  condition: Condition,
  assignees: Assignees,
  whatsApp: WhatsApp,
  envoie: Envoie,
  openai: OpenAi,
  linkedIn: LinkedIn,
  dynamicApps: DynamicApps,
  sticky: Sticky,
  unknown: () => null,
};
