import { AssigneesIcon } from "@/assets/NodeIcon";
import type { NodeConfigProps } from "../types";
import AssigneesRoundRobin from "../components/AssigneesRoundRobin";
import AssigneesCondition from "../components/AssigneesCondition";
import useTeamUsers from "../react-query/useTeamUsers";

export default function Assignees({
  data,
  onDataChange,
  onSave,
}: NodeConfigProps) {
  const { data: teamMembers } = useTeamUsers();

  function handleSave() {
    onSave();
  }

  return (
    <div className="innerRightSideBar assignees-section">
      <div className="title-header fx al-cnt jstfy-sp-btw">
        <div className="flex-cnt gap-10">
          <div
            className="node-icn flex-cnt"
            style={{ background: "#8C33D20D", border: "1px solid #8C33D21A" }}
          >
            <AssigneesIcon />
          </div>
          <div className="grid gap-6">
            <h1>
              <span>Assign to</span>Teammate
            </h1>
            <p>
              Assign leads to team members using Round robin or custom
              conditional logic.
            </p>
          </div>
        </div>
      </div>
      <div className="data-box-container fx fx-d-col gap-20">
        <div>
          <div className="data-box-label jstfy-sp-btw">Assignment Logic</div>
          <div className="info-dummy">
            {`Defined logic allocates leads to teammates effectively ${
              data.assigneesData.logicType === "round_robin"
                ? "on a round-robin basis"
                : "according to the custom distribution logic configured"
            }.`}
          </div>
          <div className="fx gap-20">
            <div
              className="data-box-wrap wd-100 cp"
              onClick={() =>
                onDataChange("assigneesData.logicType", "round_robin")
              }
            >
              <div className="fx al-cnt gap-10">
                <label className="assign-logic fx cp">
                  <input
                    type="radio"
                    checked={data.assigneesData.logicType === "round_robin"}
                  />
                  Round Robin Logic
                </label>
                <div className="assignees-tp p-relative cp grid">
                  <svg
                    width="16"
                    height="16"
                    viewBox="0 0 16 16"
                    fill="none"
                    xmlns="http://www.w3.org/2000/svg"
                  >
                    <path
                      d="M7.9987 14.6673C11.6654 14.6673 14.6654 11.6673 14.6654 8.00065C14.6654 4.33398 11.6654 1.33398 7.9987 1.33398C4.33203 1.33398 1.33203 4.33398 1.33203 8.00065C1.33203 11.6673 4.33203 14.6673 7.9987 14.6673Z"
                      stroke="#666666"
                      strokeWidth="1.5"
                      strokeLinecap="round"
                      strokeLinejoin="round"
                    />
                    <path
                      opacity="0.34"
                      d="M8 5.33398V8.66732"
                      stroke="#666666"
                      strokeWidth="1.5"
                      strokeLinecap="round"
                      strokeLinejoin="round"
                    />
                    <path
                      opacity="0.34"
                      d="M7.99609 10.666H8.00208"
                      stroke="#666666"
                      strokeWidth="2"
                      strokeLinecap="round"
                      strokeLinejoin="round"
                    />
                  </svg>
                  <span className="as-tp p-absolute as-tp-r">
                    Round Robin Logic assigns leads to team members in a
                    rotating, equal-turn allocation, ensuring each member gets
                    their fair share of leads over time
                  </span>
                </div>
              </div>
            </div>
            <div
              className="data-box-wrap wd-100 cp"
              onClick={() =>
                onDataChange("assigneesData.logicType", "conditional")
              }
            >
              <div className="fx al-cnt gap-10">
                <label className="assign-logic fx cp">
                  <input
                    type="radio"
                    checked={data.assigneesData.logicType === "conditional"}
                  />
                  Add custom conditional logic
                </label>
                <div className="assignees-tp p-relative cp grid">
                  <svg
                    width="16"
                    height="16"
                    viewBox="0 0 16 16"
                    fill="none"
                    xmlns="http://www.w3.org/2000/svg"
                  >
                    <path
                      d="M7.9987 14.6673C11.6654 14.6673 14.6654 11.6673 14.6654 8.00065C14.6654 4.33398 11.6654 1.33398 7.9987 1.33398C4.33203 1.33398 1.33203 4.33398 1.33203 8.00065C1.33203 11.6673 4.33203 14.6673 7.9987 14.6673Z"
                      stroke="#666666"
                      strokeWidth="1.5"
                      strokeLinecap="round"
                      strokeLinejoin="round"
                    />
                    <path
                      opacity="0.34"
                      d="M8 5.33398V8.66732"
                      stroke="#666666"
                      strokeWidth="1.5"
                      strokeLinecap="round"
                      strokeLinejoin="round"
                    />
                    <path
                      opacity="0.34"
                      d="M7.99609 10.666H8.00208"
                      stroke="#666666"
                      strokeWidth="2"
                      strokeLinecap="round"
                      strokeLinejoin="round"
                    />
                  </svg>
                  <span className="as-tp p-absolute as-tp-cc">
                    Custom Conditional Logic assigns leads to selected team
                    members based on specific logic configurations defined in
                    Omniengage, allowing for personalized and flexible lead
                    allocation.
                  </span>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div>
          <div className="allow-email data-box-wrap fx al-cnt jstfy-sp-btw">
            Notify assignee when a new lead or reach out request is assigned
            <div className="toggle-btn">
              <label className="switch">
                <input
                  type="checkbox"
                  onClick={() =>
                    onDataChange(
                      "assigneesData.sendEmailToAssignee",
                      !data.assigneesData.sendEmailToAssignee
                    )
                  }
                  checked={data.assigneesData.sendEmailToAssignee}
                />
                <span className="toggle round"></span>
              </label>
            </div>
          </div>
        </div>
        {data.assigneesData.logicType === "round_robin" && (
          <AssigneesRoundRobin
            teamMembers={teamMembers!}
            roundRobinData={data.assigneesData.roundRobinData}
            onDataChange={onDataChange}
          />
        )}
        {data.assigneesData.logicType === "conditional" && (
          <AssigneesCondition
            teamMembers={teamMembers!}
            conditionalData={data.assigneesData.conditionalData}
            onDataChange={onDataChange}
          />
        )}
      </div>
      <div className="btm-sticky__btn flex-cnt">
        <button className="saveDataBtn" onClick={handleSave}>
          Save
        </button>
      </div>
    </div>
  );
}
