import { useState, type MouseEvent } from "react";
import { produce } from "immer";
import { useDispatch } from "react-redux";
import { userSlice } from "@/redux/User";
import { axiosPost } from "@/services/axiosService";
import VariableDropdown from "../components/VariableDropdown";
import { CallApiIcon } from "@/assets/NodeIcon";
import type { Variable, NodeConfigProps } from "../types";
import HitApiTable from "../components/HitApiTable";
import HitApiVarSelector from "../components/HitApiVarSelector";
import ModalWrapper from "../components/ModalWrapper";
import DropDownArrow from "@/assets/DropDownArrow";
import useStore from "../store";

export default function HitApi({
  data,
  onDataChange,
  onSave,
}: NodeConfigProps) {
  const dispatch = useDispatch();
  const selectedNodeId = useStore((state) => state.selectedNodeId);
  const [fullUrl, setFullUrl] = useState<string>(
    generateFullUrl(data.hitApiDetails.url, data.hitApiDetails.params)
  );
  const [codeValue, setCodeValue] = useState<string>("");
  const [openDropdownId, setOpenDropdownId] = useState<DropDownId>("");
  const [contentTab, setContentTab] = useState<ContentTab>("headers");
  const [bodyTab, setBodyTab] = useState<BodyTab>("table");
  const [cursorData, setCursorData] = useState<Cursor | null>(null);
  const [codeErr, setCodeErr] = useState<string | null>(null);
  const [responseData, setResponseData] = useState<Record<string, any>>({});
  const [apiCallLoader, setApiCallLoader] = useState<boolean>(false);
  const [showVarSelector, setShowVariableSelector] = useState<boolean>(false);
  const [expand, setExpand] = useState(false);

  function handleCursor(
    event: MouseEvent<HTMLTextAreaElement | HTMLInputElement>,
    type: Exclude<Cursor["type"], "table">
  ) {
    const result: Cursor = {
      type,
      startPos:
        event.currentTarget.selectionStart ?? event.currentTarget.value.length,
      endPos:
        event.currentTarget.selectionEnd ?? event.currentTarget.value.length,
    };

    setCursorData(result);
  }

  function handleCodeValueChange(value: string) {
    try {
      const bodyData = convertCodeToTable(data.hitApiDetails.body, value);

      onDataChange("hitApiDetails.body", bodyData);
      setCodeErr(null);
    } catch (err: any) {
      setCodeErr(err.message ?? "Invalid JSON format!");
    } finally {
      setCodeValue(value);
    }
  }

  function handleUrlChange(value: string) {
    const pos = value.search(/\?\S/);
    const url = pos === -1 ? value : value.slice(0, pos);
    const paramStr = pos === -1 ? "?=" : value.slice(pos);
    const params = new URLSearchParams(paramStr);

    const testValues = {};
    for (const item of data.hitApiDetails.params) {
      testValues[item.key] = item.testValue;
    }

    const paramData: Array<Row> = [];
    for (const [key, value] of params) {
      paramData.push({
        key,
        value,
        testValue: testValues[key],
      });
    }

    setFullUrl(value);
    onDataChange("hitApiDetails", {
      ...data.hitApiDetails,
      url,
      params: paramData,
    });
  }

  function handleTableVarSelect(variable: Variable) {
    if (!cursorData) return;

    if (cursorData.type === "table") {
      const tableData = produce(data.hitApiDetails[contentTab], (draft) => {
        const value = draft[cursorData.rowIndex][cursorData.fieldType];
        const result = placeVariable(value, variable, cursorData);
        draft[cursorData.rowIndex][cursorData.fieldType] = result;
      });

      onDataChange(`hitApiDetails.${contentTab}`, tableData);
    } else if (cursorData.type === "code") {
      const result = placeVariable(codeValue, variable, cursorData);
      handleCodeValueChange(result);
    } else {
      const result = placeVariable(
        data.hitApiDetails.url,
        variable,
        cursorData
      );
      handleUrlChange(result);
    }
  }

  function handleBodyTabChange(toTab: BodyTab) {
    if (bodyTab === toTab) return;
    if (codeErr) {
      dispatch(
        userSlice.actions.setToastPopUp({
          show: true,
          toastType: "error",
          message: "Invalid JSON format!",
        })
      );
      return;
    }

    if (toTab === "code") {
      const bodyObj = {};
      for (const item of data.hitApiDetails.body) {
        bodyObj[item.key] = item.value;
      }
      setCodeValue(JSON.stringify(bodyObj, null, 4));
    }
    setBodyTab(toTab);
  }

  function handleDropdown(id: DropDownId) {
    setOpenDropdownId(openDropdownId ? "" : id);
  }

  async function testApi() {
    try {
      setApiCallLoader(true);
      const headers = parseApiData(data.hitApiDetails.headers);
      const params = parseApiData(data.hitApiDetails.params);
      const body = parseApiData(data.hitApiDetails.body);

      const response = await axiosPost("/livebot/testCallAPI", {
        method: data.hitApiDetails.method,
        url: data.hitApiDetails.url,
        headers,
        params,
        data: body,
      });

      setShowVariableSelector(true);
      setResponseData(response.data);
    } catch (error: any) {
      console.log(error);
      dispatch(
        userSlice.actions.setToastPopUp({
          show: true,
          toastType: "error",
          message:
            error.response?.message ?? error.message ?? "Api test failed",
        })
      );
    } finally {
      setApiCallLoader(false);
    }
  }

  function parseApiData(data: Array<Row>) {
    const result: Record<string, string> = {};

    for (const item of data) {
      if (!item.key) continue;
      result[item.key] = item.testValue;
    }

    return result;
  }

  return (
    <div className="innerRightSideBar">
      <div className="title-header fx al-cnt jstfy-sp-btw">
        <div className="flex-cnt gap-10">
          <div
            className="node-icn flex-cnt"
            style={{
              background: "rgba(197, 71, 71, 0.10)",
              border: "1px solid rgba(197, 71, 71, 0.10)",
            }}
          >
            <CallApiIcon />
          </div>
          <div className="grid gap-6">
            <h1>
              <span>Edit</span>Call Api
            </h1>
            <p>
              Trigger an API call with all API methods such as GET, PUT, POST,
              PATCH and DELETE.
            </p>
          </div>
        </div>
      </div>
      <div className="data-box-container">
        <div className="data-box-label call-api">API details</div>
        <div className="data-box-wrap mg-btm">
          <div className="callapi-input-wrap al-fx-end">
            <div className="callapi-input">
              <span className="input-label">Method</span>
              <div
                className="node-dropdown variableTypeDropDown rightSidebar-tarea"
                onClick={() => handleDropdown("method")}
                onMouseLeave={() => handleDropdown("")}
              >
                <p>{data.hitApiDetails.method}</p>
                <DropDownArrow />
                {openDropdownId === "method" && (
                  <div className="dropdown-items api-menu pd-10">
                    <ul>
                      {METHOD.map((method, index) => (
                        <li
                          key={index}
                          onClick={() =>
                            onDataChange("hitApiDetails.method", method)
                          }
                        >
                          {method}
                        </li>
                      ))}
                    </ul>
                  </div>
                )}
              </div>
            </div>
            <div className="callapi-input">
              <div className="fx al-fx-cnt">
                <span className="input-label">URL</span>
                <div
                  className="node-dropdown h-mc"
                  onClick={() => handleDropdown("url")}
                  onMouseLeave={() => handleDropdown("")}
                >
                  <span className="color-red">Add Variable</span>
                  {openDropdownId === "url" && (
                    <VariableDropdown onSelect={handleTableVarSelect} />
                  )}
                </div>
              </div>
              <div className="copy-url-btns url-links-input">
                <input
                  className="rightSidebar-tarea"
                  placeholder="Enter URL links..."
                  value={fullUrl}
                  onClick={(event) => handleCursor(event, "url")}
                  onChange={(event) => handleUrlChange(event.target.value)}
                />
              </div>
            </div>
          </div>
        </div>
        <div className="data-box-label call-api">Contents</div>
        <div className="data-box-wrap">
          <div className="call-api-tabs w-100 fx al-cnt gap-10">
            {CONTENT_TAB.map((tab, index) => (
              <div
                key={index}
                className={tab.id === contentTab ? "active-tab" : ""}
                onClick={() => setContentTab(tab.id)}
              >
                {tab.name}
              </div>
            ))}
          </div>
          <div className="edit-header-section">
            <div className="fx al-cnt jstfy-sp-btw mb-10">
              <label>{contentTab} Contents</label>
              <div className="edit-body-right-section">
                {contentTab === "body" && (
                  <>
                    <div className="edit-body-tab">
                      {BODY_TAB.map((tab, index) => (
                        <button
                          key={index}
                          className={
                            tab.id === bodyTab
                              ? "active-tab-body flex-cnt"
                              : "flex-cnt"
                          }
                          onClick={() => handleBodyTabChange(tab.id)}
                        >
                          {tab.icon}
                        </button>
                      ))}
                    </div>
                    <hr />
                  </>
                )}
                <div
                  className="node-dropdown h-mc"
                  onClick={() => handleDropdown("content")}
                  onMouseLeave={() => handleDropdown("")}
                >
                  <span className="color-red">Add Variable</span>
                  {openDropdownId === "content" && (
                    <VariableDropdown onSelect={handleTableVarSelect} />
                  )}
                </div>
              </div>
            </div>
            {contentTab === "headers" && (
              <HitApiTable
                data={data.hitApiDetails.headers}
                onDataChange={(tableData) =>
                  onDataChange("hitApiDetails.headers", tableData)
                }
                onCursorChange={setCursorData}
                expand={expand}
              />
            )}
            {contentTab === "body" &&
              (bodyTab === "table" ? (
                <HitApiTable
                  data={data.hitApiDetails.body}
                  onDataChange={(tableData) =>
                    onDataChange("hitApiDetails.body", tableData)
                  }
                  onCursorChange={setCursorData}
                  expand={expand}
                />
              ) : (
                <div className="rightSidebar-tarea-wrapper">
                  <textarea
                    className="rightSidebar-tarea grey-scrollbar textarea-hg"
                    value={codeValue}
                    onClick={(event) => handleCursor(event, "code")}
                    onChange={(event) =>
                      handleCodeValueChange(event.target.value)
                    }
                  />
                  <span className="code-err">{codeErr}</span>
                </div>
              ))}
            {contentTab === "params" && (
              <HitApiTable
                data={data.hitApiDetails.params}
                onDataChange={(tableData) => {
                  onDataChange("hitApiDetails.params", tableData);
                  setFullUrl(
                    generateFullUrl(data.hitApiDetails.url, tableData)
                  );
                }}
                onCursorChange={setCursorData}
                expand={expand}
              />
            )}
          </div>
        </div>
      </div>
      <div className="btm-sticky__btn flex-cnt">
        <button
          className="saveDataBtn"
          onClick={() => !apiCallLoader && testApi()}
        >
          {apiCallLoader ? "Fetching..." : "Test API"}
        </button>
      </div>
      {showVarSelector && (
        <ModalWrapper>
          <HitApiVarSelector
            nodeId={selectedNodeId}
            responseData={responseData}
            onSave={(data) => onDataChange("hitApiDetails.variables", data)}
            onClose={() => setShowVariableSelector(false)}
          />
        </ModalWrapper>
      )}
    </div>
  );
}

type Field = "url" | "content";
type DropDownId = Field | "method" | "";

const METHOD = ["GET", "POST", "PATCH", "PUT", "DELETE"] as const;

const CONTENT_TAB = [
  {
    id: "headers",
    name: "Header",
  },
  {
    id: "body",
    name: "Body",
  },
  {
    id: "params",
    name: "URL Parameters",
  },
] as const;
type ContentTab = (typeof CONTENT_TAB)[number]["id"];

const BODY_TAB = [
  {
    id: "code",
    icon: (
      <svg
        width="15"
        height="12"
        viewBox="0 0 15 12"
        fill="none"
        xmlns="http://www.w3.org/2000/svg"
      >
        <path
          d="M14.8466 5.61171L11.3778 1.91362C11.1646 1.68609 10.8087 1.67571 10.5826 1.89042C10.3569 2.10495 10.3464 2.46323 10.5597 2.69059L13.6642 6.00001L10.5597 9.3098C10.3464 9.53716 10.3569 9.89527 10.5826 10.11C10.687 10.2092 10.8251 10.2645 10.9687 10.2643C11.0453 10.2644 11.1211 10.2487 11.1915 10.2183C11.2618 10.1878 11.3252 10.1432 11.3778 10.0871L14.8466 6.38886C15.0511 6.17058 15.0511 5.82981 14.8466 5.61171ZM4.44043 9.30963L1.33617 6.00001L4.44043 2.69041C4.65362 2.46306 4.64331 2.10475 4.41737 1.89024C4.19161 1.67571 3.83536 1.68609 3.62219 1.91344L0.153414 5.61154C-0.0511381 5.82964 -0.0511381 6.17058 0.153414 6.38871L3.62237 10.087C3.73299 10.2049 3.88206 10.2643 4.0315 10.2643C4.16987 10.2643 4.30883 10.213 4.41737 10.1098C4.64352 9.89527 4.65362 9.53698 4.44043 9.30963ZM8.41004 0.00646751C8.10309 -0.0403188 7.81584 0.171738 7.76896 0.48061L6.11895 11.3485C6.07207 11.6575 6.28301 11.9464 6.59013 11.9936C6.61902 11.9979 6.6475 12 6.67583 12C6.94902 12 7.18864 11.7994 7.23121 11.5194L8.88123 0.65157C8.9281 0.342492 8.71716 0.0536371 8.41004 0.00646751Z"
          fill="#6D6D6D"
        />
      </svg>
    ),
  },
  {
    id: "table",
    icon: (
      <svg
        width="14"
        height="12"
        viewBox="0 0 14 12"
        fill="none"
        xmlns="http://www.w3.org/2000/svg"
      >
        <path
          d="M5.64886 0H0.448748C0.200951 0 0 0.188936 0 0.422052V2.29197C0 2.52504 0.200909 2.71402 0.448748 2.71402H5.64882C5.89662 2.71402 6.09757 2.52508 6.09757 2.29197V0.422052C6.09757 0.188936 5.89666 0 5.64886 0ZM13.5511 0H8.35126C8.10351 0 7.90247 0.188936 7.90247 0.422052V2.29197C7.90247 2.52504 8.10343 2.71402 8.35126 2.71402H13.5511C13.799 2.71402 14 2.52508 14 2.29197V0.422052C13.9999 0.188936 13.799 0 13.5511 0ZM5.64886 4.64289H0.448748C0.200951 4.64289 0 4.83191 0 5.06502V6.93486C0 7.16793 0.200909 7.35699 0.448748 7.35699H5.64882C5.89662 7.35699 6.09757 7.16797 6.09757 6.93486V5.06502C6.09757 4.83191 5.89666 4.64289 5.64886 4.64289ZM13.5511 4.64289H8.35126C8.10351 4.64289 7.90247 4.83191 7.90247 5.06502V6.93486C7.90247 7.16793 8.10343 7.35699 8.35126 7.35699H13.5511C13.799 7.35699 14 7.16797 14 6.93486V5.06502C13.9999 4.83191 13.799 4.64289 13.5511 4.64289ZM5.64886 9.2859H0.448748C0.200951 9.2859 0 9.47492 0 9.70799V11.5778C0 11.8109 0.200909 12 0.448748 12H5.64882C5.89662 12 6.09757 11.8109 6.09757 11.5778V9.70799C6.09757 9.47492 5.89666 9.2859 5.64886 9.2859ZM13.5511 9.2859H8.35126C8.10351 9.2859 7.90247 9.47492 7.90247 9.70803V11.5779C7.90247 11.8109 8.10343 12 8.35126 12H13.5511C13.799 12 14 11.811 14 11.5779V9.70803C13.9999 9.47492 13.799 9.2859 13.5511 9.2859Z"
          fill="#6D6D6D"
        />
      </svg>
    ),
  },
] as const;
type BodyTab = (typeof BODY_TAB)[number]["id"];

interface CursorBase {
  startPos: number;
  endPos: number;
}

interface CursorUrl extends CursorBase {
  type: "url";
}

interface CursorCode extends CursorBase {
  type: "code";
}

interface CursorTable extends CursorBase {
  type: "table";
  rowIndex: number;
  fieldType: keyof Row;
}

export type Cursor = CursorUrl | CursorCode | CursorTable;

export interface Row {
  key: string;
  value: any;
  testValue: string;
}

function generateFullUrl(url: string, params: Array<Row>) {
  let result = url;
  for (const index in params) {
    const param = params[index];
    const delimiter = +index ? "&" : "?";
    if (!param.key && !param.key) continue;
    result += `${delimiter}${param.key}=${param.value}`;
  }

  return result;
}

function convertCodeToTable(data: Array<Row>, codeValue: string) {
  const codeObject = JSON.parse(codeValue);
  const testValues = {};

  for (const item of data) {
    testValues[item.key] = item.testValue;
  }

  const tableData: Array<Row> = [];
  for (const key in codeObject) {
    tableData.push({
      key,
      value: codeObject[key],
      testValue: testValues[key],
    });
  }

  return tableData;
}

function placeVariable(value: string, variable: Variable, cursorData: Cursor) {
  const preText = value.slice(0, cursorData.startPos);
  const postText = value.slice(cursorData.endPos);
  const result = preText + `{ ${variable.name} }` + postText;

  return result;
}
