import { useState, useRef, useEffect, useMemo } from "react";
import useStore from "../store";
import Picker from "@emoji-mart/react";
import emojiData from "@emoji-mart/data";
import VariableDropdown from "../components/VariableDropdown";
import { SendEmailIcon } from "@/assets/NodeIcon";
import placeVariable from "../utils/placeVariable";
import type { Variable, NodeConfigProps } from "../types";
import GenWithAiModal from "@/components/GenWithAiModal";
import ModalWrapper from "../components/ModalWrapper";
import EmailConfigSelector from "../components/EmailConfigSelector";
import AddIcon from "@/assets/AddIcon";
import useEmailConfig from "../react-query/configs/useEmailConfig";
import { axiosDelete, axiosPost } from "@/services/axiosService";
import { useDispatch } from "react-redux";
import { userSlice } from "@/redux/User";
import Editor from "@/components/shared/QuillEditor/Editor";
import { variableParser } from "@/services/parserService";

export default function SendEmail({
  data,
  onDataChange,
  onSave,
}: NodeConfigProps) {
  const setOpenConfigModalId = useStore((state) => state.setOpenConfigModalId);
  const fieldInputRefs = useRef<FieldInputRefs>({});
  const [openDropdownId, setOpenDropdownId] = useState<DropDownId>("");
  const [showEmojiPicker, setShowEmojiPicker] = useState(false);
  const [showConfigSelector, setShowConfigSelector] = useState(false);
  const [showGenWithAiModal, setShowGenWithAiModal] = useState(false);
  const [configName, setConfigName] = useState("");
  const [restrictFrom, setRestrictFrom] = useState(false);
  const [emailConfigs, setEmailConfigs] = useState<Array<any>>([]);
  const [allESPs, setAllESPs] = useState<Array<any>>([]);
  const [gmailConfigs, setGmailConfigs] = useState<Array<any>>([]);
  const [generatedData, setGeneratedData] = useState([]);
  const [editEmailTable, setEditEmailTable] = useState(false);
  const dispatch = useDispatch();
  const metaData = useStore((state) => state.metaData);
  const showLeadsSelector = useStore(
    (state) =>
      state.metaData.botType === "campaign" &&
      state.metaData.campaignType === "segment",
  );
  const leadsData = useStore((state) => state.leadsData);
  const updateLeadsData = useStore((state) => state.updateLeadsData);
  const [selectedLeadId, setSelectedLeadId] = useState<string | null>(
    leadsData?.[0]?._id ?? null,
  );
  const [editorContent, setEditorContent] = useState("");
  const [subject, setSubject] = useState("");
  const [loader, setLoader] = useState(false);

  const selectedLead = useMemo(
    () => leadsData.find((lead) => lead._id === selectedLeadId),
    [leadsData, selectedLeadId],
  );
  const subjectKey = useMemo(
    () => data?.sendEmailDetails?.content?.subject.match(/{(.*)}/)?.[1].trim(),
    [data?.sendEmailDetails?.content?.subject],
  );
  const bodyKey = useMemo(
    () => data?.sendEmailDetails?.content?.body.match(/{(.*)}/)?.[1].trim(),
    [data?.sendEmailDetails?.content?.body],
  );

  const { data: configData, status: configStatus } = useEmailConfig();

  useEffect(() => {
    setEditorContent(
      showLeadsSelector && selectedLead
        ? bodyKey === undefined
          ? data?.sendEmailDetails?.content?.body
          : selectedLead[bodyKey]
        : data?.sendEmailDetails?.content?.body || "",
    );
  }, [showLeadsSelector, selectedLead, data, bodyKey]);

  const handleEditorChange = (content) => {
    setEditorContent(content);
    if (showLeadsSelector && selectedLead) {
      updateLeadsData(selectedLead._id, { [bodyKey]: content });
    } else {
      onDataChange("sendEmailDetails.content.body", content);
    }
  };

  const replaceNewlines = (value) => value.replaceAll("\n", "<br>");

  const parseLeadVariables = (value) => {
    if (showLeadsSelector && selectedLead) {
      const key = value
        .trim()
        .match(/{(.*)}/)?.[1]
        .trim();
      return selectedLead[key] ?? value;
    }
    return value;
  };

  const getValue = () => {
    return replaceNewlines(editorContent);
  };

  const getOnChange = (value) => {
    return handleEditorChange(value);
  };

  function generateContent(body: string, subject: string) {
    setLoader(true);
    const content = `I would like to update the email content based on the following information:
    Subject: ${subject}
    Body: ${body}
    Please enhance the professionalism and engagement of the email. The recipient's name remains the same. The return body and subject should be in JSON format.
    
    give response as 

    {
      body: body,
      subject: subject
    }
    `;

    const emailBody = {
      configId: "6629fd49faa87bdbb7f9ded5",
      content: content,
    };

    axiosPost(`/configurations/completechat?responses=${1}`, emailBody)
      .then((response) => {
        if (response.status === 200) {
          setLoader(false);
          let tempArr: any = [];
          response.data.choices.forEach((data, index) => {
            tempArr.push(JSON.parse(data.message?.content));
          });
          setGeneratedData(tempArr);
          tempArr &&
            onDataChange(
              "sendEmailDetails.content.subject",
              tempArr[0].subject,
            );
          tempArr &&
            onDataChange("sendEmailDetails.content.body", tempArr[0].body);
        }
      })
      .catch((err) => {
        setLoader(false);
        dispatch(
          userSlice.actions.setToastPopUp({
            show: true,
            toastType: "error",
            message:
              err.response?.data?.message ??
              err.message ??
              "Something went wrong, please try again.",
          }),
        );
      });
  }

  useEffect(() => {
    if (configData) {
      let selectedConfig: any;

      const esps: any = new Set();

      for (const config of configData) {
        esps.add(config?.esp);
        if (config._id === data?.sendEmailDetails.emailConfig) {
          selectedConfig = config;
        }
      }
      setAllESPs(Array.from(esps));
      setEmailConfigs(configData);

      if (selectedConfig) {
        const configName =
          selectedConfig.esp === "gmail"
            ? selectedConfig.sender
            : selectedConfig.configName;

        setConfigName(configName);
        setRestrictFrom(selectedConfig.esp === "gmail");
      } else if (configData[0]) {
        const config = configData[0];
        const configName =
          config.esp === "gmail" ? config.sender : config.configName;

        onDataChange("sendEmailDetails", {
          ...data?.sendEmailDetails,
          emailConfig: config._id,
          from: config.sender,
        });
        setConfigName(configName);
        setRestrictFrom(config.esp === "gmail");
      } else if (data?.sendEmailDetails.emailConfig) {
        onDataChange("sendEmailDetails", {
          ...data?.sendEmailDetails,
          emailConfig: "",
          from: "",
        });
      }
    }
  }, [configData]);

  function handleVariableSelect(field: Field, variable: Variable) {
    const inputRef = fieldInputRefs.current[field];
    if (!inputRef) return;
    const result = placeVariable(inputRef, variable);
    onDataChange(`sendEmailDetails.${field}`, result);
    if (field === "content.subject") {
      onDataChange(`sendEmailDetails.${field}`, result);
    }
  }

  function handleDropdown(id: DropDownId) {
    setOpenDropdownId(openDropdownId ? "" : id);
  }

  function handleSave() {
    onSave();
  }
  async function disconnectWarmy(configId: string) {
    try {
      const res = await axiosDelete(
        `/configurations/disconnectWarmy?configId=${configId}`,
      );
      if (res.status === 200) {
        dispatch(
          userSlice.actions.setToastPopUp({
            show: true,
            toastType: "success",
            message: "Disconnected Successfully",
          }),
        );
      }
    } catch (err: any) {
      dispatch(
        userSlice.actions.setToastPopUp({
          show: true,
          toastType: "error",
          message:
            err.response?.data?.message ??
            err.message ??
            "Something went wrong, please try again.",
        }),
      );
    } finally {
    }
  }

  return (
    <div className="innerRightSideBar">
      <div className="title-header fx al-cnt jstfy-sp-btw">
        <div className="flex-cnt gap-10">
          <div
            className="node-icn flex-cnt"
            style={{ background: "#BB3DDB0D", border: "1px solid #BB3DDB1A" }}
          >
            <SendEmailIcon />
          </div>
          <div className="grid gap-6">
            <h1>
              <span>Edit</span> E-Mail Configuration
            </h1>
            <p>Send Email to user at that instance using SMTP configuration.</p>
          </div>
        </div>
      </div>
      <div className="data-box-container fx fx-d-col gap-20">
        <div>
          <span className="data-box-label">Integrations details</span>
          <div
            className={`data-box-wrap data-box-half-width ${
              !emailConfigs.length && !gmailConfigs.length
                ? "create-new-integration"
                : ""
            }`}
          >
            <div
              className="node-dropdown variableTypeDropDown rightSidebar-tarea no-m"
              onMouseLeave={() => handleDropdown("")}
            >
              {configStatus === "pending" ? (
                <div className="react-loading-skeleton" />
              ) : emailConfigs.length || gmailConfigs.length ? (
                <div
                  className="config-button"
                  onClick={() => handleDropdown("config")}
                >
                  <span>{configName || "Select integration"}</span>
                  <svg
                    width="10"
                    height="6"
                    viewBox="0 0 10 6"
                    fill="none"
                    xmlns="http://www.w3.org/2000/svg"
                  >
                    <path
                      d="M1 1L5 5L9 1"
                      stroke="#444444"
                      strokeWidth="2"
                      strokeLinecap="round"
                      strokeLinejoin="round"
                    />
                  </svg>
                  {openDropdownId === "config" && (
                    <div className="dropdown-items w-100 full-view-menu fx fx-d-col">
                      <div className="grey-scrollbar email-int-dropdown config-list">
                        {allESPs.map((esp) => (
                          <>
                            <span>{esp.toUpperCase()}</span>
                            <ul className="fx fx-d-col gap-5">
                              {emailConfigs.map(
                                (value, index) =>
                                  value.esp === esp && (
                                    <li
                                      key={index}
                                      className={
                                        value.sender === configName
                                          ? "active-item"
                                          : ""
                                      }
                                      onClick={() => {
                                        onDataChange("sendEmailDetails", {
                                          ...data?.sendEmailDetails,
                                          emailConfig: value._id,
                                          from: value.sender,
                                        });
                                        setConfigName(value.sender);
                                        if (
                                          esp === "gmail" ||
                                          esp === "outlook"
                                        )
                                          setRestrictFrom(true);
                                        else {
                                          setRestrictFrom(false);
                                        }
                                      }}
                                    >
                                      {value.sender}

                                      {value.warmyId && (
                                        <button
                                          onClick={(e) => {
                                            e.stopPropagation();
                                            disconnectWarmy(value._id);
                                          }}
                                        >
                                          &nbsp;&nbsp; &nbsp; &nbsp;( warmy x)
                                        </button>
                                      )}
                                    </li>
                                  ),
                              )}
                            </ul>
                            <hr />
                          </>
                        ))}
                      </div>
                      <span
                        className="add-condition add_new_button add-acc p-absolute bottom-0 left-0 cursor-pointer"
                        onClick={() => setShowConfigSelector(true)}
                      >
                        <AddIcon />
                        Add account
                      </span>
                    </div>
                  )}
                </div>
              ) : (
                <span onClick={() => setShowConfigSelector(true)}>
                  Create new integration
                </span>
              )}
            </div>
          </div>
        </div>
        <div>
          <span className="data-box-label">Email Address Details</span>
          <div className="data-box-wrap grid gap-20">
            <div className="fx fx-d-col">
              <div className="fx al-fx-cnt">
                <span className="input-label">From Email Address</span>
                <div
                  className="node-dropdown h-mc"
                  onClick={() => handleDropdown("from")}
                  onMouseLeave={() => handleDropdown("")}
                >
                  <span className="color-red">Add Variable</span>
                  {openDropdownId === "from" && (
                    <VariableDropdown
                      onSelect={(variable) =>
                        handleVariableSelect("from", variable)
                      }
                    />
                  )}
                </div>
              </div>
              <input
                className="rightSidebar-tarea"
                ref={(ref) => ref && (fieldInputRefs.current.from = ref)}
                value={data?.sendEmailDetails?.from}
                placeholder="Enter email address"
                onChange={(event) =>
                  onDataChange("sendEmailDetails.from", event.target.value)
                }
                readOnly={restrictFrom}
              ></input>
            </div>
            <div className="fx fx-d-col">
              <div className="fx al-fx-cnt">
                <span className="input-label">To Email Address</span>
                <div
                  className="node-dropdown h-mc"
                  onClick={() => handleDropdown("to")}
                  onMouseLeave={() => handleDropdown("")}
                >
                  <span className="color-red">Add Variable</span>
                  {openDropdownId === "to" && (
                    <VariableDropdown
                      onSelect={(variable) =>
                        handleVariableSelect("to", variable)
                      }
                    />
                  )}
                </div>
              </div>
              <input
                className="condition rightSidebar-tarea"
                ref={(ref) => ref && (fieldInputRefs.current.to = ref)}
                value={parseLeadVariables(data?.sendEmailDetails?.to)}
                placeholder="Type or select the variable"
                onChange={(event) => {
                  onDataChange("sendEmailDetails.to", event.target.value);
                }}
              />
            </div>
            <div className="fx fx-d-col">
              <div className="fx al-fx-cnt">
                <span className="input-label">Reply To</span>
                <div
                  className="node-dropdown h-mc"
                  onClick={() => handleDropdown("replyTo")}
                  onMouseLeave={() => handleDropdown("")}
                >
                  <span className="color-red">Add Variable</span>
                  {openDropdownId === "replyTo" && (
                    <VariableDropdown
                      onSelect={(variable) =>
                        handleVariableSelect("replyTo", variable)
                      }
                    />
                  )}
                </div>
              </div>
              <input
                className="condition rightSidebar-tarea"
                ref={(ref) => ref && (fieldInputRefs.current.replyTo = ref)}
                value={data?.sendEmailDetails?.replyTo}
                placeholder="Type or select the variable"
                onChange={(event) =>
                  onDataChange("sendEmailDetails.replyTo", event.target.value)
                }
              />
            </div>
            {showLeadsSelector && (
              <div className="fx fx-d-col">
                <div className="fx al-fx-cnt">
                  <span className="input-label">Choose Leads</span>
                  {/* <span
                    className="input-label"
                    onClick={() => setSelectedLeadId(null)}
                  >
                    Clear
                  </span> */}
                </div>
                <div
                  className="node-dropdown variableTypeDropDown rightSidebar-tarea no-m h-mc w-100"
                  onClick={() => handleDropdown("leadsSelector")}
                  onMouseLeave={() => handleDropdown("")}
                >
                  <div className="config-button">
                    {selectedLead?.name ?? "Choose leads"}
                    <svg
                      width="10"
                      height="6"
                      viewBox="0 0 10 6"
                      fill="none"
                      xmlns="http://www.w3.org/2000/svg"
                    >
                      <path
                        d="M1 1L5 5L9 1"
                        stroke="#444444"
                        strokeWidth="2"
                        strokeLinecap="round"
                        strokeLinejoin="round"
                      />
                    </svg>
                  </div>
                  {openDropdownId === "leadsSelector" && (
                    <div className="dropdown-items w-100 full-view-menu fx fx-d-col">
                      <div className="grey-scrollbar mb-0">
                        <ul className="grey-scrollbar">
                          {leadsData.map((lead) => (
                            <li
                              key={lead._id}
                              value={lead._id}
                              onClick={() => setSelectedLeadId(lead._id)}
                              className="fx fx-d-col gap-5"
                            >
                              {lead.name}
                            </li>
                          ))}
                        </ul>
                      </div>
                    </div>
                  )}
                </div>
              </div>
            )}
          </div>
        </div>
        <div>
          <span className="data-box-label fx jstfy-sp-btw">
            Email Contents
            {metaData?.botType != "campaign" && (
              <button
                className="generate-btn fx al-cnt"
                onClick={() => setShowGenWithAiModal(true)}
              >
                <svg
                  width="14"
                  height="14"
                  viewBox="0 0 14 14"
                  fill="none"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    d="M12.25 3.50001C12.25 2.53348 13.0335 1.75 14 1.75C13.0335 1.75 12.25 0.966502 12.25 0C12.25 0.966473 11.4665 1.75 10.5 1.75C11.4665 1.75 12.25 2.53345 12.25 3.50001ZM12.6875 5.24998C12.6875 5.97484 12.0999 6.56248 11.375 6.56248C12.0999 6.56248 12.6875 7.15011 12.6875 7.87497C12.6875 7.15008 13.2751 6.56248 14 6.56248C13.2751 6.56251 12.6875 5.97487 12.6875 5.24998ZM6.125 5.24998C6.125 3.80023 7.30024 2.62499 8.74999 2.62499C7.30024 2.62499 6.125 1.44975 6.125 0C6.125 1.44975 4.94976 2.62499 3.50001 2.62499C4.94976 2.62499 6.125 3.80026 6.125 5.24998ZM9.62501 2.62499L0 12.25L1.75 14L11.375 4.37499L9.62501 2.62499ZM8.13134 5.43115L9.36863 4.19386L9.80614 4.63137L8.56885 5.86866L8.13134 5.43115Z"
                    fill="#FB5F66"
                  />
                </svg>
                {loader ? "Generating..." : "Generate with AI "}
              </button>
            )}
          </span>
          <div className="data-box-wrap">
            {!(showLeadsSelector && selectedLead) && (
              <div className="row-container">
                <span className="input-label">Subject</span>
                <div
                  className="node-dropdown h-mc"
                  onClick={() => handleDropdown("content.subject")}
                  onMouseLeave={() => handleDropdown("")}
                >
                  <span className="color-red">Add Variable</span>
                  {openDropdownId === "content.subject" && (
                    <VariableDropdown
                      onSelect={(variable) =>
                        showLeadsSelector && selectedLead
                          ? handleVariableSelect("content.subject", variable)
                          : handleVariableSelect("content.subject", variable)
                      }
                    />
                  )}
                </div>
              </div>
            )}
            <div className="rightSidebar-tarea fx emoji_input">
              <input
                className="rightSidebar-tarea no-m bd-none no-bg"
                ref={(ref) =>
                  ref && (fieldInputRefs.current["content.subject"] = ref)
                }
                value={
                  metaData?.botType != "campaign" && subject !== ""
                    ? data.sendEmailDetails.content.subject !== subject
                      ? subject
                      : data.sendEmailDetails.content.subject
                    : showLeadsSelector && selectedLead
                      ? subjectKey === undefined
                        ? data?.sendEmailDetails?.content?.subject
                        : selectedLead[subjectKey]
                      : data?.sendEmailDetails?.content?.subject || ""
                }
                placeholder="Write Something Here....."
                onChange={(event) => {
                  setSubject(event.target.value);
                  showLeadsSelector && selectedLead
                    ? updateLeadsData(selectedLead._id, {
                        [subjectKey]: event.target.value,
                      })
                    : onDataChange(
                        "sendEmailDetails.content.subject",
                        event.target.value,
                      );
                }}
              />
              <button
                className="p-relative no-bg bd-none"
                onClick={() => setShowEmojiPicker(!showEmojiPicker)}
              >
                <svg
                  xmlns="http://www.w3.org/2000/svg"
                  width="16"
                  height="16"
                  viewBox="0 0 16 16"
                  fill="none"
                >
                  <path
                    d="M7.99563 1.88867C4.62229 1.88867 1.89062 4.62645 1.89062 7.99978C1.89062 11.3731 4.62229 14.1109 7.99563 14.1109C11.3751 14.1109 14.1128 11.3731 14.1128 7.99978C14.1128 4.62645 11.3751 1.88867 7.99563 1.88867ZM8.00174 12.8887C5.30063 12.8887 3.11285 10.7009 3.11285 7.99978C3.11285 5.29867 5.30063 3.11089 8.00174 3.11089C10.7028 3.11089 12.8906 5.29867 12.8906 7.99978C12.8906 10.7009 10.7028 12.8887 8.00174 12.8887ZM10.1406 7.38867C10.6478 7.38867 11.0573 6.97923 11.0573 6.47201C11.0573 5.96478 10.6478 5.55534 10.1406 5.55534C9.6334 5.55534 9.22396 5.96478 9.22396 6.47201C9.22396 6.97923 9.6334 7.38867 10.1406 7.38867ZM5.86285 7.38867C6.37007 7.38867 6.77951 6.97923 6.77951 6.47201C6.77951 5.96478 6.37007 5.55534 5.86285 5.55534C5.35563 5.55534 4.94618 5.96478 4.94618 6.47201C4.94618 6.97923 5.35563 7.38867 5.86285 7.38867ZM8.00174 11.3609C9.42563 11.3609 10.6356 10.4687 11.1245 9.22201H4.87896C5.36785 10.4687 6.57785 11.3609 8.00174 11.3609Z"
                    fill="black"
                    fillOpacity="0.54"
                  />
                </svg>
              </button>
              <div className="p-absolute emoji-picker">
                {showEmojiPicker && (
                  <Picker
                    data={emojiData}
                    previewPosition="none"
                    emojiSize="18"
                    maxFrequentRows="1"
                    onEmojiSelect={(event) =>
                      showLeadsSelector && selectedLead
                        ? updateLeadsData(selectedLead._id, {
                            [subjectKey]:
                              selectedLead[subjectKey] + event.native,
                          })
                        : onDataChange(
                            "sendEmailDetails.content.subject",
                            data?.sendEmailDetails.content.subject +
                              event.native,
                          )
                    }
                    onClickOutside={() => setShowEmojiPicker(false)}
                  />
                )}
              </div>
            </div>
            <div className="row-container mg-top mg-btm">
              <span className="input-label">Body</span>
            </div>
            <div className="editor-container no-m bd-none">
              <Editor
                value={getValue()}
                onChange={getOnChange}
                showVariables={
                  !(showLeadsSelector && selectedLead) ? true : false
                }
              />
            </div>
          </div>
        </div>
      </div>
      <div className="btm-sticky__btn flex-cnt">
        <button onClick={handleSave} className="saveDataBtn">
          Save
        </button>
      </div>
      {showConfigSelector && (
        <ModalWrapper>
          <EmailConfigSelector
            onSelect={(type) => setOpenConfigModalId(type)}
            onClose={() => setShowConfigSelector(false)}
          />
        </ModalWrapper>
      )}
      {showGenWithAiModal && (
        <ModalWrapper>
          <GenWithAiModal
            nodeName="sendEmail"
            setEditEmailTable={setEditEmailTable}
            setShowGenWithAiModal={setShowGenWithAiModal}
            generatedData={generatedData}
            onSave={(subject, body) => {
              // setBody(body);
              setSubject(subject);
            }}
            onClose={() => {
              setShowGenWithAiModal(false);
            }}
          />
        </ModalWrapper>
      )}
    </div>
  );
}

type Field = "from" | "to" | "replyTo" | "content.subject" | "content.body";
type DropDownId = Field | "config" | "leadsSelector" | "";
type FieldInputRefs = Partial<Record<Field, React.ElementRef<"input">>>;
