import { useState } from "react";
import { produce } from "immer";
import type { TeamMember, NodeConfigProps } from "../types";

interface Props {
  teamMembers: Map<string, TeamMember>;
  roundRobinData: Record<string, string>;
  onDataChange: NodeConfigProps["onDataChange"];
}

export default function AssigneesRoundRobin({
  teamMembers,
  roundRobinData,
  onDataChange,
}: Props) {
  const [showDropDown, setShowDropDown] = useState(false);

  function handleAssigneesSelect(memberId: string) {
    const data = produce(roundRobinData, (draft) => {
      if (draft[memberId]) delete draft[memberId];
      else draft[memberId] = memberId;
    });

    onDataChange("assigneesData.roundRobinData", data);
  }

  return (
    <>
      <div
        className="inputs-data-box"
        onMouseLeave={() => setShowDropDown(false)}
      >
        <div className="data-box-wrap">
          <div className="data-box-segment condition">
            <span className="input-label">Assignees</span>
            <div className="custom-conditions">
              <div
                className="node-dropdown rightSidebar-tarea mt-0"
                onClick={() => setShowDropDown(!showDropDown)}
              >
                <span>Select assignees</span>
                <ArrowIcon />
                {showDropDown && (
                  <div
                    className="assignees-drp p-absolute wd-100"
                    onClick={(event) => event.stopPropagation()}
                  >
                    {Array.from(teamMembers, ([id, member]) => (
                      <div key={id} className="to-assign fx al-cnt gap-10">
                        <label className="checkbox-container assign-logic fx">
                          <input
                            type="checkbox"
                            id="main_box"
                            checked={Boolean(roundRobinData[id])}
                            onChange={() => handleAssigneesSelect(id)}
                          />
                          <div className="mark">
                            <CheckMark />
                          </div>
                          <span>{member.name}</span>
                        </label>
                      </div>
                    ))}
                  </div>
                )}
              </div>
            </div>
          </div>
        </div>
      </div>
      <div className="data-box-wrap">
        <div className="custom-conditions">
          {roundRobinData && Object.keys(roundRobinData)?.length ? (
            <div className="assignees-name mt-10">
              {Array.from(
                teamMembers,
                ([id, member]) =>
                  roundRobinData[id] && (
                    <span key={id}>
                      {member.name}
                      <div
                        className="cancl-name flex-cnt"
                        onClick={() => handleAssigneesSelect(id)}
                      >
                        <CrossIcon />
                      </div>
                    </span>
                  )
              )}
            </div>
          ) : (
            <div className="no-data-assignees">
              <img
                src="https://dzvexx2x036l1.cloudfront.net/calc_images/Group%201881.svg"
                alt=""
              />
              <p>You haven't added any assignee yet</p>
            </div>
          )}
        </div>
      </div>
    </>
  );
}

function ArrowIcon() {
  return (
    <svg
      width="10"
      height="6"
      viewBox="0 0 10 6"
      fill="none"
      xmlns="http://www.w3.org/2000/svg"
    >
      <path
        d="M1 1L5 5L9 1"
        stroke="#444444"
        strokeWidth="2"
        strokeLinecap="round"
        strokeLinejoin="round"
      ></path>
    </svg>
  );
}

function CheckMark() {
  return (
    <svg
      width="10"
      height="8"
      viewBox="0 0 10 8"
      fill="none"
      xmlns="http://www.w3.org/2000/svg"
    >
      <path
        d="M3.22883 7.60948C2.94497 7.60959 2.67274 7.49677 2.47219 7.29588L0.184609 5.00915C-0.0615362 4.76293 -0.0615362 4.3638 0.184609 4.11758C0.430832 3.87143 0.829961 3.87143 1.07618 4.11758L3.22883 6.27022L8.92382 0.575234C9.17004 0.329089 9.56917 0.329089 9.81539 0.575234C10.0615 0.821457 10.0615 1.22059 9.81539 1.46681L3.98547 7.29588C3.78492 7.49677 3.51269 7.60959 3.22883 7.60948Z"
        fill="white"
      />
    </svg>
  );
}

function CrossIcon() {
  return (
    <svg
      width={9}
      height={9}
      viewBox="0 0 9 9"
      fill="none"
      xmlns="http://www.w3.org/2000/svg"
    >
      <path
        fillRule="evenodd"
        clipRule="evenodd"
        d="M4.06886 5.12952L7.07107 8.13173L8.13173 7.07107L5.12952 4.06886L8.13175 1.06663L7.07109 0.00597316L4.06886 3.0082L1.06066 0L0 1.06066L3.0082 4.06886L2.21729e-05 7.07704L1.06068 8.1377L4.06886 5.12952Z"
        fill="#D9D9D9"
      />
    </svg>
  );
}
