import { useState } from "react";
import { useDispatch } from "react-redux";
import { userSlice } from "@/redux/User";
import useStore from "../store";
import { axiosPost } from "@/services/axiosService";
import FirstNode from "../components/FirstNode";
import { AudienceIcon } from "@/assets/NodeIcon";
import type { NodeConfigProps } from "../types";
import useLists from "../react-query/useLists";
import useSegments from "../react-query/useSegments";
import SegmentDatePicker from "../components/SegmentDatePicker";

interface Audience {
  name: string;
  type: "uploadList" | "botList" | "segment";
  listId: string;
  segmentId?: string;
  scheduleType?: "now" | "delay";
  scheduleTime?: string;
}

export default function Segment({
  data,
  onDataChange,
  onSave,
  onClose,
}: NodeConfigProps) {
  const dispatch = useDispatch();
  const fetchSegmentData = useStore((state) => state.fetchSegmentData);
  const [showDropdown, setShowDropdown] = useState(false);
  const [showDatePicker, setShowDatePicker] = useState(false);

  const { data: listData } = useLists();
  const { data: segmentData } = useSegments();

  async function sendCampaign() {
    try {
      if (!data.audienceData?.listId) return;
      // const delay: number = moment(selectedAudience?.scheduleTime).diff(
      //   moment()
      // );
      await axiosPost("/campaign/trigger", {
        url: window.location.pathname.split("/")[2],
        listId: data.audienceData?.listId,
        segmentId: data.audienceData?.segmentId,
        type: data.audienceData?.type,
        delay: 0,
      });

      dispatch(
        userSlice.actions.setToastPopUp({
          show: true,
          toastType: "success",
          message: "Sent Campaign Successfully",
        }),
      );
      onClose();
    } catch (err: any) {
      console.log(err);
      dispatch(
        userSlice.actions.setToastPopUp({
          show: true,
          toastType: "error",
          message: err.response.data.message,
        }),
      );
    }
  }

  function handleAudienceSelect(newData: Audience) {
    onDataChange("audienceData", { ...data.audienceData, ...newData });
    fetchSegmentData(newData.listId);
  }

  function handleSave() {
    const newData = {
      ...data,
      audienceData: {
        ...data.audienceData,
        scheduleTime: showDatePicker ? formatter.format(Date.now()) : "",
      },
    };
    onSave(newData);
    dispatch(
      userSlice.actions.setToastPopUp({
        show: true,
        toastType: "success",
        message: "Data Saved",
      }),
    );
    onClose();
  }

  return (
    <div className="innerRightSideBar">
      <div className="title-header fx al-cnt jstfy-sp-btw">
        <div className="flex-cnt gap-10">
          <div
            className="node-icn flex-cnt"
            style={{
              background: "rgba(176, 35, 145, 0.05)",
              border: "1px solid rgba(176, 35, 145, 0.10)",
            }}
          >
            <AudienceIcon />
          </div>
          <div className="grid gap-6">
            <h1>Edit Audience</h1>
            <p>Send or Schedule campaigns to selected audience segments.</p>
          </div>
        </div>
        {/* <FirstNode /> */}
      </div>
      <div className="data-box-container">
        <div className="inputs-data-box">
          <div className="condition">
            <span className="data-box-label">SELECT SEGMENT FOR CAMPAIGN</span>
          </div>
          <div className="data-box-wrap condition">
            <div className="data-box-segment">
              <span className="input-label">Select Segment/List</span>
              <div
                className="node-dropdown d-block"
                onClick={() => setShowDropdown(!showDropdown)}
                onMouseLeave={() => setShowDropdown(false)}
              >
                <span className="condition-comparison-operator rightSidebar-tarea condition">
                  {data.audienceData?.name || "Select Segment/List"}
                  <svg
                    width="10"
                    height="6"
                    viewBox="0 0 10 6"
                    fill="none"
                    xmlns="http://www.w3.org/2000/svg"
                  >
                    <path
                      d="M1 1L5 5L9 1"
                      stroke="#444444"
                      strokeWidth="2"
                      strokeLinecap="round"
                      strokeLinejoin="round"
                    />
                  </svg>
                </span>
                {showDropdown && (
                  <div className="dropdown-items segment-dropdown">
                    <ul className="grey-scrollbar">
                      {!listData.length && !segmentData.length && (
                        <li className="color-red hover-none">
                          No Segment/List Found
                        </li>
                      )}
                      {segmentData.map((item, index) => (
                        <li
                          key={index}
                          onClick={() =>
                            handleAudienceSelect({
                              name: item.name,
                              type: "segment",
                              listId: item.listData[0].listId,
                              segmentId: item._id,
                            })
                          }
                        >
                          {item.name ?? ""}
                        </li>
                      ))}
                      {listData.map((item, index) => (
                        <li
                          key={index}
                          onClick={() => {
                            setShowDropdown(false);
                            handleAudienceSelect({
                              name: item.name,
                              type:
                                item.type === "bot" ? "botList" : "uploadList",
                              listId: item.listId,
                            });
                          }}
                        >
                          {item.name ?? ""}
                        </li>
                      ))}
                    </ul>
                  </div>
                )}
              </div>
            </div>
          </div>
        </div>
        {/* <div className="inputs-data-box audience-rght">
          <div className=" condition">
            <span className="data-box-label mg-top">Send Campaign</span>
          </div>
          <div className="audience_campaign grid gap-20">
            <div className="data-box-wrap">
              <div className="send_campaign ">
                <input
                  id="now"
                  value="now"
                  type="radio"
                  name="sendCampaign"
                  checked={data.audienceData?.scheduleType === "now"}
                  onChange={() =>
                    onDataChange("audienceData.scheduleType", "now")
                  }
                />
                <label htmlFor="now">Send campaign now</label>
              </div>
            </div>
            <div className="data-box-wrap">
              <div className="send_campaign">
                <input
                  id="delay"
                  type="radio"
                  value="delay"
                  name="sendCampaign"
                  checked={data.audienceData?.scheduleType === "delay"}
                  onChange={() =>
                    onDataChange("audienceData.scheduleType", "delay")
                  }
                />
                <label htmlFor="delay">Send campaign Later</label>
              </div>
            </div>
          </div>
        </div> */}
        {/* {data.audienceData?.scheduleType === "delay" && (
          <div className="inputs-data-box">
            <span className="data-box-label mg-top">Schedule campaign</span>
            <div className="data-box-wrap">
              <div className="input-label">Select date and time</div>
              <div
                className="segment-date-input  p-relative"
                onClick={() => setShowDatePicker(!showDatePicker)}
              >
                <svg
                  width="14"
                  height="14"
                  viewBox="0 0 14 14"
                  fill="none"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    d="M11.9855 12.6H2.00405V4.9H11.9855V12.6ZM9.84664 0V1.4H4.14294V0H2.71701V1.4H2.00405C1.21266 1.4 0.578125 2.023 0.578125 2.8V12.6C0.578125 12.9713 0.728356 13.3274 0.995769 13.5899C1.26318 13.8525 1.62587 14 2.00405 14H11.9855C12.3637 14 12.7264 13.8525 12.9938 13.5899C13.2612 13.3274 13.4115 12.9713 13.4115 12.6V2.8C13.4115 2.4287 13.2612 2.0726 12.9938 1.81005C12.7264 1.5475 12.3637 1.4 11.9855 1.4H11.2726V0H9.84664ZM10.5596 7.7H6.99479V11.2H10.5596V7.7Z"
                    fill="#fb5f66"
                  />
                </svg>
                <span className="analytics-date-range">
                  {formatter.format(Date.now())}
                </span>
                {showDatePicker && <SegmentDatePicker />}
              </div>
            </div>
          </div>
        )} */}
      </div>
      {/* <div className="btm-sticky__btn flex-cnt">
        <button
          className="saveDataBtn no-mt"
          onClick={sendCampaign}
          disabled={!data.audienceData?.listId}
        >
          Send Campaign
        </button>
      </div> */}
      <div className="btm-sticky__btn flex-cnt">
        <button onClick={handleSave} className="saveDataBtn">
          Save
        </button>
      </div>
    </div>
  );
}

const formatter = new Intl.DateTimeFormat("en-US", {
  dateStyle: "medium",
  timeStyle: "medium",
});
