import { useQuery } from "@tanstack/react-query";
import { axiosGet } from "@/services/axiosService";

import type { TeamMember } from "../types";

export default function useTeamUsers() {
  return useQuery<Map<string, TeamMember>>({
    queryKey: ["users", "getCompanyUsers"],
    queryFn: async () => {
      const response = await axiosGet("users/getCompanyUsers");
      const teamMembers: Map<string, TeamMember> = new Map();

      for (const item of response.data.users ?? []) {
        teamMembers.set(item.userId._id, {
          name: item.userId.userName,
          email: item.userId.userEmail,
          role: item.role,
        });
      }
      return teamMembers;
    },
    placeholderData: new Map(),
    staleTime: Infinity,
  });
}
