import React, {
  useEffect,
  useMemo,
  useRef,
  useState,
  useCallback,
} from "react";
import FroalaEditorComponent from "react-froala-wysiwyg";
import FroalaEditor from "froala-editor";
import * as filestack from "filestack-js";
import environment from "@/constants/Environment";
import useStore from "../store";
import { VARIABLE_CATEGORY } from "../constant";

import "froala-editor/js/plugins.pkgd.min.js";
import "froala-editor/css/froala_style.min.css";
import "froala-editor/css/froala_editor.pkgd.min.css";
import "../utils/froalaPlugin";
import "@/styles/froala.css";
import { Variable } from "../types";
import SmallLoader from "@/components/loaders/SmallLoader";

const environmentData = environment();

interface Props {
  type: string;
  content: string;
  onContentChange: (content: string) => void;
}

const Froala: React.FC<Props> = ({ type, content, onContentChange }) => {
  const variables = useStore((state) => state.variables);
  const contentRef = useRef<HTMLDivElement | null>(null);
  const [loadingContent, setLoadingContent] = useState(true);
  const [editorContent, setEditorContent] = useState(content);

  const config = useMemo(() => getFroalaConfig(type), [type]);

  // useEffect(() => {
  //   const unwantedDivRegex =
  //     /<div[^>]*?style="z-index:9999;width:100%;position:relative;"[^>]*?>.*?<\/div>/gi;
  //   const froalaPhrases = [
  //     "Powered by Froala Editor",
  //     "Froala EditorPowered by Froala Editor",
  //     "Froala Editor",
  //     "Powered by",
  //     "Froala",
  //   ];

  //   let updatedContent = content;
  //   updatedContent = updatedContent.replace(unwantedDivRegex, "");

  //   froalaPhrases.forEach((phrase) => {
  //     updatedContent = updatedContent.replaceAll(phrase, "");
  //   });

  //   if (contentRef.current) {
  //     const paragraphs = contentRef.current.querySelectorAll("p");
  //     paragraphs.forEach((p) => {
  //       froalaPhrases.forEach((phrase) => {
  //         if (p.innerHTML.includes(phrase)) {
  //           p.innerHTML = p.innerHTML.replaceAll(phrase, "");
  //         }
  //       });
  //     });

  //     const anchorTags = contentRef.current.querySelectorAll(
  //       'a[title="Froala Editor"]',
  //     );
  //     anchorTags.forEach((anchor) => {
  //       const parentP = anchor.closest("p");
  //       if (parentP) {
  //         parentP.remove();
  //       }
  //     });

  //     const manualDiv = contentRef.current.querySelector(
  //       'div[style="z-index:9999;width:100%;position:relative"]',
  //     );
  //     if (manualDiv) {
  //       manualDiv.remove();
  //     }
  //   }

  //   setEditorContent(updatedContent);
  //   setLoadingContent(false);
  // }, []);

  useEffect(() => {
    setEditorContent(content);
    setLoadingContent(false);
  });

  const handleContentChange = useCallback((content) => {
    setEditorContent(content);
    onContentChange(content);
  }, []);

  const insertContent = useCallback(
    (editor, content) => {
      const snapshot = editor.snapshot.get();
      editor.html.insert(content);
      handleContentChange(editor.html.get());
      editor.snapshot.restore(snapshot);
    },
    [handleContentChange],
  );

  // dont remove
  // useEffect(() => {
  //   if (!loadingContent) {
  // const unwantedDivRegex =
  //   /<div[^>]*?style="z-index:9999;width:100%;position:relative;"[^>]*?>.*?<\/div>/gi;
  // const froalaPhrases = [
  //   "Powered by Froala Editor",
  //   "Froala EditorPowered by Froala Editor",
  //   "Froala Editor",
  //   "Powered by",
  //   "Froala",
  // ];

  // let updatedContent = editorContent;
  // updatedContent = updatedContent.replace(unwantedDivRegex, "");

  // froalaPhrases.forEach((phrase) => {
  //   updatedContent = updatedContent.replaceAll(phrase, "");
  // });

  //     setEditorContent(updatedContent);

  //     if (contentRef.current) {
  //       const paragraphs = contentRef.current.querySelectorAll("p");
  //       paragraphs.forEach((p) => {
  //         froalaPhrases.forEach((phrase) => {
  //           if (p.innerHTML.includes(phrase)) {
  //             p.innerHTML = p.innerHTML.replaceAll(phrase, "");
  //           }
  //         });
  //       });

  // const anchorTags = contentRef.current.querySelectorAll(
  //   'a[title="Froala Editor"]',
  // );
  // anchorTags.forEach((anchor) => {
  //   const parentP = anchor.closest("p");
  //   if (parentP) {
  //     parentP.remove();
  //   }
  // });

  // const manualDiv = contentRef.current.querySelector(
  //   'div[style="z-index:9999;width:100%;position:relative"]',
  // );
  // if (manualDiv) {
  //   manualDiv.remove();
  // }
  //     }
  //   }
  // }, []);

  useEffect(() => {
    const options: { [key: string]: string } = {};
    for (const category of VARIABLE_CATEGORY) {
      for (const variable of variables[category] ?? []) {
        options[variable.name] = variable.name;
      }
    }

    if (!Object.keys(options).length) {
      options["noVariable"] = "No Variable Found";
    }

    if (type !== "onboarding-email") {
      FroalaEditor.RegisterCommand("add_variable", {
        title: "Add variable",
        type: "dropdown",
        icon: "Add variable",
        focus: false,
        undo: false,
        options,
        callback: function (btnName, val) {
          if (val === "noVariable") return;
          insertContent(this, `{ ${val} }`);
        },
      });
    }

    FroalaEditor.DefineIcon("filestackIcon", {
      NAME: "add",
      SVG_KEY: "insertFile",
    });

    FroalaEditor.RegisterCommand("openFilePicker", {
      type: "button",
      icon: "filestackIcon",
      title: "Insert image",
      plugin: "filestack",
      callback: function () {
        const selectedImage = this.image.get();
        const client = filestack.init(environmentData.FILESTACK_API_KEY);

        const pickerConfig: filestack.PickerOptions = {
          displayMode: filestack.PickerDisplayMode.overlay,
          uploadInBackground: false,
          accept: ["image/*", "video/*"],
          fromSources: ["local_file_system"],
          onUploadDone: (res) => {
            this.selection.save();
            this.filestack.onUploadDone(res, selectedImage);
          },
        };

        client.picker(pickerConfig).open();
      },
    });
  }, [type, variables, insertContent]);

  return (
    <div className="froala-editor" ref={contentRef}>
      {loadingContent ? (
        <SmallLoader />
      ) : (
        <FroalaEditorComponent
          tag="textarea"
          config={config}
          model={editorContent?.replaceAll("\n", "<br>")}
          onModelChange={handleContentChange}
        />
      )}
    </div>
  );
};

function getFroalaConfig(type: string) {
  return {
    toolbarBottom: true,
    useClasses: false,
    emoticonsUseImage: false,
    fontFamilyDefaultSelection: "Verdana",
    fontFamilySelection: true,
    quickInsertEnabled: false,
    heightMin: type === "onboarding-email" ? 200 : 300,
    paragraphFormatSelection: false,
    attribution: false,
    charCounterCount: true,
    toolbarButtons:
      type === "sms" || type === "notification"
        ? {
            moreMisc: { buttons: ["undo", "redo"] },
            moreText: {},
            moreParagraph: {},
            moreRich: {
              buttons: ["insertLink", "emoticons", "add_variable"],
              buttonsVisible: 3,
            },
          }
        : {
            moreMisc: {
              buttons: ["undo", "redo"],
              align: "left",
              buttonsVisible: 2,
            },
            moreText: {
              buttons: [
                "fontFamily",
                "bold",
                "italic",
                "underline",
                "fontSize",
                "textColor",
                "backgroundColor",
                "inlineStyle",
                "clearFormatting",
              ],
              buttonsVisible: 4,
            },
            moreRich: {
              buttons: [
                type !== "sticky" && "add_variable",
                "insertLink",
                "emoticons",
                "insertImage",
              ].filter(Boolean),
              buttonsVisible: type !== "sticky" ? 4 : 3,
            },
            moreParagraph: {
              buttons: [
                "paragraphFormat",
                "alignLeft",
                "alignCenter",
                "alignRight",
              ],
              buttonsVisible: 4,
            },
          },
    imageInsertButtons: ["openFilePicker", "imageManager"],
    paragraphFormat: {
      N: "Normal",
      H1: "Heading 1",
      H2: "Heading 2",
      H3: "Heading 3",
      H4: "Heading 4",
    },
    inlineStyles: {
      "Big Red": "font-size: 20px; color: red;",
      "Small Blue": "font-size: 14px; color: blue;",
      Highlighted: "background-color: #ffff00;",
      Code: 'border-color: #cccccc; border-radius: 2px; background-clip: padding-box; background: #f5f5f5; padding: 1px; font-family: "Courier New", Courier, monospace;',
    },
  };
}

export default React.memo(Froala);
