import {
  AskQuestionIcon,
  GoogleSheetIcon,
  HubspotIcon,
  OpenAIIcon,
} from "@/assets/NodeIcon";
import type { NodeConfigProps } from "../types";

export default function DynamicApps({
  data,
  onDataChange,
  onSave,
}: NodeConfigProps) {
  const getNodeItems = (nodeName: string) => {
    return apps.filter((app) => app.onDrag == nodeName)[0];
  };

  return (
    <div
      className="innerRightSideBar"
      //   onClick={() => {
      //     handleOuterClick();
      //   }}
    >
      <div className="title-header fx al-cnt jstfy-sp-btw">
        <div className="flex-cnt gap-10">
          <div
            className="node-icn flex-cnt"
            style={{
              background: getNodeItems(data?.name)?.background,
              border: getNodeItems(data?.name)?.border,
            }}
          >
            {getNodeItems(data?.name)?.icon}
          </div>
          <div className="grid gap-6">
            <h1>
              <span>Edit</span>
              {getNodeItems(data?.name)?.name}
            </h1>
            <p>{getNodeItems(data?.name)?.description}</p>
          </div>
        </div>
      </div>
      <div className="data-box-container">
        <span className="data-box-label">Question</span>
        <div className="data-box-wrap">
          <div className="row-container">
            <span className="input-label">Question title</span>
          </div>

          <textarea
            // onChange={(e) => {
            //   handelEdit(e);
            //   handleTextareaResize();
            // }}
            value={""}
            name=""
            id=""
            className="rightSidebar-tarea no-scroll"
            // ref={textInput}
            placeholder={"Ask me a question"}
          ></textarea>
        </div>

        {/* {suggestionModel && <div className="dropdown-items">{showSuggestions()}</div>} */}

        <span className="data-box-label mg-top">Edit variable name</span>
        <div className="data-box-wrap ">
          <span className="input-label">Variable name</span>
          <input
            name=""
            id=""
            className="rightSidebar-tarea"
            placeholder="variable"
          ></input>
        </div>
      </div>
      <div className="btm-sticky__btn flex-cnt">
        <button className="saveDataBtn no-mt">Save</button>
      </div>
    </div>
  );
}

const apps = [
  {
    name: "Hubspot",
    background: "#FF7A590D",
    icon: <HubspotIcon />,
    onDrag: "hubspot",
    border: "1px solid #FF7A59",
    description: "Connect your Hubspot",
  },
  {
    name: "Open AI",
    background: "#0000000D",
    icon: <OpenAIIcon />,
    onDrag: "openai",
    border: "1px solid #000000",
  },
  {
    name: "Google Sheets",
    background: "#23A5660D",
    icon: <GoogleSheetIcon />,
    onDrag: "googleSheet",
    border: "1px solid #23A566",
    description: "Connect your Google sheet",
  },
  {
    name: "Linkedin",
    background: "#23A5660D",
    icon: <AskQuestionIcon />,
    onDrag: "linkedIn",
    border: "1px solid #23A566",
  },
];
