import { useState, useReducer } from "react";
import flatObject from "@/utils/flatObject";

interface Props {
  nodeId: string;
  responseData: Record<string, any>;
  onSave: (data: Array<any>) => void;
  onClose: () => void;
}

export default function HitApiVarSelector({
  nodeId,
  responseData,
  onSave,
  onClose,
}: Props) {
  const [variableData, variableDispatch] = useReducer(
    reducer,
    transformInitData(responseData)
  );
  const [isEditingVarName, setIsEditingVarName] = useState(false);

  function handleSave() {
    const data: Array<any> = [];

    for (const variable of variableData) {
      if (!variable.isSelected) continue;
      data.push({
        name: variable.id,
        customName: `${nodeId}_${variable.name}`,
      });
    }

    onSave(data);
    onClose();
  }

  return (
    <div className="textapi-modal">
      <div className="apiModal-content">
        <div className="closing-modal" onClick={onClose}>
          <button>
            <svg
              width="10"
              height="10"
              viewBox="0 0 10 10"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                d="M10 1.00714L8.99286 0L5 3.99286L1.00714 0L0 1.00714L3.99286 5L0 8.99286L1.00714 10L5 6.00714L8.99286 10L10 8.99286L6.00714 5L10 1.00714Z"
                fill="#6D6D6D"
              ></path>
            </svg>
          </button>
        </div>
        <div className="var-bx-title">Choose Variables</div>
        <div className="var-main-container">
          <div className="var-bx-row">
            <div className="var-bx-left">
              <div className="var-col-title">
                <h3>All variables</h3>
                <p onClick={() => variableDispatch({ type: "select-all" })}>
                  Move all
                </p>
              </div>
              {variableData.some((item) => !item.isSelected) ? (
                <div className="var-list-outer">
                  <ul>
                    {variableData.map(
                      (item, index) =>
                        !item.isSelected && (
                          <li key={index}>
                            <p> {item.id}</p>
                            <span
                              onClick={() =>
                                variableDispatch({
                                  type: "select",
                                  payload: { index },
                                })
                              }
                            >
                              <svg
                                width="16"
                                height="17"
                                viewBox="0 0 16 17"
                                fill="none"
                                xmlns="http://www.w3.org/2000/svg"
                              >
                                <path
                                  d="M10.875 8H8.5V5.625C8.5 5.55625 8.44375 5.5 8.375 5.5H7.625C7.55625 5.5 7.5 5.55625 7.5 5.625V8H5.125C5.05625 8 5 8.05625 5 8.125V8.875C5 8.94375 5.05625 9 5.125 9H7.5V11.375C7.5 11.4437 7.55625 11.5 7.625 11.5H8.375C8.44375 11.5 8.5 11.4437 8.5 11.375V9H10.875C10.9437 9 11 8.94375 11 8.875V8.125C11 8.05625 10.9437 8 10.875 8Z"
                                  fill="#999999"
                                />
                                <path
                                  d="M8 1.5C4.13438 1.5 1 4.63438 1 8.5C1 12.3656 4.13438 15.5 8 15.5C11.8656 15.5 15 12.3656 15 8.5C15 4.63438 11.8656 1.5 8 1.5ZM8 14.3125C4.79063 14.3125 2.1875 11.7094 2.1875 8.5C2.1875 5.29063 4.79063 2.6875 8 2.6875C11.2094 2.6875 13.8125 5.29063 13.8125 8.5C13.8125 11.7094 11.2094 14.3125 8 14.3125Z"
                                  fill="#999999"
                                />
                              </svg>
                            </span>
                          </li>
                        )
                    )}
                  </ul>
                </div>
              ) : (
                <div className="no-data-bx">
                  <div className="no-data-title">All variable selected.</div>
                  <div className="no-data-note">
                    Click on the any variables <br /> on the right to unselect.
                  </div>
                </div>
              )}
            </div>
            <div className="var-bx-divider">
              <svg
                width="20"
                height="14"
                viewBox="0 0 20 14"
                fill="none"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  d="M3.32812 4.83268H18.7448L14.9948 0.666016"
                  stroke="#6D6D6D"
                  strokeWidth="1.25"
                  strokeLinecap="round"
                />
                <path
                  d="M14.9948 9H0.828125L4.57813 12.75"
                  stroke="#6D6D6D"
                  strokeWidth="1.25"
                  strokeLinecap="round"
                />
              </svg>
            </div>
            <div className="var-bx-right-wrapper">
              <div className="var-bx-right">
                <div className="var-col-title">
                  <h3>Selected variables</h3>
                  <p onClick={() => variableDispatch({ type: "unselect-all" })}>
                    Clear all
                  </p>
                </div>
                {variableData.some((item) => item.isSelected) ? (
                  <>
                    {isEditingVarName ? (
                      <div className="var-e-wrapper">
                        <div className="var-e-header">
                          <div className="var-head-title">Previous Name</div>
                          <div className="var-head-title">New Name</div>
                        </div>
                        {variableData.map(
                          (item, index) =>
                            item.isSelected && (
                              <div key={index} className="var-e-outer">
                                <div className="var-e-row">
                                  <div className="var-e-col">{item.id}</div>
                                  <div className="var-e-data">
                                    <input
                                      type="text"
                                      placeholder="Type here"
                                      onChange={(event) =>
                                        variableDispatch({
                                          type: "change-name",
                                          payload: {
                                            index,
                                            value: event.target.value,
                                          },
                                        })
                                      }
                                    />
                                  </div>
                                </div>
                              </div>
                            )
                        )}
                      </div>
                    ) : (
                      <div className="var-list-outer">
                        <ul className="selected-list">
                          {variableData.map(
                            (item, index) =>
                              item.isSelected && (
                                <li key={index}>
                                  <p>{item.name ?? item.id}</p>
                                  <span
                                    onClick={() =>
                                      variableDispatch({
                                        type: "unselect",
                                        payload: { index },
                                      })
                                    }
                                  >
                                    <svg
                                      width="16"
                                      height="17"
                                      viewBox="0 0 16 17"
                                      fill="none"
                                      xmlns="http://www.w3.org/2000/svg"
                                    >
                                      <path
                                        d="M10.875 8H5.125C5.05625 8 5 8.05625 5 8.125V8.875C5 8.94375 5.05625 9 5.125 9H10.875C10.9437 9 11 8.94375 11 8.875V8.125C11 8.05625 10.9437 8 10.875 8Z"
                                        fill="#FB5F66"
                                      />
                                      <path
                                        d="M8 1.5C4.13438 1.5 1 4.63438 1 8.5C1 12.3656 4.13438 15.5 8 15.5C11.8656 15.5 15 12.3656 15 8.5C15 4.63438 11.8656 1.5 8 1.5ZM8 14.3125C4.79063 14.3125 2.1875 11.7094 2.1875 8.5C2.1875 5.29063 4.79063 2.6875 8 2.6875C11.2094 2.6875 13.8125 5.29063 13.8125 8.5C13.8125 11.7094 11.2094 14.3125 8 14.3125Z"
                                        fill="#FB5F66"
                                      />
                                    </svg>
                                  </span>
                                </li>
                              )
                          )}
                        </ul>
                      </div>
                    )}
                    <div className="var-edit-btm">
                      <span
                        onClick={() => setIsEditingVarName(!isEditingVarName)}
                      >
                        {isEditingVarName ? "Save" : "Edit Name"}
                      </span>
                    </div>
                  </>
                ) : (
                  <div className="no-data-bx">
                    <div className="no-data-title">No variable selected.</div>
                    <div className="no-data-note">
                      Click on the variables <br /> on the left to select.
                    </div>
                  </div>
                )}
              </div>
            </div>
          </div>
        </div>
        <div className="btn-save text-center">
          <button onClick={handleSave}>Submit</button>
        </div>
      </div>
    </div>
  );
}

interface Variable {
  id: string;
  name: string;
  value: string;
  isSelected: boolean;
}

type State = Array<Variable>;

type Action =
  | { type: "select"; payload: { index: number } }
  | { type: "unselect"; payload: { index: number } }
  | { type: "select-all" }
  | { type: "unselect-all" }
  | { type: "change-name"; payload: { index: number; value: string } };

function reducer(state: State, action: Action): State {
  switch (action.type) {
    case "select": {
      return state.map((item, index) => {
        if (index === action.payload.index)
          return { ...item, isSelected: true };
        return item;
      });
    }
    case "unselect": {
      return state.map((item, index) => {
        if (index === action.payload.index)
          return { ...item, isSelected: false };
        return item;
      });
    }
    case "select-all": {
      return state.map((item) => ({ ...item, isSelected: true }));
    }
    case "unselect-all": {
      return state.map((item) => ({ ...item, isSelected: false }));
    }
    case "change-name": {
      return state.map((item, index) => {
        if (index === action.payload.index)
          return { ...item, name: action.payload.value };
        return item;
      });
    }
    default: {
      return [];
    }
  }
}

function transformInitData(responseData: Record<string, any>): Array<Variable> {
  const result = flatObject(responseData);
  const variableData: Array<Variable> = [];

  for (const key in result) {
    variableData.push({
      id: key,
      name: key,
      value: result[key],
      isSelected: false,
    });
  }

  return variableData;
}
