import { StickyNotesIcon } from "@/assets/NodeIcon";
import type { NodeConfigProps } from "../types";
import Froala from "../components/Froala";
import "@/styles/stickynote.css";

export default function Sticky({
  data,
  onDataChange,
  onSave,
}: NodeConfigProps) {
  function handleSave() {
    onSave();
  }

  return (
    <div className="innerRightSideBar">
      <div className="title-header fx al-cnt jstfy-sp-btw">
        <div className="flex-cnt gap-10">
          <div
            className="node-icn flex-cnt"
            style={{ background: "#D9880D0D", border: "1px solid #D9880D1A" }}
          >
            <StickyNotesIcon />
          </div>
          <div className="grid gap-6">
            <h1>
              <span>Edit </span>Note
            </h1>
            <p>
              Use note to provide additional information or add instructions for
              others
            </p>
          </div>
        </div>
      </div>
      <div className="ab-test-variable data-box-container">
        <span className="data-box-label">Edit Note Details</span>
        <div className="data-box-wrap">
          <div className="notes-main">
            <div className="notes-content">
              <h3> Content </h3>
              <Froala
                type="sticky"
                content={data.stickyDetails.content}
                onContentChange={(content) =>
                  onDataChange("stickyDetails.content", content)
                }
              />
              {/* <FroalaEditorComponent
                addContent={onContentChange}
                addLabel={setLabel}
                editorRef={editorRef}
                content={props.data?.stickyDetails?.content}
                type="sticky"
                isAIContent={isAIContent}
                allVariables={props?.nodeVariables}
              /> */}
            </div>
          </div>
        </div>
      </div>
      <div className="btm-sticky__btn flex-cnt">
        <button className="saveDataBtn" onClick={handleSave}>
          Save
        </button>
      </div>
    </div>
  );
}
